import React, { Component } from 'react';
import { connect } from 'react-redux';
import shortId from 'shortid';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { MosaicWindow, SplitButton } from 'react-mosaic-component';
import { injectIntl } from 'react-intl';

import { mosaicViewType } from './types';
import IconButton from '../../components/buttons/IconButton';
import KeyHandler from '../../components/KeyHandler';
import ViewSetFactory, {
  IMAGE_VIEW,
  LIST_VIEW,
  PARAMETER_VIEW,
  IMAGE_STRIP_VIEW,
  TOOLSET_VIEW,
} from '../../views/ViewSetFactory';
import ViewSelection from '../ViewSelection';
import NoSVOConfigurationLoadedView from '../NoSVOConfigurationLoadedView';
import { isObjectValid } from '../../utils/objectValidator';
import CloseIcon from '../../assets/icons/close.svg';
import EditIcon from '../../assets/icons/edit.svg';
import {
  canEditViewSet,
  canEditImageView,
  canEditListView,
  canEditImageStripView,
  canEditParameterView,
  canEditToolSetView,
  canDeleteImageView,
  canDeleteListView,
  canDeleteImageStripView,
  canDeleteParameterView,
  canDeleteToolSetView,
} from '../../utils/permissions';

const viewSetFactory = new ViewSetFactory();

class SVOView extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isFullscreen: false,
      resetView: false,
      selectedInspectionId: 0,
      isConfirmOpen: false,
    };
  }

  setFullscreen = (isFullscreen) =>
    // this function can be triggered from reducer
    // where state modifications are not allowed
    setTimeout(() => this.setState({ isFullscreen }), 0);

  resetView = () => {
    this.setState({
      resetView: true,
    });
  };

  removeIdsForUnknownInspectionIds = (currentViewSet, inspectionIds) => {
    const viewSet = currentViewSet.viewSet;

    Object.keys(viewSet).forEach((viewId) => {
      const inspectionId = viewSet[viewId].inspectionId;
      if (!inspectionIds.includes(inspectionId)) {
        delete viewSet[viewId].id;
        delete viewSet[viewId].inspectionId;
      }
    });
  };

  canEditCurrentView = () => {
    const { id, views, myPermissions } = this.props;
    if (views && views.viewSet && views.viewSet[id]) {
      switch (views.viewSet[id].viewComponentClassId) {
        case IMAGE_VIEW:
          return canEditImageView(myPermissions);
        case LIST_VIEW:
          return canEditListView(myPermissions);
        case IMAGE_STRIP_VIEW:
          return canEditImageStripView(myPermissions);
        case PARAMETER_VIEW:
          return canEditParameterView(myPermissions);
        case TOOLSET_VIEW:
          return canEditToolSetView(myPermissions);
        default:
          return false;
      }
    }

    return false;
  };

  canDeleteCurrentView = () => {
    const { id, views, myPermissions } = this.props;
    if (views && views.viewSet && views.viewSet[id]) {
      switch (views.viewSet[id].viewComponentClassId) {
        case IMAGE_VIEW:
          return canDeleteImageView(myPermissions);
        case LIST_VIEW:
          return canDeleteListView(myPermissions);
        case IMAGE_STRIP_VIEW:
          return canDeleteImageStripView(myPermissions);
        case PARAMETER_VIEW:
          return canDeleteParameterView(myPermissions);
        case TOOLSET_VIEW:
          return canDeleteToolSetView(myPermissions);
        default:
          return false;
      }
    }

    return false;
  };

  getInspectionId = () => {
    const { id, views } = this.props;
    const { selectedInspectionId } = this.state;

    return selectedInspectionId || (views.viewSet[id] && views.viewSet[id].inspectionId);
  };

  render() {
    const {
      configuration,
      id,
      path,
      views,
      dispatch,
      editingViewIds,
      intl,
      windowName,
      isSVOConfigLoaded,
      myPermissions,
    } = this.props;
    const { isFullscreen, resetView, selectedInspectionId } = this.state;
    const viewClass = classNames({
      fullscreen: isFullscreen,
      'editing-focus': editingViewIds.includes(id),
    });

    const inspectionId = this.getInspectionId();
    let inspectionName = '<UNKNOWN INSPECTION>';
    if (configuration) {
      const name = configuration.getInspectionName(inspectionId);
      if (name && name.length > 0) {
        inspectionName = `[${name}]`;
      }
      if (windowName && !resetView) {
        inspectionName = `${inspectionName} ${windowName}`;
      }

      const currentInspections =
        configuration &&
        configuration.inspections &&
        configuration.inspections.map((inspection) => inspection.id);
      if (currentInspections) {
        this.removeIdsForUnknownInspectionIds(views, currentInspections);
      }
    }

    const hasInspectionId = views.viewSet && views.viewSet[id] && views.viewSet[id].inspectionId;
    const viewToShowWithoutInspection =
      !hasInspectionId &&
      views.viewSet &&
      views.viewSet[id] &&
      views.viewSet[id].viewComponentClassId;

    const toolbarControls = [];

    if (canEditViewSet(myPermissions)) {
      toolbarControls.push(<SplitButton key="splitButton" />);
    }

    if (this.canEditCurrentView()) {
      toolbarControls.push(
        <IconButton
          tooltip={intl.messages['viewTypeSelection.chooseViewTooltip']}
          onClick={() => this.resetView(id)}
          iconUrl={EditIcon}
          key="swapButton"
        />,
      );
    }

    if (resetView || !hasInspectionId || this.canDeleteCurrentView()) {
      toolbarControls.push(
        <IconButton
          tooltip={intl.messages['svoView.closeView']}
          onClick={() => this.props.onCloseView()}
          iconUrl={CloseIcon}
          key="customRemoveButton"
          style={{ width: '18px', height: '18px' }}
        />,
      );
    }

    return (
      <MosaicWindow
        className={viewClass}
        createNode={() => shortId.generate()}
        title={
          (selectedInspectionId ||
            (views.viewSet && views.viewSet[id] && views.viewSet[id].titleId)) &&
          this.props.intl.formatMessage(
            {
              id:
                selectedInspectionId || windowName
                  ? 'svoView.emptyViewInspectionTemplate'
                  : views.viewSet[id].titleId,
            },
            {
              inspectionName,
            },
          )
        }
        toolbarControls={isSVOConfigLoaded ? toolbarControls : []}
        path={path}
        draggable={canEditViewSet(myPermissions)}
      >
        <KeyHandler view={this} dispatch={dispatch} editingViewIds={editingViewIds}>
          {!resetView && hasInspectionId ? (
            viewSetFactory.createViewInstance(views.viewSet[id], {
              ...views.viewSet[id].passedProps,
              id,
              inspectionId: views.viewSet[id].inspectionId,
            })
          ) : isSVOConfigLoaded ? (
            <ViewSelection
              viewId={id}
              changeContent={() => this.setState({ resetView: false, selectedInspectionId: 0 })}
              changeTitle={(selectedInspectionId) => this.setState({ selectedInspectionId })}
              showCancel={resetView}
              viewToShowWithoutInspection={viewToShowWithoutInspection}
            />
          ) : (
            <NoSVOConfigurationLoadedView />
          )}
        </KeyHandler>
      </MosaicWindow>
    );
  }
}

SVOView.defaultProps = {
  editingViewIds: [],
};

SVOView.propTypes = {
  id: PropTypes.string.isRequired,
  path: PropTypes.arrayOf(PropTypes.string).isRequired,
  views: mosaicViewType.isRequired,
  dispatch: PropTypes.func.isRequired,
  editingViewIds: PropTypes.arrayOf(PropTypes.string),
};

const mapStateToProps = (state, ownProps) => {
  const { views, id } = ownProps;
  const inspectionId = isObjectValid(() => views.viewSet[id].inspectionId);
  const configuration = state.app.configuration;
  const isSVOConfigLoaded = state.viewSet.isSVOConfigLoaded;
  const myPermissions = state.app.myPermissions;

  let windowName = isObjectValid(() => state.inspections[inspectionId].views[id].windowName);
  if (windowName) {
    const inspectionName = configuration.getInspectionName(inspectionId);
    windowName = windowName.replace(`Inspections.${inspectionName}.`, '');
    return {
      configuration,
      windowName,
      isSVOConfigLoaded,
      myPermissions,
    };
  }
  return {
    configuration,
    isSVOConfigLoaded,
    myPermissions,
  };
};

const mapDispatchToProps = () => {
  return {};
};

export default connect(mapStateToProps, mapDispatchToProps)(injectIntl(SVOView));
