import React from 'react';

import logger from '../../../utils/logger';
import IconButton from '../IconButton';

import './style.scss';

export const ToggleButton = ({
  id,
  intl,
  intlId,
  iconUrl,
  onClickHandler,
  intlIdAlternative,
  iconUrlAlternative,
  onClickHandlerAlternative,
  showAlternative,
}) => {
  if (
    !intl ||
    !intlId ||
    !iconUrl ||
    !onClickHandler ||
    !intlIdAlternative ||
    !iconUrlAlternative ||
    !onClickHandlerAlternative
  ) {
    logger.error('ToggleButton component did not receive required parameters');
  }

  let button = {};
  if (showAlternative) {
    button = (
      <IconButton
        key={id}
        tooltip={intl.messages[`${intlIdAlternative}`]}
        onClick={() => {
          onClickHandlerAlternative();
        }}
        iconUrl={iconUrlAlternative}
      />
    );
  } else {
    button = (
      <IconButton
        key={intlId}
        tooltip={intl.messages[`${intlId}`]}
        onClick={() => {
          onClickHandler();
        }}
        iconUrl={iconUrl}
      />
    );
  }

  return button;
};
