import React from 'react';
import { Button } from '@material-ui/core';
import { FormattedMessage } from 'react-intl';

const ArrayParameter = ({
  value,
  changeCallback,
  typeName,
  defaultValue,
  options,
  labels,
  SingleParameterComponent,
}) => {
  const numberOfEntries = (value && value.length) || 0;
  const fields = [];

  const addArrayEntry = () => {
    changeCallback([...(value || []), defaultValue]);
  };
  const removeArrayEntry = (idx) => {
    const copy = [...value];
    copy.splice(idx, 1);
    changeCallback(copy);
  };

  for (let i = 0; i < numberOfEntries; i++) {
    fields.push(
      <div className="field-container" key={`${typeName}_array_${i}`}>
        <div className="field-name-type">
          {i}
          <small>{typeName}</small>
        </div>
        <div className="field-settings">
          <SingleParameterComponent
            value={value[i]}
            changeCallback={(newValue) => {
              const copy = [...(value || [])];
              copy[i] = newValue;
              changeCallback(copy);
            }}
            options={options}
            labels={labels}
            removeCallback={() => removeArrayEntry(i)}
          />
        </div>
      </div>,
    );
  }

  return (
    <div>
      <div className="field-container is-array">
        {fields.length ? (
          fields
        ) : (
          <div className="field-container">
            <div className="field-name-type">
              <em>
                <FormattedMessage id="debugger.noItemsMessage" />
              </em>
            </div>
            <div className="field-settings"></div>
          </div>
        )}
      </div>
      <Button variant="contained" color="primary" onClick={() => addArrayEntry()}>
        <FormattedMessage id="debugger.addItemButtonText" />
      </Button>
    </div>
  );
};

export default ArrayParameter;
