import semver from 'semver';

import logger from '../utils/logger';
import Protobuf from '../protobuf/Protobuf';
import { bufferToUtf8String, utf8StringToBuffer } from '../protobuf/Utils';

import ViewSetFactory, { DEFAULT_VIEWSET_NAME } from '../views/ViewSetFactory';

const viewSetFactory = new ViewSetFactory();

/*
 * Version Changelog:
 * 1:   createdViewSets and currentViewSet property as initial state
 * 2:   added inspectionOverlays property
 * 3:   added inspectionListSelection property
 * 3.1: added inspectionEditingViewIds property
 * 4.0.0: breaking change bw 8.10-8.20. Uses GUIDs instead dotted names
 * 5.0.0: breaking change between 8.20 and 10.00. Uses int ids instead of GUIDs
 */

const WEBAPP_CHUNK_FILENAME = 'WebApp.json';
const WEBAPP_IDMAPPING_FILENAME = 'WebAppIds.json';
const WEBAPP_CHUNK_VERSION = '5.0.0';
const WEBAPP_CHUNK_PARAMETERS = {
  VERSION: 'version',
  SV_OBSERVER_VERSION: 'svObserverVersion',
  GATEWAY_VERSION: 'gatewayVersion',
  WEBAPP_VERSION: 'webAppVersion',
  CREATED_VIEW_SETS: 'createdViewSets',
  CURRENT_VIEW_SETS: 'currentViewSet',
  INSPECTION_OVERLAYS: 'inspectionOverlays',
  INSPECTION_LIST_SELECTION: 'inspectionListSelection',
  INSPECTION_EDITING_VIEW_IDS: 'inspectionEditingViewIds',
  INSPECTION_LIST_ALIASES: 'inspectionListAliases',
  INSPECTION_IMAGE_VIEW_SETTINGS: 'inspectionImageViewSettings',
};

export const saveChunk = (protobuf, currentState, actions) => async (dispatch) => {
  const { setSaveOperationInProcess, setSaveFlag } = actions;
  // extract the saveable information from store
  const inspectionOverlays = Object.keys(currentState.inspections).map((inspectionIdStr) => {
    const inspectionId = parseInt(inspectionIdStr, 10);
    const inspection = currentState.inspections[inspectionIdStr];
    const overlays = Object.keys(inspection.sharedState.views).map((viewId) => ({
      viewId,
      overlays:
        inspection.sharedState.views[viewId] && inspection.sharedState.views[viewId].overlays,
      sourceImage:
        inspection.sharedState.views[viewId] && inspection.sharedState.views[viewId].sourceImage,
    }));
    return { inspectionId, overlays };
  });

  const inspectionListSelection = Object.keys(currentState.inspections).map((inspectionIdStr) => {
    const inspectionId = parseInt(inspectionIdStr, 10);
    const inspection = currentState.inspections[inspectionIdStr];
    const listSelection = Object.keys(inspection.views).map((viewId) => ({
      viewId,
      selection: inspection.views[viewId] && inspection.views[viewId].selection,
      isSelectionMode: inspection.views[viewId] && inspection.views[viewId].isSelectionMode,
      selectAll: inspection.views[viewId] && inspection.views[viewId].selectAll,
    }));
    return { inspectionId, listSelection };
  });

  const inspectionEditingViewIds = Object.keys(currentState.inspections).map((inspectionIdStr) => {
    const inspectionId = parseInt(inspectionIdStr, 10);
    const inspection = currentState.inspections[inspectionIdStr];
    const editingViewId = inspection.sharedState.editingViewId;
    return { inspectionId, editingViewId };
  });

  const inspectionListAliases = Object.keys(currentState.inspections).map((inspectionIdStr) => {
    const inspectionId = parseInt(inspectionIdStr, 10);
    const inspection = currentState.inspections[inspectionIdStr];
    if (inspection.sharedState.listViewAliases) {
      const aliases = convertAliasesToJson(inspection.sharedState.listViewAliases);
      return { inspectionId, aliases, showInList: inspection.sharedState.showAliasesInList };
    }

    return {};
  });

  const inspectionImageViewSettings = Object.keys(currentState.inspections).map(
    (inspectionIdStr) => {
      const inspectionId = parseInt(inspectionIdStr, 10);
      const inspection = currentState.inspections[inspectionIdStr];
      const imageViewSettings = Object.keys(inspection.views).map((viewId) => ({
        viewId,
        zoomFactor: inspection.views[viewId] && inspection.views[viewId].zoomFactor,
        imagePosition: inspection.views[viewId] && inspection.views[viewId].imagePosition,
      }));

      return { inspectionId, imageViewSettings };
    },
  );

  const chunk = {
    [WEBAPP_CHUNK_PARAMETERS.VERSION]: WEBAPP_CHUNK_VERSION,
    [WEBAPP_CHUNK_PARAMETERS.SV_OBSERVER_VERSION]:
      currentState.app.configuration.getSvObserverVersion(),
    [WEBAPP_CHUNK_PARAMETERS.GATEWAY_VERSION]: currentState.app.configuration.getGatewayVersion(),
    [WEBAPP_CHUNK_PARAMETERS.WEBAPP_VERSION]: currentState.app.configuration.getWebAppVersion(),
    [WEBAPP_CHUNK_PARAMETERS.CREATED_VIEW_SETS]: currentState.viewSet.createdViewSets,
    [WEBAPP_CHUNK_PARAMETERS.CURRENT_VIEW_SETS]: currentState.viewSet.currentViewSet,
    [WEBAPP_CHUNK_PARAMETERS.INSPECTION_OVERLAYS]: inspectionOverlays,
    [WEBAPP_CHUNK_PARAMETERS.INSPECTION_LIST_SELECTION]: inspectionListSelection,
    [WEBAPP_CHUNK_PARAMETERS.INSPECTION_EDITING_VIEW_IDS]: inspectionEditingViewIds,
    [WEBAPP_CHUNK_PARAMETERS.INSPECTION_LIST_ALIASES]: inspectionListAliases,
    [WEBAPP_CHUNK_PARAMETERS.INSPECTION_IMAGE_VIEW_SETTINGS]: inspectionImageViewSettings,
  };
  const json = JSON.stringify(chunk);

  await dispatch(setSaveOperationInProcess(true));
  try {
    const req = Protobuf.SvPb.PutFileRequest.create({
      destinationPath: WEBAPP_CHUNK_FILENAME,
      fileData: utf8StringToBuffer(json),
      saveInConfig: true,
    });
    await protobuf.request(req);
  } catch (err) {
    // TODO: report save error to user
    logger.error({ err }, 'Error while saving client chunk');
    return;
  }
  await dispatch(setSaveOperationInProcess(false));
  dispatch(setSaveFlag(false));
};

const convertAliasesToJson = (map) => {
  const result = [];
  map.forEach((entry) => {
    result.push(entry);
  });
  return result;
};

export const loadChunk = (protobuf, actions) => async (dispatch) => {
  const {
    setSharedStateData,
    setClientChunk,
    setViewData,
    setEditingFocus,
    addIncompatibilityDetectedNotification,
    setListViewAliases,
    saveImageSettings,
  } = actions;

  const req = Protobuf.SvPb.GetFileRequest.create({
    sourcePath: WEBAPP_CHUNK_FILENAME,
  });
  let response = null;
  try {
    response = await protobuf.request(req);
  } catch (err) {
    logger.error({ err }, 'Error while loading client chunk');
  }

  if (response.hResult) {
    logger.info('There is no web app configuration to restore');
    return;
  }

  const chunk = bufferToUtf8String(response.fileData);
  let config = JSON.parse(chunk.toString());

  // will check whether config contains deprecated string GUIDs and try to convert them if required
  config = await updateConfigWithGuidToIntMapping(protobuf, config);

  const { version: configVersion } = semver.coerce(config.version);
  if (semver.satisfies(configVersion, '<5.0.0')) {
    throw new Error('Unable to load deprecated config file');
  }

  const receivedConfigParameters = Object.keys(config);
  const expectedConfigParameters = Object.keys(WEBAPP_CHUNK_PARAMETERS).map(
    (key) => WEBAPP_CHUNK_PARAMETERS[key],
  );

  for (let i = 0; i < expectedConfigParameters.length; i++) {
    const currentParameterName = expectedConfigParameters[i];
    if (!receivedConfigParameters.includes(currentParameterName)) {
      logger.warn(`"${currentParameterName}" can not be found in loaded config`);
    }
  }

  if (!semver.valid(configVersion)) {
    logger.error('Config version is invalid!');
    throw new Error('Selected config version is invalid');
  }

  if (semver.diff(WEBAPP_CHUNK_VERSION, configVersion) === 'major') {
    logger.error('Selected config is not compatible with the current Web App version');
    throw new Error('Selected config is not compatible with the current Web App version');
  }

  // Notify user when a minor change detected and a newer config is loaded on an older webapp
  if (
    semver.diff(WEBAPP_CHUNK_VERSION, configVersion) === 'minor' &&
    semver.gt(configVersion, WEBAPP_CHUNK_VERSION)
  ) {
    dispatch(addIncompatibilityDetectedNotification());
  }

  if (semver.satisfies(configVersion, '>=3.0.0')) {
    // restore custom overlays
    config.inspectionOverlays.forEach((inspection) =>
      inspection.overlays.forEach((view) => {
        dispatch(
          setSharedStateData(inspection.inspectionId, 'overlays', view.overlays, view.viewId),
        );
        dispatch(
          setSharedStateData(inspection.inspectionId, 'sourceImage', view.sourceImage, view.viewId),
        );
      }),
    );

    // restore list view data
    config.inspectionListSelection.forEach((inspection) =>
      inspection.listSelection.forEach((view) => {
        dispatch(setViewData(inspection.inspectionId, view.viewId, 'selection', view.selection));
        dispatch(
          setViewData(
            inspection.inspectionId,
            view.viewId,
            'isSelectionMode',
            view.isSelectionMode,
          ),
        );
        dispatch(setViewData(inspection.inspectionId, view.viewId, 'selectAll', view.selectAll));
      }),
    );
  }

  if (semver.satisfies(configVersion, '>=3.1.0')) {
    // restore editing focus
    config.inspectionEditingViewIds.forEach((inspection) => {
      if (inspection.inspectionId && inspection.editingViewId) {
        dispatch(setEditingFocus(inspection.inspectionId, inspection.editingViewId));
      }
    });
  }

  if (semver.satisfies(configVersion, '>=5.0.0')) {
    (config.inspectionListAliases || []).forEach((inspection) => {
      if (inspection.inspectionId && Array.isArray(inspection.aliases)) {
        const aliases = new Map();
        inspection.aliases.forEach((alias) => aliases.set(alias.objectId, alias));
        dispatch(setListViewAliases(inspection.inspectionId, aliases, inspection.showInList));
      }
    });
    (config.inspectionImageViewSettings || []).forEach((inspection) => {
      (inspection.imageViewSettings || []).forEach((setting) => {
        dispatch(
          saveImageSettings(
            inspection.inspectionId,
            setting.viewId,
            setting.zoomFactor,
            setting.imagePosition,
          ),
        );
      });
    });
  }

  //now the views are created with all configurations set before
  dispatch(setClientChunk(config));
};

export const resetChunk = (protobuf, actions) => async (dispatch) => {
  const { setClientChunk } = actions;
  const name = DEFAULT_VIEWSET_NAME;
  const emptyViewset = viewSetFactory.buildEmptyViewSet(name);
  const emptyViewSets = {
    [name]: emptyViewset,
  };

  const emptyChunk = {
    [WEBAPP_CHUNK_PARAMETERS.VERSION]: WEBAPP_CHUNK_VERSION,
    [WEBAPP_CHUNK_PARAMETERS.SV_OBSERVER_VERSION]: 'UNDEFINED',
    [WEBAPP_CHUNK_PARAMETERS.GATEWAY_VERSION]: 'UNDEFINED',
    [WEBAPP_CHUNK_PARAMETERS.WEBAPP_VERSION]: 'UNDEFINED',
    [WEBAPP_CHUNK_PARAMETERS.CREATED_VIEW_SETS]: emptyViewSets,
    [WEBAPP_CHUNK_PARAMETERS.CURRENT_VIEW_SETS]: emptyViewset,
    [WEBAPP_CHUNK_PARAMETERS.INSPECTION_OVERLAYS]: [],
    [WEBAPP_CHUNK_PARAMETERS.INSPECTION_LIST_SELECTION]: [],
    [WEBAPP_CHUNK_PARAMETERS.INSPECTION_EDITING_VIEW_IDS]: [],
    [WEBAPP_CHUNK_PARAMETERS.INSPECTION_LIST_ALIASES]: [],
    [WEBAPP_CHUNK_PARAMETERS.INSPECTION_IMAGE_VIEW_SETTINGS]: [],
  };

  dispatch(setClientChunk(emptyChunk));
};

const updateConfigWithGuidToIntMapping = async (protobuf, config) => {
  const { version } = semver.coerce(config.version);

  // config is not using GUIDs any more already
  if (semver.satisfies(version, '>=5.0.0') || semver.satisfies(version, '<4.0.0')) {
    return config;
  }

  const sourcePath = WEBAPP_IDMAPPING_FILENAME;
  const req = Protobuf.SvPb.GetFileRequest.create({ sourcePath });

  let res;
  try {
    res = await protobuf.request(req);
  } catch (err) {
    logger.error({ err }, 'Error while loading id mapping');
    throw new Error('Unable to load id mapping file');
  }

  if (res.hResult) {
    logger.info(`The file "${WEBAPP_IDMAPPING_FILENAME}" does not exist.`);
    throw new Error('Unable to load id mapping file');
  }

  let idMapping;
  try {
    const idMappingChunk = bufferToUtf8String(res.fileData);
    idMapping = JSON.parse(idMappingChunk.toString());
  } catch (err) {
    logger.error({ err }, `Error while parsing content of ${WEBAPP_IDMAPPING_FILENAME}`);
    return config;
  }

  const mapping = parseGuidToObjectIdMapping(idMapping.guidToObjectIdMapping);
  const error = { hasError: false };
  recursivlyUpdateConfigWithGuidToIntMapping(config, mapping, error);

  // this is the first version with int ids
  config.version = '5.0.0';
  config.patchedIds = true;
  if (error.hasError) {
    throw new Error(
      'Selected config is not compatible with the current Web App version (could not migrate all string GUIDs to int ObjectIds)',
    );
  } else {
    logger.debug('Successfully migrated string GUIDs to int ObjectIds');
  }

  return config;
};

const parseGuidToObjectIdMapping = (mapping) => {
  const result = new Map();
  Object.entries(mapping || {}).forEach(([key, value]) => {
    const guid = key.toLowerCase().replace('{', '').replace('}', '');
    result.set(guid, value);
  });
  return result;
};

const lookUpKeyInMapping = (key, mapping) => {
  if (mapping.has(key)) {
    return mapping.get(key);
  }
  const lowerCaseKey = key.toLowerCase();
  if (mapping.has(lowerCaseKey)) {
    return mapping.get(lowerCaseKey);
  }
  return undefined;
};

const recursivlyUpdateConfigWithGuidToIntMapping = (obj, mapping, error) => {
  for (const key in obj) {
    if (typeof obj[key] === 'object' && obj[key] !== null) {
      if (key === 'selection') {
        obj[key] = obj[key]
          .map((entry) => {
            const value = lookUpKeyInMapping(entry, mapping);
            if (value) {
              return value;
            } else {
              error.hasError = true;
              logger.error(
                `There is a guid in the config which is not included in the mapping: ${entry}`,
              );
              return undefined;
            }
          })
          .filter((entry) => !!entry); // remove undefined entries from selection
      }
      recursivlyUpdateConfigWithGuidToIntMapping(obj[key], mapping, error);
    } else {
      if (
        key === 'inspectionId' ||
        key === 'sourceImage' ||
        key === 'xVariable' ||
        key === 'offsetLeftVariable' ||
        key === 'yVariable' ||
        key === 'offsetTopVariable' ||
        key === 'widthVariable' ||
        key === 'heightVariable' ||
        key === 'colorVariable'
      ) {
        const value = lookUpKeyInMapping(obj[key], mapping);
        if (value) {
          obj[key] = value;
        } else {
          obj[key] = undefined;
          error.hasError = true;
          logger.error(
            `There is a guid in the config which is not included in the mapping: ${obj[key]}`,
          );
        }
      }
    }
  }
};
