import React from 'react';
import SelectParameter from '../../generic/SelectParameter';

const EnumParameter = ({ value, options, changeCallback, removeCallback }) => {
  return (
    <SelectParameter
      value={String(value)}
      options={Object.values(options)}
      labels={Object.keys(options)}
      changeCallback={(value) => changeCallback(value)}
    />
  );
};

export default EnumParameter;
