import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import React, { Component } from 'react';
import { injectIntl } from 'react-intl';
import { AutoResizer } from 'react-base-table';
import 'react-base-table/styles.css';

import iconList from '../../assets/icons/overlay-list.svg';
import ActionBar from '../../components/ActionBar';
import ActionBarType from '../../types/ActionBarType';
import ProxyType from '../../components/Protobuf/ProxyType';
import ObjectSelector from '../../components/ObjectSelector';
import logger from '../../utils/logger';
import { canReadListView, canEditListView } from '../../utils/permissions';

import AliasEditor from './components/List/AliasEditor';
import DraggableTable from '../../components/DraggableTable';
import IconButton from '../../components/buttons/IconButton';
import { TOGGLE_BUTTON } from '../../types/ActionBarType';

import EditIcon from '../../assets/icons/edit.svg';
import iconShow from '../../assets/icons/show.svg';
import iconShowOff from '../../assets/icons/show-off.svg';

import {
  initViewState,
  setViewData,
  setSharedStateData,
  setSaveFlag,
  updateListViewShowAliases,
} from '../../redux/actions/index';
import './style.scss';

class ListView extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listValues: [],
      expandedKeys: [],
      isAliasEditorOpen: false,
      selectedAlias: {},
      showAliases: false,
    };
  }

  componentDidMount() {
    this.unsubscribeFromProductStream();
    if (!this.props.isSelectionMode && this.props.selection.length > 0) {
      this.subscribeToProductStream();
    }

    if (this.props.showAliasesInList) {
      this.setState({ showAliases: true });
    }
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    this.unsubscribeFromProductStream();
    if (!nextProps.isSelectionMode && nextProps.selection.length > 0) {
      this.subscribeToProductStream();
    }
  }

  componentWillUnmount() {
    this.unsubscribeFromProductStream();
  }

  subscribeToProductStream() {
    if (!this.productSub) {
      const { inspectionId, selection } = this.props;
      const startAtTriggerCount = -1; // list view can only show one item at a time
      const desc = { inspectionId, valueIds: selection, startAtTriggerCount };
      this.productSub = this.props.proxy.subscribeToProductStream(desc, this.onProduct.bind(this));
    }
  }

  unsubscribeFromProductStream() {
    if (this.productSub) {
      this.props.proxy.unsubscribeFromProductStream(this.productSub);
      this.productSub = null;
    }
  }

  onProduct({ product }) {
    const listValues = this.createListValues(product);
    this.setState({ listValues });
    return Promise.resolve();
  }

  createListValues(product) {
    const { configuration, inspectionId, selection } = this.props;
    const values = product.getValuesAsString(selection);
    return values.map((entry) => ({
      ...entry,
      name: configuration.getValueNameForId(inspectionId, entry.id),
    }));
  }

  setAliasColumnVisibility = (visibility) => {
    this.props.updateListViewShowAliases(visibility);
    this.props.setSaveFlag(true);
    this.setState({ showAliases: visibility });
  };

  saveNewOrdner = (before, after) => {
    const { selection } = this.props;
    const [removed] = selection.splice(before, 1);
    selection.splice(after, 0, removed);
    this.props.setViewData('selection', selection);
    this.props.setSaveFlag(true);
  };

  renderValueSelector = () => {
    const { configuration, inspectionId, selection } = this.props;

    if (!configuration || !inspectionId) {
      return;
    }

    let tree = configuration.getObjectSelectorItemsForInspection(inspectionId).tree;
    tree = configuration.filterArrayValuesFromTree(tree);

    const filterIgnore = [];
    const name = configuration.getInspectionName(inspectionId);

    filterIgnore.push(`Inspections.${name}`);
    filterIgnore.push(`Inspections${name}`);
    filterIgnore.push(name);

    return (
      <div style={{ width: '100%', overflow: 'auto', height: '100%' }}>
        <ObjectSelector
          data={tree}
          onChange={(selectedIds) => {
            this.props.setViewData('selection', selectedIds);
            this.props.setSaveFlag(true);
          }}
          onExpand={(expandedKeys) => this.setState({ expandedKeys })}
          selection={selection}
          expandedKeys={this.state.expandedKeys}
          filterIgnore={filterIgnore}
        />
      </div>
    );
  };

  renderList = () => {
    const { intl, locale, myPermissions } = this.props;
    const { showAliases } = this.state;
    const data = this.getListData();
    const columns = [
      {
        key: showAliases ? 'Alias/Name' : 'Name',
        dataKey: 'name',
        title: showAliases ? 'Alias/Name' : 'Name',
        width: 0,
        flexGrow: 2,
      },
      {
        key: 'Values',
        dataKey: 'value',
        title: 'Values',
        width: 0,
        flexGrow: 1,
      },
    ];

    if (canEditListView(myPermissions)) {
      columns.push({
        key: '',
        dataKey: 'editAlias',
        title: '',
        width: 50,
      });
    }

    const tableData = data.map((row) => {
      row.name = showAliases ? (row.alias && row.alias[locale]) || row.name : row.name;
      return {
        ...row,
        editAlias: (
          <IconButton
            tooltip={intl.messages['listView.AliasEditor.title']}
            onClick={() => this.setState({ selectedAlias: row, isAliasEditorOpen: true })}
            iconUrl={EditIcon}
            key="editAlias"
          />
        ),
      };
    });

    return (
      <div style={{ height: '100%' }}>
        <AutoResizer>
          {({ width, height }) => (
            <DraggableTable
              columns={columns}
              data={tableData}
              width={width}
              height={height}
              headerHeight={30}
              rowHeight={30}
              onSequenceChange={(before, after) => this.saveNewOrdner(before, after)}
            />
          )}
        </AutoResizer>
        {this.renderAliasEditor()}
      </div>
    );
  };

  renderAliasEditor = () => {
    const { selectedAlias, listValues, isAliasEditorOpen } = this.state;
    const currentValueObject = (listValues || []).find((value) => value.id === selectedAlias.id);

    if (!currentValueObject || !isAliasEditorOpen) {
      return;
    }

    return (
      <AliasEditor
        onClose={(alias, isSaved = false) => this.closeAliasEditor(alias, isSaved)}
        open={this.state.isAliasEditorOpen}
        selectedAlias={currentValueObject}
      />
    );
  };

  getListData = () => {
    const { aliases } = this.props;
    const data = this.state.listValues;

    if (aliases) {
      (data || []).map((entry) => {
        if (aliases.has(entry.id)) {
          const alias = aliases.get(entry.id);
          if (!alias) {
            logger.error(`Could not load alias for objectId ${entry.id}`);
            return entry;
          }

          if (!alias.alias) {
            logger.info(`There is no alias value stored for ${alias.dottedName}.`);
            return entry;
          }

          entry.alias = alias.alias;
        }
        return entry;
      });
    }
    return data;
  };

  closeAliasEditor = (alias, isSaved) => {
    if (alias) {
      const { aliases } = this.props;

      Object.keys(alias.alias).forEach((language) => {
        if (alias.alias[language] === '') {
          delete alias.alias[language];
        }
      });

      const isEmpty = Object.keys(alias.alias).length === 0;
      if (aliases.has(alias.id)) {
        const newAlias = aliases.get(alias.id);
        newAlias.alias = alias.alias;

        if (isEmpty) {
          aliases.delete(alias.id);
        } else {
          aliases.set(alias.id, newAlias);
        }
      } else {
        if (!isEmpty) {
          aliases.set(alias.id, { objectId: alias.id, dottedName: alias.name, alias: alias.alias });
        }
      }
    }

    this.setState({ isAliasEditorOpen: false });
    if (isSaved) {
      this.props.setSaveFlag(true);
    }
  };

  render() {
    const { viewActions, isSelectionMode, myPermissions } = this.props;
    const canEdit = canEditListView(myPermissions);

    if (canReadListView(myPermissions) || canEdit) {
      return (
        <div className="list-view-wrapper">
          {isSelectionMode && canEdit ? this.renderValueSelector() : this.renderList()}
          <ActionBar isViewAction actions={viewActions(this)} />
        </div>
      );
    }

    return <div></div>;
  }
}

ListView.defaultProps = {
  viewActions: (listView) => {
    const actions = [];
    if (canEditListView(listView.props.myPermissions)) {
      actions.push({
        intlId: 'listView.save',
        iconUrl: iconList,
        onClickHandler: () => {
          listView.props.setViewData('isSelectionMode', !listView.props.isSelectionMode);
          listView.props.setSaveFlag(true);
        },
      });
    }

    actions.push({
      type: TOGGLE_BUTTON,
      id: listView.props.inspectionId,
      intlId: 'listView.Actionbar.showAliases',
      iconUrl: iconShow,
      onClickHandler: () => {
        listView.setAliasColumnVisibility(true);
      },
      intlIdAlternative: 'listView.Actionbar.hideAliases',
      iconUrlAlternative: iconShowOff,
      onClickHandlerAlternative: () => {
        listView.setAliasColumnVisibility(false);
      },
      showAlternative: listView.props.showAliasesInList,
    });

    return actions;
  },
  currentMode: undefined,
  proxy: undefined,
  isSelectionMode: true,
  inspections: {},
  selection: [],
};

ListView.propTypes = {
  viewActions: ActionBarType.isRequired,
  currentMode: PropTypes.string,
  proxy: ProxyType,
  inspections: PropTypes.object,
  selection: PropTypes.array,
  isSelectionMode: PropTypes.bool,
};

const initState = {
  selection: [],
  isSelectionMode: true,
};

const initSharedState = {};

const mapDispatchToProps = (dispatch, ownProps) => {
  const { inspectionId, id } = ownProps;
  const viewId = id;
  dispatch(initViewState(inspectionId, viewId, initState, initSharedState));
  return {
    setViewData: (key, data) => dispatch(setViewData(inspectionId, viewId, key, data)),
    setSharedStateData: (key, data) => dispatch(setSharedStateData(inspectionId, key, data)),
    updateListViewShowAliases: (show) => dispatch(updateListViewShowAliases(inspectionId, show)),
    setSaveFlag: (hasChanges) => dispatch(setSaveFlag(hasChanges)),
  };
};

const mapStateToProps = (state, ownProps) => {
  const { inspectionId, id } = ownProps;
  const viewId = id;
  try {
    return {
      proxy: state.app.proxy,
      configuration: state.app.configuration,
      currentMode: state.app.currentMode,
      selection: state.inspections[inspectionId].views[viewId].selection,
      isSelectionMode: state.inspections[inspectionId].views[viewId].isSelectionMode,
      aliases: state.inspections[inspectionId].sharedState.listViewAliases,
      showAliasesInList: state.inspections[inspectionId].sharedState.showAliasesInList,
      locale: state.app.locale,
      myPermissions: state.app.myPermissions,
    };
  } catch (error) {
    return {};
  }
};

export default injectIntl(connect(mapStateToProps, mapDispatchToProps)(ListView));
