import React from 'react';
import { sortableContainer, sortableElement, sortableHandle } from 'react-sortable-hoc';
import Table, { callOrReturn } from 'react-base-table';

import DragIcon from '../../assets/icons/drag.svg';

import './style.css';

const DraggableContainer = sortableContainer(({ children }) => children);
const DraggableElement = sortableElement(({ children }) => children);
const DraggableHandle = sortableHandle(({ children }) => children);

const Row = ({ key, index, children, draggable, ...rest }) => {
  if (!draggable) {
    return <div {...rest}>{children}</div>;
  }

  return (
    <DraggableElement key={key} index={index}>
      <div {...rest}>
        <DraggableHandle>
          <div className="draggable-table-handle">
            <img src={DragIcon} alt="Drag" />
          </div>
        </DraggableHandle>
        {children}
      </div>
    </DraggableElement>
  );
};

class DraggableTable extends React.PureComponent {
  state = {
    data: this.props.data,
  };

  table = React.createRef();

  componentDidUpdate(prevProps) {
    if (prevProps.data !== this.props.data) {
      const data = [...this.props.data];
      this.setState({ data });
    }
  }

  getContainer = () => {
    return this.table.current.getDOMNode().querySelector('.BaseTable__body');
  };

  getHelperContainer = () => {
    return this.table.current.getDOMNode().querySelector('.BaseTable__table');
  };

  rowProps = (args) => {
    const extraProps = callOrReturn(this.props.rowProps);
    return {
      ...extraProps,
      draggable: this.props.draggable,
      tagName: Row,
      index: args.rowIndex,
    };
  };

  handleSortEnd = ({ oldIndex, newIndex }) => {
    this.props.onSequenceChange(oldIndex, newIndex);
    const data = [...this.state.data];
    const [removed] = data.splice(oldIndex, 1);
    data.splice(newIndex, 0, removed);
    this.setState({ data });
  };

  render() {
    if (!this.props.draggable) {
      return (
        <Table {...this.props} ref={this.table} data={this.state.data} rowProps={this.rowProps} />
      );
    }

    return (
      <DraggableContainer
        useDragHandle
        getContainer={this.getContainer}
        helperContainer={this.getHelperContainer}
        onSortEnd={this.handleSortEnd}
      >
        <Table
          {...this.props}
          ref={this.table}
          data={this.state.data}
          rowProps={this.rowProps}
          className="draggable"
        />
      </DraggableContainer>
    );
  }
}

DraggableTable.defaultProps = {
  draggable: true,
};

export default DraggableTable;
