export default class Product {
  constructor({ history, trigger, isReject }) {
    this.history = history;
    this.trigger = trigger;
    this.isReject = isReject;
    this.useCount = 0;
    this.images = new Map();
    this.values = new Map();
  }

  getImage(imageId) {
    this.checkNotReleasedYet();
    return this.images.get(imageId);
  }

  getImages(imageIds) {
    this.checkNotReleasedYet();
    return (imageIds || []).map((imageId) => this.images.get(imageId));
  }

  getValue(valueId) {
    this.checkNotReleasedYet();
    return this.values.get(valueId);
  }

  getValuesAsString(valueIds) {
    this.checkNotReleasedYet();
    return (valueIds || []).map((valueId) => {
      let value = this.values.has(valueId) && this.values.get(valueId).value;

      if (value === undefined) {
        value = '<UNAVAILABLE>';
      }
      return {
        id: valueId,
        value: value.toString(),
      };
    });
  }

  buildUrl(b64Data, contentType = '', sliceSize = 512) {
    const byteCharacters = atob(b64Data);
    const byteArrays = [];

    for (let offset = 0; offset < byteCharacters.length; offset += sliceSize) {
      const slice = byteCharacters.slice(offset, offset + sliceSize);

      const byteNumbers = new Array(slice.length);
      for (let i = 0; i < slice.length; i++) {
        byteNumbers[i] = slice.charCodeAt(i);
      }

      const byteArray = new Uint8Array(byteNumbers);
      byteArrays.push(byteArray);
    }

    const blob = new Blob(byteArrays, { type: contentType });
    return URL.createObjectURL(blob);
  }

  async addData({ images, values }) {
    this.checkNotReleasedYet();

    const imagePromises = [];
    images.forEach(({ id, image, overlays }) => {
      if (!this.images.has(id) && image && image.rgbData) {
        imagePromises.push(this.initImage(id, image, overlays));
      }
    });

    values.forEach(({ id, value }) => {
      if (!this.values.has(id) && value) {
        const valueStr = this.getValueFromVariant(value);
        this.values.set(id, {
          id,
          value: valueStr,
        });
      }
    });

    await Promise.all(imagePromises);
  }

  async initImage(id, image, overlays) {
    const { domImage, url } = await this.buildImage(image);
    const overlayImages = await this.initOverlayImages(overlays);
    this.images.set(id, {
      id,
      image,
      domImage,
      overlays,
      overlayImages,
      url,
    });
  }

  buildImage({ rgbData, type }) {
    return new Promise((resolve, reject) => {
      const arrayBufferView = new Uint8Array(rgbData);
      const blob = new Blob([arrayBufferView], { type: type || 'image/bmp' });
      const domURL = self.URL || self.webkitURL || self; // eslint-disable-line
      const url = domURL.createObjectURL(blob);
      const domImage = new Image();
      domImage.onload = () => {
        resolve({ domImage, url });
      };
      domImage.onerror = (e) => {
        domURL.revokeObjectURL(url);
        reject(e);
      };
      domImage.src = url;
    });
  }

  initOverlayImages(overlaysDesc) {
    const promises = [];
    ((overlaysDesc && overlaysDesc.overlays) || []).forEach((overlay) => {
      (overlay.shapeGroups || []).forEach((shapeGroup) => {
        (shapeGroup.shapes || []).forEach((shape) => {
          if (shape && shape.image && shape.image.image && shape.image.image.rgbData) {
            promises.push(
              this.buildImage(shape.image.image).then(({ domImage, url }) => {
                shape.image.domImage = domImage;
                shape.image.url = url;
                return shape.image;
              }),
            );
          }
        });
      });
    });
    return Promise.all(promises);
  }

  getValueFromVariant(v) {
    if (!v) {
      return undefined;
    }
    return v[v.data];
  }

  lock() {
    this.useCount++;
  }

  release() {
    this.history?.releaseProduct(this.trigger);
  }

  releaseImpl() {
    if (this.useCount <= 0) {
      throw new Error(`Invalid use count ${this.useCount} in Product::release()`);
    }

    this.useCount--;
    if (this.useCount > 0) {
      return false;
    }

    this.releaseImages();

    this.images.clear();
    this.values.clear();
    this.history = null;
    this.released = true;

    return true;
  }

  releaseImages() {
    this.images.forEach((image) => {
      this.releaseImage(image);

      image.overlayImages.forEach((overlayImage) => {
        this.releaseImage(overlayImage);
      });
    });
  }

  releaseImage(image) {
    if (image.domImage) {
      image.domImage.onload = undefined;
      image.domImage.onerror = undefined;
      image.domImage.remove(); // explicitly remove the image from the DOM
      image.domImage = null;
    }

    if (image.url) {
      const domURL = self.URL || self.webkitURL || self; // eslint-disable-line
      domURL.revokeObjectURL(image.url);
    }
  }

  checkNotReleasedYet() {
    if (this.released) {
      throw new Error('Can not use Product after releasing it!');
    }
  }
}
