import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { FormattedMessage } from 'react-intl';

import './style.scss';

import Dialog from '../../components/Dialog';
import map from './viewMappings';

class HelpView extends Component {
  render() {
    const { onClose, open, focusedViewType, locale } = this.props;
    let helpUrl;
    switch (locale) {
      case 'en':
        helpUrl = `/help/en/SVOWebApp_WebHelp.htm#cshid=${map[focusedViewType]}`;
        break;

      case 'de':
        helpUrl = `/help/de/SVOWebApp_WebHelp.htm#cshid=${map[focusedViewType]}`;
        break;

      default:
        helpUrl = `/help/en/SVOWebApp_WebHelp.htm#cshid=${map[focusedViewType]}`;
        break;
    }
    return (
      <Dialog isFullscreen title={<FormattedMessage id="helpView" />} onClose={onClose} open={open}>
        <iframe
          className="help-frame"
          title="help"
          style={{ width: '100%', height: '97%' }}
          src={helpUrl}
        />
      </Dialog>
    );
  }
}

HelpView.defaultProps = {
  open: false,
  locale: 'en',
};

HelpView.propTypes = {
  open: PropTypes.bool,
  onClose: PropTypes.func.isRequired,
  locale: PropTypes.string,
};

const mapStateToProps = (state) => ({
  focusedViewType: state.app.focusedViewType,
  locale: state.app.locale,
});

export default connect(mapStateToProps)(HelpView);
