import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import Protobuf from '../../protobuf/Protobuf';
import RequestParameter from '../RequestParameter';
import RequestedResponseVisualizer from '../RequestedResponseVisualizer';
import { Button } from '@material-ui/core';
import RequestsList from '../RequestsList';
import ArrowIcon from '../../assets/icons/back.svg';
import { FormattedMessage } from 'react-intl';

import './style.scss';

const Debugger = () => {
  const [currentRequest, setCurrentRequest] = useState('');
  const [response, setResponse] = useState(null);

  const filteredProto = Object.keys(Protobuf.SvPb)
    .sort()
    .reduce((requests, key) => {
      if (!key.endsWith('Request')) return requests;
      if (key.endsWith('StreamRequest')) return requests;
      if (
        Protobuf.SvPb[key].filename.includes('InspectionCommands') &&
        key !== 'InspectionCmdRequest'
      )
        return requests;
      if (Protobuf.SvPb[key].filename.includes('ConfigCommands') && key !== 'ConfigCommandRequest')
        return requests;

      return { ...requests, [key]: Protobuf.SvPb[key] };
    }, {});

  return (
    <div className="debugger-wrapper">
      <div className="debugger-header">
        <Button component={Link} variant="contained" color="primary" to="/app">
          <img src={ArrowIcon} alt="back" className="image-item" />
          <FormattedMessage id="debugger.backToWebApp" />
        </Button>
      </div>
      <div className="request-list-container">
        <RequestsList
          proto={filteredProto}
          chooseCallback={(key) => {
            setCurrentRequest(key);
            setResponse(null);
          }}
        />
      </div>
      <div className="request-container">
        <RequestParameter name={currentRequest} onResponse={(response) => setResponse(response)} />
      </div>
      <div className="response-container">
        <RequestedResponseVisualizer response={response} />
      </div>
    </div>
  );
};

export default Debugger;
