import protobufjs from 'protobufjs';

export const utf8StringToBuffer = (str) => {
  const len = protobufjs.util.utf8.length(str);
  const buf = new Uint8Array(len);
  protobufjs.util.utf8.write(str, buf, 0);
  return buf;
};

export const bufferToUtf8String = (buffer) => {
  return protobufjs.util.utf8.read(buffer, 0, buffer.length);
};
