import ProductHistory from './ProductHistory';

export default class ProductHistoryManager {
  constructor() {
    this.productHistories = new Map();
  }

  init(inspections) {
    this.productHistories.clear();

    (inspections || []).forEach((inspection) => {
      const inspectionId = inspection.id;
      const history = new ProductHistory(inspectionId);
      this.productHistories.set(inspectionId, history);
    });
  }

  async addProductData(inspectionId, productData) {
    const productHistory = this.productHistories.get(inspectionId);
    if (!productHistory) {
      return undefined;
    }
    return await productHistory.addProductData(productData);
  }
}
