import React from 'react';
import PropTypes from 'prop-types';
import { FormattedMessage } from 'react-intl';
import { useSelector } from 'react-redux';

import './style.scss';
import allModes from '../../../../utils/modes';
import { canReadSVOMode, canEditSVOMode } from '../../../../utils/permissions';
import MenuList from '../../../../components/MenuList';

const ModeSwitch = ({ changeMode, currentMode, modes, open, handleClose, enabled }) => {
  const myPermissions = useSelector((state) => state.app.myPermissions);
  const selectableModes = Object.keys(modes).filter((m) => m !== 'unknownMode');
  const canRead = canReadSVOMode(myPermissions);
  const canEdit = canEditSVOMode(myPermissions);
  const menuItems = selectableModes.map((key) => ({
    key: key,
    onClick: () => {
      changeMode(key, modes[key].id);
    },
    caption: <FormattedMessage id={key} />,
    img: modes[key].iconSrc,
  }));

  return canRead || canEdit ? (
    <div>
      <MenuList
        buttonCaption={currentMode}
        buttonIcon={modes[currentMode.props.id].iconSrc}
        menuItems={menuItems}
        onClose={handleClose}
        open={open}
        showBottomArrow
        enabled={enabled && canEdit}
      />
    </div>
  ) : (
    <div></div>
  );
};

ModeSwitch.defaultProps = {
  open: false,
  currentMode: undefined,
  modes: allModes,
  handleClose: () => {},
};

export const modeShape = PropTypes.shape({
  id: PropTypes.string,
  iconSrc: PropTypes.string,
  text: PropTypes.string,
});

ModeSwitch.propTypes = {
  changeMode: PropTypes.func.isRequired,
  open: PropTypes.bool,
  anchorEl: PropTypes.node,
  handleRequestClose: PropTypes.func.isRequired,
  openModePopover: PropTypes.func.isRequired,
  currentMode: PropTypes.node,
  modes: modeShape,
};

export default ModeSwitch;
