import { parse } from 'url';

function getDefaultWebsocketUrl() {
  const { host, protocol } = window.location;
  const wsProtocol = protocol === 'https:' ? 'wss:' : 'ws:';
  return `${wsProtocol}//${host}`;
}

function getEndpoint(url) {
  const parsedUrl = url ? parse(url) : window.location;
  const protocol = parsedUrl.protocol.replace('ws', 'http');
  return `${protocol}//${parsedUrl.host}`;
}

const env = {
  authEndpoint: getEndpoint(process.env.REACT_APP_WEBSOCKET_SERVER_URL),
  websocketServerUrl: process.env.REACT_APP_WEBSOCKET_SERVER_URL || getDefaultWebsocketUrl(),
  enableAutoSave: false,
};

export default env;
