import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { IntlProvider } from 'react-intl';

import PermissionManagement from '../components/PermissionManagement';
import HelpView from '../views/HelpView';
import viewMappings from '../views/HelpView/viewMappings';
import KeyHandler from '../components/KeyHandler';
import translations from '../translations';
import { closeHelpView } from '../redux/actions';

const PermissionPage = () => {
  const dispatch = useDispatch();
  const { isHelpViewOpen, locale } = useSelector((state) => state.app);

  return (
    <IntlProvider locale={locale} messages={translations[locale]}>
      <KeyHandler dispatch={dispatch} helpSection={viewMappings.PERMISSIONS}>
        <PermissionManagement />
        <HelpView open={isHelpViewOpen} onClose={() => dispatch(closeHelpView())}></HelpView>
      </KeyHandler>
    </IntlProvider>
  );
};

export default PermissionPage;
