import shortId from 'shortid';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import React, { Component } from 'react';
import { injectIntl } from 'react-intl';

import './style.scss';

import OutsideClickHandler from '../../../../../src/components/OutsideClickHandler';
import LogIcon from '../../../../assets/icons/description.svg';
import { canReadLogs } from '../../../../utils/permissions';

class StatusReport extends Component {
  state = {
    statusListActive: false,
  };

  closeStatusList = (e) => {
    this.setState({ statusListActive: false });
  };

  render() {
    const { notifications, myPermissions } = this.props;
    return canReadLogs(myPermissions) ? (
      <OutsideClickHandler
        onOutsideClick={() => {
          this.closeStatusList();
        }}
        ref={(ref) => {
          this.wrapper = ref;
        }}
      >
        {notifications && notifications.length > 0 ? (
          <div style={{ position: 'relative' }}>
            <div
              role="button"
              tabIndex="0"
              className="status-bar"
              onClick={() => {
                this.setState({
                  statusListActive: !this.state.statusListActive,
                });
              }}
            >
              <img src={LogIcon} alt="Log" />
              <p className={`${notifications[0].type} pb1 ma0 `}>{notifications[0].message}</p>
            </div>
            <div className="bottomArrow"></div>
            <div className={`status-list ${this.state.statusListActive ? '' : 'hidden'}`}>
              <div className="status-container">
                {notifications.map((notification) => (
                  <p key={shortId.generate()} className={`${notification.type} status`}>
                    {notification.timestamp}: <b>{notification.message}</b>{' '}
                    {notification.count > 1 ? `(${notification.count})` : ''}
                  </p>
                ))}
              </div>
            </div>
          </div>
        ) : (
          <div></div>
        )}
      </OutsideClickHandler>
    ) : (
      <div></div>
    );
  }
}

StatusReport.defaultProps = {
  notifications: undefined,
};

StatusReport.propTypes = {
  notifications: PropTypes.arrayOf(
    PropTypes.shape({
      type: PropTypes.string,
    }),
  ),
};

const mapStateToProps = (state) => ({
  notifications: state.app.notifications,
  myPermissions: state.app.myPermissions,
});

export default connect(mapStateToProps)(injectIntl(StatusReport));
