import React from 'react';
import shortId from 'shortid';

import ListView from './ListView';
import ImageView from './ImageView';
import RecipeView from './RecipeView';
import ToolsetView from './ToolsetView';
import ParameterView from './ParameterView';
import ImageStripView from './ImageStripView';

const mapViewSetArray = (views) => {
  return views.reduce((viewSet, view) => ({ ...viewSet, [view.id]: view }), {});
};

const configurations = [
  {
    title: 'No. of Cameras',
    intlId: 'noOfCameras',
    options: [
      { title: '1', value: 1 },
      { title: '2', value: 2 },
      { title: '3', value: 3 },
      { title: '4', value: 4 },
    ],
  },
  {
    title: 'No. of Triggers',
    intlId: 'noOfTriggers',
    options: [
      { title: '1', value: 1 },
      { title: '2', value: 2 },
      { title: '3', value: 3 },
      { title: '4', value: 4 },
    ],
  },
  {
    title: 'No. of MonitorLists',
    intlId: 'noOfMonitors',
    options: [
      { title: '1', value: 1 },
      { title: '2', value: 2 },
      { title: '3', value: 3 },
      { title: '4', value: 4 },
    ],
  },
];

export const CONFIG_VIEW = 'CONFIG_VIEW';
export const TOOLSET_VIEW = 'TOOLSET_VIEW';
export const IMAGE_VIEW = 'IMAGE_VIEW';
export const PARAMETER_VIEW = 'PARAMETER_VIEW';
export const LIST_VIEW = 'LIST_VIEW';
export const IMAGE_STRIP_VIEW = 'IMAGE_STRIP_VIEW';

export const DEFAULT_RECIPE_VIEWSET = 'DEFAULT_RECIPE_VIEWSET';
export const DEFAULT_EDIT_VIEWSET = 'DEFAULT_EDIT_VIEWSET';
export const DEFAULT_VIEWSET = 'DEFAULT_VIEWSET';

export const DEFAULT_VIEWSET_NAME = 'Initial';

export default class ViewSetFactory {
  getViewSet = (id) => {
    switch (id) {
      case DEFAULT_VIEWSET:
        return this.buildEmptyViewSet();
      case DEFAULT_EDIT_VIEWSET:
      case DEFAULT_RECIPE_VIEWSET:
      default:
        return null;
    }
  };

  createViewInstance = (viewDesc, props) => {
    const meta = this.getViewById(viewDesc.viewComponentClassId);
    if (!meta) {
      return false;
    }

    const key = `${viewDesc.viewComponentClassId}_${viewDesc.id}_${viewDesc.inspectionId}`;

    const passedProps = {
      ...meta.passedProps,
      ...props,
    };
    switch (meta.viewComponentClassId) {
      case CONFIG_VIEW:
        return <RecipeView {...passedProps} key={key} />;
      case TOOLSET_VIEW:
        return <ToolsetView {...passedProps} key={key} />;
      case IMAGE_VIEW:
        return <ImageView {...passedProps} key={key} />;
      case PARAMETER_VIEW:
        return <ParameterView {...passedProps} key={key} />;
      case IMAGE_STRIP_VIEW:
        return <ImageStripView {...passedProps} key={key} />;
      case LIST_VIEW:
        return <ListView {...passedProps} key={key} />;
      default:
        return <div>Unknown Component {meta.viewComponentClassId}</div>;
    }
  };

  getViewById = (id) => {
    switch (id) {
      case CONFIG_VIEW:
        return {
          id: shortId.generate(),
          titleId: 'recipeViewInspectionTemplate',
          viewComponentClassId: CONFIG_VIEW,
          passedProps: { configurations },
        };
      case TOOLSET_VIEW:
        return {
          id: shortId.generate(),
          titleId: 'toolsetView.inspectionTemplate',
          viewComponentClassId: TOOLSET_VIEW,
        };
      case IMAGE_VIEW:
        return {
          id: shortId.generate(),
          titleId: 'imageView.inspectionTemplate',
          viewComponentClassId: IMAGE_VIEW,
        };
      case PARAMETER_VIEW:
        return {
          id: shortId.generate(),
          titleId: 'parameterView.inspectionTemplate',
          viewComponentClassId: PARAMETER_VIEW,
        };
      case IMAGE_STRIP_VIEW:
        return {
          id: shortId.generate(),
          titleId: 'imageStripView.inspectionTemplate',
          viewComponentClassId: IMAGE_STRIP_VIEW,
        };
      case LIST_VIEW:
        return {
          id: shortId.generate(),
          titleId: 'listView.inspectionTemplate',
          viewComponentClassId: LIST_VIEW,
        };
      default:
        return null;
    }
  };

  buildRecipeViewSet = () => {
    const views = [
      this.getViewById(IMAGE_VIEW),
      this.getViewById(LIST_VIEW),
      this.getViewById(IMAGE_STRIP_VIEW),
    ];
    return {
      id: DEFAULT_RECIPE_VIEWSET,
      viewSet: mapViewSetArray(views),
      initialValue: {
        first: {
          direction: 'row',
          splitPercentage: 60,
          first: views[0].id,
          second: views[1].id,
        },
        direction: 'column',
        splitPercentage: 75,
        second: views[2].id,
      },
    };
  };

  buildEmptyViewSet = (name = DEFAULT_VIEWSET_NAME) => {
    const emptyViewId = shortId.generate();
    return {
      id: name,
      name,
      viewSet: {
        [emptyViewId]: {},
      },
      initialValue: emptyViewId,
    };
  };

  buildEditViewSet = () => {
    const views = [
      this.getViewById(TOOLSET_VIEW),
      this.getViewById(IMAGE_VIEW),
      this.getViewById(PARAMETER_VIEW),
    ];
    return {
      id: DEFAULT_VIEWSET,
      viewSet: mapViewSetArray(views),
      initialValue: {
        direction: 'row',
        first: views[0].id,
        splitPercentage: 30,
        second: {
          direction: 'column',
          splitPercentage: 45,
          first: views[1].id,
          second: views[2].id,
        },
      },
    };
  };

  createNewViewSet = () => {
    const views = [
      {
        id: shortId.generate(),
        viewComponentClassId: TOOLSET_VIEW,
        titleId: 'toolsetViewInspectionTemplate',
      },
    ];
    return {
      id: 'NEW_VIEWSET',
      viewSet: mapViewSetArray(views),
      initialValue: views[0].id,
    };
  };
}
