import React, { useState, useEffect } from 'react';
import thunk from 'redux-thunk';
import 'react-tippy/dist/tippy.css';
import { Provider } from 'react-redux';
import { createStore, applyMiddleware, compose } from 'redux';

import Protobuf from './protobuf/Protobuf';
import createReducers from './redux/reducers';
import {
  saveClientChunk,
  setProtobufStaticInitialized,
  setBrowserWindowVisibility,
} from './redux/actions';

import App from './App';
import LoginPage from './pages/LoginPage';
import { loadState } from './utils/localStorage';
import env from './utils/env';

import './App.scss';
import './css/utils.css';
import './css/reset.scss';

const PreLogin = () => {
  const [user, setUser] = useState(null);
  const [authToken, setAuthToken] = useState('');
  const [store, setStore] = useState(null);

  const onLoggedIn = (data) => {
    if (data && data.user && data.authToken) {
      setUser(data.user);
      setAuthToken(data.authToken);
    }
  };

  const onLoggedOut = () => {
    setUser(null);
    setAuthToken('');
  };

  const create = async () => {
    const autoSave = (store) => (next) => (action) => {
      if (env.enableAutoSave) {
        // TODO: for now trigger an auto-save whenever dispatching a "SET_*" action
        const state = store.getState();
        if (
          action.type.substr(0, 4) === 'SET_' &&
          !state.app.saveOperationInProcess &&
          state.app.protobuf
        ) {
          // only store if we have a valid protobuf connection
          const { currentClientChunkRevision, protobuf, user } = state.app;
          store.dispatch(saveClientChunk(protobuf, user, state, currentClientChunkRevision));
        }
      }
      return next(action);
    };

    const store = createStore(
      createReducers({
        websocketUrl: env.websocketServerUrl,
        dispatch: (action) => store.dispatch(action),
      }),
      compose(
        applyMiddleware(thunk, autoSave),
        window.devToolsExtension ? window.devToolsExtension() : (f) => f,
      ),
    );

    await Protobuf.init();
    store.dispatch(setProtobufStaticInitialized());

    setStore(store);
  };

  useEffect(() => {
    if ((user && authToken) || window.location.pathname === '/hmi-v1') {
      create();
    }
  }, [user, authToken]);

  const fromLocalStorage = loadState();
  if (fromLocalStorage) {
    if (fromLocalStorage.user && !user) {
      setUser(fromLocalStorage.user);
    }

    if (fromLocalStorage.authToken && !authToken) {
      setAuthToken(fromLocalStorage.authToken);
    }
  }

  if (!(user && authToken) && window.location.pathname !== '/hmi-v1') {
    return <LoginPage onLogin={(data) => onLoggedIn(data)} />;
  } else {
    if (!store) {
      return <div></div>;
    }

    document.addEventListener('visibilitychange', function () {
      if (document.visibilityState === 'visible') {
        store.dispatch(setBrowserWindowVisibility(true));
        clearTimeout(this.hibernateTimer);
      } else {
        this.hibernateTimer = setTimeout(() => {
          store.dispatch(setBrowserWindowVisibility(false));
        }, 10000);
      }
    });

    return (
      <Provider store={store}>
        <App user={user} authToken={authToken} onLoggedOut={() => onLoggedOut()} />
      </Provider>
    );
  }
};

export default PreLogin;
