import each from 'lodash/each';
import logger from '../utils/logger';
import Protobuf from './Protobuf';

export default class InspectionSubscription {
  constructor({ inspectionId, productHistoryManager, protobuf, paused }) {
    this.inspectionId = inspectionId;
    this.productHistoryManager = productHistoryManager;
    this.protobuf = protobuf;
    this.lastTrigger = -1;
    this.subsById = new Map();
    this.paused = paused;
    this.selectedTrigger = null;
    this.baseSub = null;
  }

  pause() {
    this.paused = true;
  }

  resume() {
    this.paused = false;
    this.selectedTrigger = null;
  }

  replayProduct(product) {
    this.selectedTrigger = product.trigger;
    this.emitProductToAllSubscriptions(product);
  }

  addSubscription(id, desc, cb) {
    const subscription = { id, desc, cb };
    this.subsById.set(id, subscription);

    const { inspectionId } = desc;
    if (this.inspectionId !== inspectionId) {
      throw new Error('Insection id does not match');
    }

    this.resubscribeToStream();
  }

  removeSubscription(id) {
    const subscription = this.subsById.get(id);
    if (!subscription) {
      logger.warn({ id }, 'Unable to unsubscribe listener: unknown id');
      return;
    }

    this.swapLastProduct(subscription, null);

    this.subsById.delete(id);

    this.resubscribeToStream();
  }

  isEmpty() {
    return this.subsById.size === 0;
  }

  getSubscribedImages() {
    return this.uniqueIds('imageIds');
  }

  getSubscribedValues() {
    return this.uniqueIds('valueIds');
  }

  setProtobuf(protobuf) {
    this.protobuf = protobuf;
    // TODO re-subscribe!
  }

  resubscribeToStream() {
    this.unsubscribeFromStream();
    if (!this.isEmpty()) {
      this.subscribeToStream();
    }
  }

  subscribeToStream() {
    // TODO generate nonce per subscription so we know whether we get an event for an old sub
    const params = this.buildReqParams();
    const req = Protobuf.SvPb.GetProductStreamRequest.create(params);
    this.baseSub = this.protobuf.stream(req).subscribe({
      next: (evt) => this.onStreamEvent(req, evt),
      error: (err) => this.onStreamError(err),
      // TODO: how do we want to report complete event to the client?
      //       we could return an event emitter...
      complete: () => logger.debug('Product observable completed'),
    });
  }

  buildReqParams() {
    return {
      inspectionId: this.inspectionId,
      startAtTriggerCount: this.getStartAtTriggerCount(),
      includeFailStatusValues: true,
      valueIds: this.uniqueIds('valueIds'),
      imageIds: this.uniqueIds('imageIds'),
      rejectValueIds: this.uniqueIds('rejectValueIds'),
      rejectImageIds: this.uniqueIds('rejectImageIds'),
      rejectsOnly: this.isRejectsOnly(),
      imageFormat: Protobuf.SvPb.ImageFormatEnum.bmp,
      includeOverlays: true,
    };
  }

  getStartAtTriggerCount() {
    let startAtTriggerCount = 0;
    this.subsById.forEach((sub) => {
      if (
        !isNaN(sub.desc.startAtTriggerCount) &&
        sub.desc.startAtTriggerCount < startAtTriggerCount
      ) {
        startAtTriggerCount = sub.desc.startAtTriggerCount;
      }
    });
    if (startAtTriggerCount === -1 && this.trigger > 0) {
      return this.trigger;
    }
    return startAtTriggerCount;
  }

  isRejectsOnly() {
    let rejectsOnly = true;
    this.subsById.forEach((sub) => {
      if (!sub.desc.rejectsOnly) {
        rejectsOnly = false;
      }
    });
    return rejectsOnly;
  }

  uniqueIds(key) {
    const ids = new Set();
    this.subsById.forEach((sub) => {
      each(sub.desc[key], (id) => ids.add(id));
    });
    return Array.from(ids);
  }

  unsubscribeFromStream() {
    if (this.baseSub && this.baseSub.unsubscribe) {
      this.baseSub.unsubscribe();
    }
    this.baseSub = null;
  }

  async onStreamEvent(req, rawEvent) {
    const { trigger, isReject } = rawEvent;
    const images = this.gatherImages(req, rawEvent);
    const values = this.gatherValues(req, rawEvent);

    const productData = {
      trigger,
      isReject,
      images,
      values,
    };

    const product = await this.productHistoryManager.addProductData(this.inspectionId, productData);
    this.lastTrigger = trigger;
    this.emitProductToAllSubscriptions(product);
  }

  gatherImages(req, rawEvent) {
    const { imageIds, rejectImageIds } = req;
    const { isReject, images, rejectImages, overlays: overlaysList, rejectOverlays } = rawEvent;

    const result = new Map();

    imageIds.forEach((id, idx) => {
      const image = (images || [])[idx];
      const overlays = (overlaysList || [])[idx];
      if (image) {
        result.set(id, {
          id,
          image,
          overlays,
        });
      }
    });

    if (isReject) {
      rejectImageIds.forEach((id, idx) => {
        const image = (rejectImages || [])[idx];
        const overlays = (rejectOverlays || [])[idx];
        if (image) {
          result.set(id, {
            id,
            image,
            overlays,
          });
        }
      });
    }

    return result;
  }

  gatherValues(req, rawEvent) {
    const { valueIds, rejectValueIds } = req;
    const { isReject, values, rejectValues } = rawEvent;

    const result = new Map();

    valueIds.forEach((id, idx) => {
      const value = (values || [])[idx];
      if (value) {
        result.set(id, {
          id,
          value,
        });
      }
    });

    if (isReject) {
      rejectValueIds.forEach((id, idx) => {
        const value = (rejectValues || [])[idx];
        if (value) {
          result.set(id, {
            id,
            value,
          });
        }
      });
    }

    return result;
  }

  emitProductToAllSubscriptions(product) {
    this.subsById.forEach((subscription) => {
      this.emitProductToSubscription(subscription, product);
    });
  }

  emitProductToSubscription(subscription, product) {
    const hasRequestedData = this.hasAllRequestedDataForSubscription(product, subscription);
    const isInterestedIn = this.isInterestedInNewPorduct(product, subscription);
    if (hasRequestedData && isInterestedIn) {
      product.lock();
      subscription.cb({ product });
      this.swapLastProduct(subscription, product);
    }
  }

  hasAllRequestedDataForSubscription(product, subscription) {
    // if only reject values requested and product is not a reject
    if (!subscription.desc.imageIds && !subscription.desc.valueIds && !product.isReject) {
      return false;
    }
    return (
      this.hasDataForSubscriptionInList(subscription.desc.valueIds, product.values) ||
      this.hasDataForSubscriptionInList(subscription.desc.rejectValueIds, product.values) ||
      this.hasDataForSubscriptionInList(subscription.desc.imageIds, product.images) ||
      this.hasDataForSubscriptionInList(subscription.desc.rejectImageIds, product.images)
    );
  }

  isInterestedInNewPorduct(product, subscription) {
    if (this.selectedTrigger) {
      return this.selectedTrigger === product.trigger;
    }
    if (subscription.lastProduct) {
      return (
        subscription.desc.allowOldTrigger || subscription.lastProduct.trigger < product.trigger
      );
    }
    return true;
  }

  hasDataForSubscriptionInList(requestedIds, map) {
    const arr = requestedIds || [];
    for (let i = 0; i < arr.length; ++i) {
      if (!map.has(arr[i])) {
        return false;
      }
    }
    return true;
  }

  swapLastProduct(subscription, product) {
    if (subscription.lastProduct && !subscription.desc.manualRelease) {
      subscription.lastProduct.release();
    }
    subscription.lastProduct = product;
  }

  onStreamError(err) {
    // TODO: forward err to callback
    logger.error({ err }, 'Stream error');
    // TODO: unsubscribe all in case of error!
  }
}
