import { combineReducers } from 'redux';
import { routerReducer as routing } from 'react-router-redux';

import appReducer from './appReducer';
import viewSetReducer from './viewSetReducer';
import toolsetReducer from './toolsetReducer';
import viewDataReducer from './viewDataReducer';

function createReducers(params) {
  return combineReducers({
    app: appReducer(params),
    viewSet: viewSetReducer,
    toolSet: toolsetReducer,
    inspections: viewDataReducer,
    routing,
  });
}

export default createReducers;
