import LZString from 'lz-string';

import logger from '../utils/logger';
import { CONFIGURATION_CHUNK_LIST } from '../utils/localStorage';
import Protobuf from './Protobuf';
import { isObjectValid } from '../utils/objectValidator';
import { reviverJsonToMap } from '../utils/jsonMapper';

const extractConfigName = (filename) => {
  if (!filename) {
    return '';
  }
  const slashIdx = filename.lastIndexOf('\\');
  if (slashIdx !== -1) {
    filename = filename.substr(slashIdx + 1);
  }
  const dotIdx = filename.lastIndexOf('.');
  if (dotIdx !== -1) {
    filename = filename.substr(0, dotIdx);
  }
  return filename;
};

const getConfigName = async (protobuf) => {
  const request = Protobuf.SvPb.GetConfigurationInfoRequest.create();
  const { fileName } = await protobuf.request(request);
  return extractConfigName(fileName);
};

const getListNames = async (protobuf) => {
  let listNames = [];
  try {
    const monitorListReq = Protobuf.SvPb.QueryMonitorListNamesRequest.create({});
    const monitorListRes = await protobuf.request(monitorListReq);
    const listNamesStr =
      monitorListRes &&
      monitorListRes.names &&
      monitorListRes.names.item &&
      monitorListRes.names.item.strVal;
    listNames = (listNamesStr || '').split(';');
  } catch (e) {
    // ignore error. will fall back to empty monitor list.
    logger.error('Error while fetching monitor list names:', e);
  }

  return listNames;
};

const getInspections = async (protobuf) => {
  try {
    const inspectionsReq = Protobuf.SvPb.GetInspectionsRequest.create({});
    const inspectionsRes = await protobuf.request(inspectionsReq);
    return (inspectionsRes.inspections || []).map((inspection) => ({
      id: inspection.id,
      name: inspection.name,
    }));
  } catch (e) {
    // ignore error. will fall back to empty inspection name list.
    logger.error('Error while fetching inspection name list:', e);
    return [];
  }
};

const getInspectionIds = (inspections) => {
  return (inspections || []).map((inspection) => inspection.id);
};

const getInspectionNames = (inspections) => {
  return (inspections || []).map((inspection) => inspection.name);
};

const getInspectionMappings = async (protobuf, listNames, inspections) => {
  const inspectionMappings = new Map();
  for (let i = 0; i < listNames.length; ++i) {
    let imageNames = [];
    let valueNames = [];
    try {
      const listItemReq = Protobuf.SvPb.QueryListItemRequest.create({
        listName: listNames[i],
        queryValues: true,
        queryImages: true,
        type: Protobuf.SvPb.ListType.all,
      });
      const listItemRes = await protobuf.request(listItemReq);
      imageNames = listItemRes.imageDefList.map((image) => image.name);
      valueNames = listItemRes.valueDefList.map((value) => value.name);
    } catch (e) {
      // ignore error and fall back to empty list
      logger.error('Error while fetching list item:', e);
    }

    inspections.forEach((inspection) => {
      const inspectionImages = imageNames.filter((name) => name.indexOf(inspection.name) !== -1);
      const inspectionValues = valueNames.filter((name) => name.indexOf(inspection.name) !== -1);
      if (inspectionImages.length > 0 || inspectionValues.length > 0) {
        // inspections can be assigned to a single monitor list only
        inspectionMappings.set(inspection.id, {
          listName: listNames[i],
          images: inspectionImages,
          values: inspectionValues,
        });
      }
    });
  }

  return inspectionMappings;
};

// objectIDIndex has the format:
//   {#123}
// and sometimes also an index at the end:
//   {#123}[10]
// or this for empty arrays
//   {#123}[ ]
// except of the index at the end, it is a normal id
const parseObjectIdIndex = (str) => {
  if (!str.startsWith('{#')) {
    return;
  }
  const endIdx = str.indexOf('}');
  if (endIdx < 0) {
    return;
  }
  // TODO parse array index
  str = str.substr(2, endIdx);
  return parseInt(str, 10);
};

const cleanObjectSelectorNodeRecursively = (node) => {
  const { name, type, location, objectIDIndex, children, selected } = node;
  const cleanedChildren = (children || []).map(cleanObjectSelectorNodeRecursively);
  const id = (objectIDIndex && parseObjectIdIndex(objectIDIndex)) || null;
  return {
    id,
    name,
    type,
    location,
    selected,
    children: cleanedChildren,
  };
};

const getIdNameMappingForInspection = async (protobuf, inspectionId, inspectionName) => {
  const req = Protobuf.SvPb.GetObjectSelectorItemsRequest.create({
    areas: [Protobuf.SvPb.SearchArea.toolsetItems],
    inspectionID: inspectionId,
    instanceID: 0, // InvalidObjectId
    attribute: Protobuf.SvPb.ObjectAttributes.viewable,
    wholeArray: true,
    type: Protobuf.SvPb.ObjectSelectorType.allValueObjects,
    attributesAllowed: 1,
  });
  const res = await protobuf.request(req);
  if (!res.tree) {
    logger.error('GetObjectSelectorItemsRequest response does not contain a tree');
    return null;
  }

  const tree = cleanObjectSelectorNodeRecursively(res.tree);

  const entries = new Map();
  const queue = (tree && tree.children && tree.children.slice(0)) || [];
  while (queue.length > 0) {
    const head = queue.shift();
    const { id, name, type, location, children } = head;
    if (id) {
      entries.set(id, {
        id,
        name,
        type,
        location,
        fullLocation: 'Inspections.' + inspectionName + '.' + location,
      });
    }
    if (children && children.length) {
      for (let i = 0; i < children.length; ++i) {
        queue.push(children[i]);
      }
    }
  }

  return entries;
};

const getObjectSelectorItemsForInspection = async (protobuf, inspectionId, inspectionName) => {
  const req = Protobuf.SvPb.GetObjectSelectorItemsRequest.create({
    areas: [Protobuf.SvPb.SearchArea.toolsetItems],
    inspectionID: inspectionId,
    instanceID: 0, // InvalidObjectId
    attribute: Protobuf.SvPb.ObjectAttributes.viewable,
    wholeArray: true,
    type: Protobuf.SvPb.ObjectSelectorType.allValueObjects,
    attributesAllowed: 1,
  });
  let res;
  try {
    res = await protobuf.request(req);
  } catch (error) {
    logger.error(error);
    return null;
  }

  if (!res.tree) {
    logger.error('GetObjectSelectorItemsRequest response does not contain a tree');
    return null;
  }

  const tree = cleanObjectSelectorNodeRecursively(res.tree);
  return { tree };
};

const getObjectSelectorImagesForInspection = async (protobuf, inspectionId, inspectionName) => {
  const req = Protobuf.SvPb.GetObjectSelectorItemsRequest.create({
    areas: [Protobuf.SvPb.SearchArea.toolsetItems],
    inspectionID: inspectionId,
    type: Protobuf.SvPb.ObjectSelectorType.allImageObjects,
    attribute: Protobuf.SvPb.ObjectAttributes.archivableImage,
    attributesAllowed: 1,
  });
  let res;
  try {
    res = await protobuf.request(req);
  } catch (error) {
    logger.error(error);
    return null;
  }

  if (!res.tree) {
    logger.error('GetObjectSelectorItemsRequest response does not contain a tree');
    return null;
  }
  const tree = cleanObjectSelectorNodeRecursively(res.tree);

  return { tree };
};

const getVersion = async (protobuf, requestClass) => {
  const request = requestClass.create();
  const response = await protobuf.request(request);
  if (!response.version) {
    logger.error(`Error while reading version via ${requestClass.name}`);
    return 'Unknown Version';
  }
  return response.version;
};

const getVersions = async (protobuf) => {
  const svObserverVersion = await getVersion(protobuf, Protobuf.SvPb.GetSVObserverVersionRequest);
  const gatewayVersion = await getVersion(protobuf, Protobuf.SvPb.GetGatewayVersionRequest);
  const webAppVersion = await getVersion(protobuf, Protobuf.SvPb.GetWebAppVersionRequest);
  return { svObserverVersion, gatewayVersion, webAppVersion };
};

const getPerInspection = async (protobuf, inspectionIds, inspectionNames, fn) => {
  const itemsByInspectionId = new Map();
  for (let i = 0; i < inspectionIds.length; ++i) {
    const inspectionId = inspectionIds[i];
    const inspectionName = inspectionNames[i];
    const item = await fn(protobuf, inspectionId, inspectionName);
    if (item) {
      itemsByInspectionId.set(inspectionId, item);
    }
  }
  return itemsByInspectionId;
};

const getConfigurationTree = async (protobuf) => {
  const request = Protobuf.SvPb.GetConfigurationTreeRequest.create();
  const response = await protobuf.request(request);
  if (!response || !response.tree) {
    logger.error('Cannot get config tree');
    return false;
  }
  return response;
};

const mapInspectionIdtoToolset = async (protobuf) => {
  const inspectionToolsetMappings = new Map();

  const configurationTree = await getConfigurationTree(protobuf);
  if (!isObjectValid(() => configurationTree.tree.children)) {
    logger.error('configTree is not valid');
    return inspectionToolsetMappings;
  }

  configurationTree.tree.children.forEach((inspection) => {
    const inspectionId = inspection.objectID;
    if (!inspectionId) {
      logger.error(`Inspection without valid id: ${inspectionId}`);
      return;
    }
    inspectionToolsetMappings.set(inspectionId, { ...inspection });
  });

  return inspectionToolsetMappings;
};

export default class Configuration {
  static async discover(protobuf) {
    if (!protobuf || !protobuf.isConnected()) {
      return null;
    }

    try {
      const name = await getConfigName(protobuf);
      const versions = await getVersions(protobuf);
      const listNames = await getListNames(protobuf);
      const inspections = await getInspections(protobuf);
      const inspectionIds = getInspectionIds(inspections);
      const inspectionNames = getInspectionNames(inspections);
      const inspectionMappings = await getInspectionMappings(protobuf, listNames, inspections);
      const idNameMappings = await getPerInspection(
        protobuf,
        inspectionIds,
        inspectionNames,
        getIdNameMappingForInspection,
      );
      const objectSelectorItems = await getPerInspection(
        protobuf,
        inspectionIds,
        inspectionNames,
        getObjectSelectorItemsForInspection,
      );
      const objectSelectorImages = await getPerInspection(
        protobuf,
        inspectionIds,
        inspectionNames,
        getObjectSelectorImagesForInspection,
      );
      const inspectionToolsetMappings = await mapInspectionIdtoToolset(protobuf);
      return new Configuration({
        name,
        listNames,
        inspections,
        inspectionIds,
        inspectionMappings,
        // TODO create lookup table with id->fullLocation and fullLocation->id
        idNameMappings,
        objectSelectorItems,
        objectSelectorImages,
        versions,
        inspectionToolsetMappings,
      });
    } catch (err) {
      logger.error({ err }, 'Error loading configuration');
      return null;
    }
  }

  static async loadFromCache(protobuf) {
    if (!protobuf || !protobuf.isConnected()) {
      return null;
    }
    try {
      let configString = '';
      const chunksString = localStorage.getItem(CONFIGURATION_CHUNK_LIST);
      const chunkList = chunksString.split(',');

      configString = chunkList.map((key) => localStorage.getItem(key)).join();
      const decompressedConfig = LZString.decompress(configString);
      const config = JSON.parse(decompressedConfig, reviverJsonToMap);

      return new Configuration({
        ...config,
      });
    } catch (err) {
      logger.error({ err }, 'Error loading configuration');
      return null;
    }
  }

  static extractConfigNameFromFileName(filename) {
    return extractConfigName(filename);
  }

  constructor(configuration) {
    // copy all provided fields into this.
    // this allows treating the configuration instance like a
    // normal json object, but with additional helper functions.
    Object.assign(this, configuration);
  }

  getSvObserverVersion() {
    return this.versions && this.versions.svObserverVersion;
  }

  getGatewayVersion() {
    return this.versions && this.versions.gatewayVersion;
  }

  getWebAppVersion() {
    return this.versions && this.versions.webAppVersion;
  }

  getInspections() {
    return this.inspections;
  }

  getInspectionName(inspectionId) {
    const inspection = (this.inspections || []).find((i) => i.id === inspectionId);
    return inspection && inspection.name;
  }

  getValueNameForId(inspectionId, id) {
    if (typeof inspectionId === 'string') {
      inspectionId = parseInt(inspectionId, 10);
    }
    const mappings = this.idNameMappings.get(inspectionId);
    if (mappings) {
      if (typeof id === 'string') {
        id = parseInt(id, 10);
      }
      const mapping = mappings.get(id);
      if (mapping) {
        return mapping.fullLocation;
      }
    }
    return null;
  }

  getListNameForInspection(inspectionId) {
    const inspection = this.inspectionMappings.get(inspectionId);
    return inspection && inspection.listName;
  }

  getValueNamesForInspection(inspectionId) {
    const inspection = this.inspectionMappings.get(inspectionId);
    return inspection && inspection.values;
  }

  getImageNamesForInspection(inspectionId) {
    const inspection = this.inspectionMappings.get(inspectionId);
    return inspection && inspection.images;
  }

  getAllListNames() {
    // we could return this.listNames but we only want to return
    // the list names that are really in use by an inspection.
    const listNames = [];
    for (let value of this.inspectionMappings.values()) {
      const { listName } = value;
      if (listNames.indexOf(listName) === -1) {
        listNames.push(listName);
      }
    }
    return listNames;
  }

  getObjectSelectorItemsForInspection(inspectionId) {
    return this.objectSelectorItems.get(inspectionId);
  }

  getObjectSelectorImagesForInspection(inspectionId) {
    return this.objectSelectorImages.get(inspectionId);
  }

  // Currently filters array data from values
  // TODO: array data should be shown in tree, but id must be changed (all have same id atm)
  filterArrayValuesFromTree = (tree) => {
    var stack = [],
      node,
      ii;
    stack.push(tree);
    while (stack.length > 0) {
      node = stack.pop();
      if (!node.id && node.children && node.children.length) {
        let firstChildId = '';
        for (ii = 0; ii < node.children.length; ii += 1) {
          if (firstChildId === node.children[ii].id) {
            node.children = [];
            node.id = firstChildId;
            break;
          }
          if (node.children[ii].id) {
            firstChildId = node.children[ii].id;
          }
        }
        for (ii = 0; ii < node.children.length; ii += 1) {
          stack.push(node.children[ii]);
        }
      }
    }
    return tree;
  };

  getConfigTreeByInspectionId(inspectionId) {
    if (!inspectionId) {
      logger.warn('getConfigTreeByInspection did not receive inspection id');
      return;
    }
    return this.inspectionToolsetMappings.get(inspectionId);
  }
}
