import React from 'react';
import { TextField, InputAdornment, IconButton } from '@material-ui/core';
import ClearIcon from '../../../../assets/icons/clear.svg';

const SingleParameter = ({ value, changeCallback, removeCallback, fieldType, defaultValue }) => {
  return (
    <TextField
      fullWidth
      type={fieldType}
      variant="outlined"
      value={value || defaultValue}
      onChange={(e) => changeCallback && changeCallback(e.target.value)}
      InputProps={
        removeCallback
          ? {
              endAdornment: (
                <InputAdornment position="end">
                  <IconButton
                    edge="end"
                    aria-label="remove"
                    onClick={() => removeCallback()}
                    title="Remove"
                  >
                    <img src={ClearIcon} alt="x" />
                  </IconButton>
                </InputAdornment>
              ),
            }
          : null
      }
    />
  );
};

export default SingleParameter;
