import React, { useEffect, useState } from 'react';
import { Button } from '@material-ui/core';
import { FormattedMessage } from 'react-intl';
import Product from '../../protobuf/Product';

import './style.scss';

const RequestedResponseVisualizer = ({ response }) => {
  const [imageUrl, setImageUrl] = useState('');
  const [fileData, setFileData] = useState('');

  useEffect(() => {
    if (response && response.imageData) {
      const product = new Product({
        history: null,
        trigger: null,
        isReject: false,
      });
      setImageUrl(product.buildUrl(response.imageData));
    } else {
      setImageUrl('');
    }
  }, [response]);

  useEffect(() => {
    if (response && response.fileData) {
      const blob = new Blob(response.fileData, { type: 'octet/stream' });
      setFileData(window.URL.createObjectURL(blob));
    } else {
      setFileData('');
    }
  }, [response]);

  const copyResponseToClipboard = async () => {
    if (!navigator.clipboard) {
      // fallback code possible, but I think not needed as we use the debugger only
      return;
    }
    await navigator.clipboard.writeText(result);
  };

  const result = response ? JSON.stringify(response, null, 2) : '';

  return (
    <div className="response-body-container">
      <div className="response-body-header">
        <div className="response-name-type">
          {response ? response.constructor.name : 'Response'}
          <small>Response</small>
        </div>
        <div>
          <Button
            variant="contained"
            color="primary"
            onClick={copyResponseToClipboard}
            disabled={!result}
          >
            <FormattedMessage id="debugger.copyToClipboard" />
          </Button>
        </div>
      </div>

      <div className="response-content">
        {imageUrl ? <img src={imageUrl} alt="" /> : undefined}
        {fileData ? <a href={fileData}>Download</a> : undefined}
        <pre className="response-formatter">{result}</pre>
      </div>
    </div>
  );
};

export default RequestedResponseVisualizer;
