import ReconnectingWebSocket from 'reconnecting-websocket';

export default class WebsocketConnector {
  constructor(onConnect, onMessage, onClose, onError, url, token) {
    this.onClose = onClose;
    const protocol = token ? ['access_token', token] : undefined;
    const options = { debug: false };
    this.socket = new ReconnectingWebSocket(url, protocol, options);
    this.socket.binaryType = 'arraybuffer';
    this.socket.onopen = () => {
      onConnect && onConnect(this.socket);
    };
    this.socket.onmessage = (e) => onMessage && onMessage(e.data, this.socket);
    this.socket.onerror = (e) => onError && onError(e);
    this.socket.onclose = () => onClose && onClose(this.socket);
    this.socket.onconnecting = (e) => console.log('CONNECTING!', e);
  }

  closeSocket() {
    if (this.socket) {
      this.socket.close();
    }
  }
}
