import React from 'react';
import { useDispatch } from 'react-redux';
import { BrowserRouter as Router, Route, Redirect } from 'react-router-dom';

import HmiLoginPage from './pages/HmiLoginPage';
import LogoutPage from './pages/LogoutPage';
import SVObserver from './pages/SVObserver';
import PermissionPage from './pages/PermissionPage';
import DebuggerPage from './pages/DebuggerPage';

const App = ({ user, authToken, onLoggedOut }) => {
  const dispatch = useDispatch();
  dispatch({
    type: 'LOG_IN',
    authToken,
    user,
  });

  return (
    <Router>
      <div>
        <Route exact path={'/'} render={() => <Redirect to="/app" />} />
        <Route
          path={'/app'}
          render={(props) => <SVObserver {...props} onLoggedOut={onLoggedOut} />}
        />
        <Route path={'/hmi-v1'} component={HmiLoginPage} />
        <Route path={'/permissions'} component={PermissionPage} />
        <Route
          path={'/logout'}
          render={(props) => <LogoutPage {...props} onLoggedOut={onLoggedOut} />}
        />
        <Route path={'/debugger'} component={DebuggerPage} />
      </div>
    </Router>
  );
};

export default App;
