import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { FormattedMessage } from 'react-intl';

import ListSelection from '../../../../components/ListSelection';
import InspectionIcon from '../../../../assets/icons/show.svg';
import BackIcon from '../../../../assets/icons/back.svg';

class InspectionSelection extends Component {
  getSelectionItems = () => {
    const { configuration } = this.props;
    const inspections = (configuration && configuration.getInspections()) || [];
    const items = inspections.map((inspection) => {
      return {
        id: inspection.id,
        text: inspection.name,
        icon: InspectionIcon,
      };
    });

    return items;
  };

  render() {
    const { onSelection, goBack, showCancel } = this.props;
    const cancelButtonProps = {
      text: <FormattedMessage id="viewTypeSelection.back" />,
      icon: BackIcon,
      goBack,
    };

    const selectionItems = this.getSelectionItems();

    return (
      <div>
        <ListSelection
          headline={<FormattedMessage id="inspectionSelection.selectInspection" />}
          onSelection={onSelection}
          cancelButtonProps={cancelButtonProps}
          showCancel={showCancel}
          selectionItems={selectionItems}
        />
      </div>
    );
  }
}

InspectionSelection.defaultProps = {};

InspectionSelection.propTypes = {
  onSelection: PropTypes.func.isRequired,
  configuration: PropTypes.object,
  goBack: PropTypes.func,
  showCancel: PropTypes.bool,
};

const mapStateToProps = (state) => ({
  configuration: state.app.configuration,
});

export default connect(mapStateToProps)(InspectionSelection);
