import React from 'react';

import ArrayParameter from '../../generic/ArrayParameter';
import TextParameter from '../TextParameter';

const TextArrayParameter = ({ value, changeCallback, typeName }) => {
  return (
    <ArrayParameter
      value={value}
      changeCallback={changeCallback}
      typeName={typeName}
      defaultValue={''}
      SingleParameterComponent={TextParameter}
    />
  );
};

export default TextArrayParameter;
