import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import React, { Component } from 'react';
import { TextField, InputAdornment, IconButton } from '@material-ui/core';
import { withStyles, MuiThemeProvider, createMuiTheme } from '@material-ui/core/styles';

import { setSaveFlag } from '../../../../redux/actions';
import { canEditParameterView } from '../../../../utils/permissions';
import OutlinedSelection from '../../../../components/OutlinedSelection';
import ClearIcon from '../../../../assets/icons/clear.svg';

const styles = () => ({
  header: {
    fontSize: '1.8rem',
    fontWeight: 'bold',
    marginBottom: 30,
  },
  input: {
    height: 37,
    fontSize: '1.4rem',
  },
  label: {
    fontSize: '1.4rem',
    transform: 'translate(14px, 11px) scale(1)',
  },
});

const theme = createMuiTheme({
  typography: {
    useNextVariants: true,
    htmlFontSize: 10,
  },
});

class OverlayPropertyList extends Component {
  onChange = (changedValue) => {
    this.props.setSaveFlag(true);
    this.props.onChange(changedValue);
  };

  renderTextField = ({ name, id, disabled }) => {
    const { currentOverlay, classes } = this.props;

    return (
      <TextField
        label={name}
        style={{ width: '100%' }}
        id={id}
        type="text"
        variant="outlined"
        disabled={disabled || !currentOverlay}
        InputProps={{ classes: { input: classes.input } }}
        InputLabelProps={{ classes: { root: classes.label } }}
        value={(currentOverlay && currentOverlay[id]) || ''}
        onChange={(e) => this.onChange({ [id]: e.target.value })}
      />
    );
  };

  renderNumberField = ({ name, id, min, max, disabled }) => {
    const { currentOverlay, classes } = this.props;

    const value = (currentOverlay && currentOverlay[id]) || '';
    const trimToMinMax = (min, max, value) => {
      let valueNumber = Number(value);
      if (valueNumber > max) {
        valueNumber = max;
      }
      if (valueNumber < min) {
        valueNumber = min;
      }

      return valueNumber;
    };
    return (
      <TextField
        label={name}
        style={{ width: '100%' }}
        id={id}
        type="number"
        variant="outlined"
        disabled={disabled || !currentOverlay}
        InputProps={{
          classes: { input: classes.input },
          inputProps: { min, max },
        }}
        InputLabelProps={{ classes: { root: classes.label } }}
        value={value}
        onChange={(e) => this.onChange({ [id]: trimToMinMax(min, max, e.target.value) })}
      />
    );
  };

  renderSelection = ({ name, id, options, disabled }) => {
    const { currentOverlay } = this.props;

    return (
      <OutlinedSelection
        selectedValue={currentOverlay && currentOverlay[id]}
        values={options}
        onSelectionChanged={this.onChange}
        name={name}
        id={id}
        disabled={disabled || !currentOverlay}
      />
    );
  };

  renderObjectSelector = ({ name, id, disabled }) => {
    const { currentOverlay, classes, inspectionId, configuration } = this.props;

    let value =
      (currentOverlay &&
        currentOverlay[id] &&
        configuration.getValueNameForId(inspectionId, currentOverlay[id])) ||
      '';

    return (
      <TextField
        label={name}
        style={{ width: '100%' }}
        id={id}
        type="text"
        variant="outlined"
        disabled={disabled || !currentOverlay}
        InputProps={{
          classes: { input: classes.input },
          endAdornment: (
            <InputAdornment position="end">
              <IconButton
                edge="end"
                aria-label="clear"
                onClick={() => this.onChange({ [id]: '' })}
                title={id}
              >
                <img src={ClearIcon} alt="x" />
              </IconButton>
            </InputAdornment>
          ),
        }}
        InputLabelProps={{ classes: { root: classes.label } }}
        value={value}
        onFocus={() => this.props.openSelection(name, id)}
      />
    );
  };

  render() {
    const { classes, imageDimensions, myPermissions } = this.props;
    const imageWidth = imageDimensions?.width;
    const imageHeight = imageDimensions?.height;

    const minWidth = Number(-imageWidth * 2);
    const maxWidth = Number(imageWidth * 2);

    const minHeight = Number(-imageHeight * 2);
    const maxHeight = Number(imageHeight * 2);

    const canEdit = canEditParameterView(myPermissions);
    return (
      <div className="parameter-view-overlay-overlaypropertylist">
        <MuiThemeProvider theme={theme}>
          <label className={classes.header}>Overlay parameters</label>
          {this.renderTextField({ name: 'Label', id: 'label', disabled: !canEdit })}

          {this.renderNumberField({
            name: 'X',
            id: 'x',
            min: minWidth,
            max: maxWidth,
            disabled: !canEdit,
          })}
          {this.renderObjectSelector({ name: 'X Variable', id: 'xVariable', disabled: !canEdit })}

          {this.renderNumberField({
            name: 'Offset Left',
            id: 'offsetLeft',
            min: minWidth,
            max: maxWidth,
            disabled: !canEdit,
          })}
          {this.renderObjectSelector({
            name: 'Offset Left Variable',
            id: 'offsetLeftVariable',
            disabled: !canEdit,
          })}

          {this.renderNumberField({
            name: 'Y',
            id: 'y',
            min: minHeight,
            max: maxHeight,
            disabled: !canEdit,
          })}
          {this.renderObjectSelector({ name: 'Y Variable', id: 'yVariable', disabled: !canEdit })}

          {this.renderNumberField({
            name: 'Offset Top',
            id: 'offsetTop',
            min: minHeight,
            max: maxHeight,
            disabled: !canEdit,
          })}
          {this.renderObjectSelector({
            name: 'Offset Top Variable',
            id: 'offsetTopVariable',
            disabled: !canEdit,
          })}

          {this.renderNumberField({
            name: 'Width',
            id: 'width',
            min: minWidth,
            max: maxWidth,
            disabled: !canEdit,
          })}
          {this.renderObjectSelector({
            name: 'Width Variable',
            id: 'widthVariable',
            disabled: !canEdit,
          })}

          {this.renderNumberField({
            name: 'Height',
            id: 'height',
            min: minHeight,
            max: maxHeight,
            disabled: !canEdit,
          })}
          {this.renderObjectSelector({
            name: 'Height Variable',
            id: 'heightVariable',
            disabled: !canEdit,
          })}
          {this.renderTextField({ name: 'Color', id: 'color', disabled: !canEdit })}
          {this.renderObjectSelector({
            name: 'Color Variable',
            id: 'colorVariable',
            disabled: !canEdit,
          })}

          {this.renderSelection({
            name: 'Line Style',
            id: 'lineStyle',
            options: ['solid', 'dotted', 'dashed'],
            disabled: !canEdit,
          })}
        </MuiThemeProvider>
      </div>
    );
  }
}

const propTypes = {
  overlay: PropTypes.object,
  onChange: PropTypes.func.isRequired,
};

OverlayPropertyList.propTypes = propTypes;

const mapStateToProps = (state) => {
  return {
    configuration: state.app.configuration,
    myPermissions: state.app.myPermissions,
  };
};

const mapDispatchToProps = (dispatch) => ({
  setSaveFlag: (hasChanges) => dispatch(setSaveFlag(hasChanges)),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(withStyles(styles)(OverlayPropertyList));
