import Product from './Product';

export default class ProductHistory {
  constructor(inspectionId) {
    this.inspectionId = inspectionId;
    this.history = [];
  }

  async addProductData(productData) {
    const { trigger, isReject } = productData;
    const entry = this.history.find((e) => e.trigger === trigger);
    let product = null;
    if (entry) {
      product = entry.product;
    } else {
      product = new Product({ trigger, isReject, history: this });
      this.history.push({
        trigger,
        product,
      });
    }

    await product.addData(productData);

    return product;
  }

  releaseProduct(trigger) {
    const entry = this.history.find((entry) => entry.trigger === trigger);
    if (!entry) {
      return;
    }

    const wasReleased = entry.product.releaseImpl();
    if (wasReleased) {
      this.history = this.history.filter((e) => e !== entry);
    }
  }
}
