import shortId from 'shortid';
import PropTypes from 'prop-types';
import React, { Component } from 'react';
import { injectIntl, FormattedMessage } from 'react-intl';

import iconBack from '../../assets/icons/back.svg';
import iconSave from '../../assets/icons/save.svg';
import Heading from '../../components/typo/Heading';
import iconClose from '../../assets/icons/close.svg';
import iconBefore from '../../assets/icons/before.svg';
import ActionBar from '../../components/ActionBar';
import ScrollContainer from '../../components/layout/ScrollContainer';

class RecipeView extends Component {
  renderConfiguration = (configuration) => (
    <div className="flex">
      <span className="sans-serif mr2 basis-100">
        <FormattedMessage id={configuration.intlId} />:
      </span>
      <span className="basis-100">
        <select className="bn w-100 tc">
          {configuration.options.map((option) => (
            <option key={option.title} value={option.value}>
              {option.title}
            </option>
          ))}
        </select>
      </span>
    </div>
  );

  render() {
    const { configurations, viewActions } = this.props;
    return (
      <div>
        <ScrollContainer>
          <div className="pa3 flex items-center">
            <div className="basis-100">
              <div>
                <Heading type="h3" intlId="name" />
                <input
                  type="text"
                  className="w-100 bg-near-white mb3 ba b--near-white outline-0 pa1"
                />
              </div>
              <div>
                <Heading type="h3" intlId="comment" />
                <textarea className="w-100 h4 bg-near-white ba b--near-white outline-0 pa1" />
              </div>
            </div>
            <div className="basis-100 pa4 pt0 self-baseline">
              <Heading type="h3" intlId="configuration" />
              {configurations.map((configuration) => (
                <div className="bb b--near-white pa1 mb1" key={shortId.generate()}>
                  {this.renderConfiguration(configuration)}
                </div>
              ))}
            </div>
          </div>
        </ScrollContainer>
        <ActionBar isViewAction actions={viewActions} />
      </div>
    );
  }
}

RecipeView.propTypes = {
  configurations: PropTypes.arrayOf(
    PropTypes.shape({
      intlId: PropTypes.string,
      options: PropTypes.arrayOf(
        PropTypes.shape({
          title: PropTypes.string,
          value: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
        }),
      ),
    }),
  ).isRequired,
  viewActions: PropTypes.arrayOf(
    PropTypes.shape({
      intlId: PropTypes.string,
    }),
  ),
};

RecipeView.defaultProps = {
  viewActions: [
    { intlId: 'back', iconUrl: iconBack },
    { intlId: 'before', iconUrl: iconBefore },
    { intlId: 'save', iconUrl: iconSave },
    { intlId: 'cancel', iconUrl: iconClose },
  ],
};

export default injectIntl(RecipeView);
