import React from 'react';
import PropTypes from 'prop-types';

import './style.scss';

const Logo = ({ src, title }) => (
  <div className="logo-wrapper">
    <img src={src} alt={title} />
  </div>
);

Logo.propTypes = {
  src: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired,
};

export default Logo;
