import PropTypes from 'prop-types';
import React, { Component } from 'react';
import { Select, OutlinedInput, InputLabel, FormControl } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';

const styles = () => ({
  input: {
    height: 37,
    fontSize: '1.4rem',
  },
  select: {
    paddingTop: 0,
    paddingBottom: 0,
    fontSize: '1.4rem',
  },
  label: {
    transform: 'translate(14px, 11px) scale(1)',
    fontSize: '1.4rem',
  },
});

class OutlinedSelection extends Component {
  state = {
    labelWidth: 0,
  };

  componentDidMount() {
    this.setState({
      labelWidth: this.InputLabelRef.offsetWidth,
    });
  }

  renderOptions = (values, id, labels) => {
    labels = labels || values;
    return values.map((name, counter) => {
      return (
        <option value={name} key={id + counter}>
          {labels[counter]}
        </option>
      );
    });
  };

  render() {
    const { values, labels, selectedValue, name, id, classes, disabled } = this.props;

    return (
      <FormControl variant="outlined" style={{ width: '100%' }} disabled={disabled}>
        <InputLabel
          htmlFor={id}
          ref={(ref) => {
            this.InputLabelRef = ref;
          }}
          classes={{ outlined: classes.label }}
        >
          {name}
        </InputLabel>
        <Select
          native
          value={selectedValue || ''}
          onChange={(e) => this.props.onSelectionChanged({ [id]: e.target.value })}
          classes={{ root: classes.input }}
          input={
            <OutlinedInput
              name={name}
              labelWidth={this.state.labelWidth}
              id={id}
              classes={{ input: classes.select }}
            />
          }
        >
          {!disabled && this.renderOptions(values, id, labels)}
        </Select>
      </FormControl>
    );
  }
}

OutlinedSelection.defaultProps = {
  values: [],
  selectedValue: '',
  name: '',
  id: '',
};

OutlinedSelection.propTypes = {
  values: PropTypes.array.isRequired,
  selectedValue: PropTypes.string.isRequired,
  onSelectionChanged: PropTypes.func,
  name: PropTypes.string,
  id: PropTypes.string,
};

export default withStyles(styles)(OutlinedSelection);
