import React, { Component } from 'react';
import { SortableContainer, SortableElement, arrayMove } from 'react-sortable-hoc';

import './style.scss';
import iconTakeOver from '../../../../assets/icons/take-over.svg';
import iconDeleteDustbin from '../../../../assets/icons/delete-dustbin.svg';
import iconDown from '../../../../assets/icons/down.svg';
import iconUp from '../../../../assets/icons/up.svg';

import ActionBar from '../../../../components/ActionBar';
import ActionBarType from '../../../../types/ActionBarType';
import AvailableFilterListItemsType from './types/AvailableFilterListType';

const SortableItem = SortableElement(({ value }) => (
  <li className="filter-list-row" key={value}>
    <span className="filter-list-row-item">{value}</span>
  </li>
));

const SortableList = SortableContainer(({ items }) => (
  <ul>
    {items.map((item, index) => (
      <SortableItem key={item.value} index={index} value={item.value} />
    ))}
  </ul>
));

class Filter extends Component {
  constructor(props) {
    super(props);
    this.state = {
      CurrentFilterListItems: this.props.CurrentFilterListItems,
    };
  }
  onSortEnd = ({ oldIndex, newIndex }) => {
    this.setState({
      CurrentFilterListItems: arrayMove(this.state.CurrentFilterListItems, oldIndex, newIndex),
    });
  };
  render() {
    const { AvailableFilterListItems, AvailableFilterListActions, CurrentFilterListActions } =
      this.props;
    return (
      <div className="parameter-view-filter-wrapper">
        <div className="available-filters filter-list">
          <div className="filter-header">Available Filters</div>
          <ul className="filter-content">
            {AvailableFilterListItems.map((item) => (
              <li className="filter-list-row" key={item.value}>
                <span className="filter-list-row-item">{item.value}</span>
              </li>
            ))}
          </ul>
          <div className="filter-list-actions">
            <ActionBar actions={AvailableFilterListActions} />
          </div>
        </div>

        <div className="current-filters filter-list">
          <div className="filter-header">Available Filters</div>
          <ul className="filter-content">
            <SortableList
              helperClass="filter-list-row-item-on-drag"
              items={this.state.CurrentFilterListItems}
              onSortEnd={this.onSortEnd}
            />
          </ul>
          <div className="filter-list-actions">
            <ActionBar actions={CurrentFilterListActions} />
          </div>
        </div>

        <div className="property-container">
          <div className="filter-header">Properties</div>
        </div>
      </div>
    );
  }
}

Filter.defaultProps = {
  AvailableFilterListItems: [
    { id: '', value: 'Closing Filter', onClick: () => {} },
    { id: '', value: 'Custom Filter', onClick: () => {} },
    { id: '', value: 'Custom2 Filter', onClick: () => {} },
    { id: '', value: 'Dilation Filter', onClick: () => {} },
    { id: '', value: 'EdgeDetect Filter', onClick: () => {} },
    { id: '', value: 'EdgeDetect2 Filter', onClick: () => {} },
    { id: '', value: 'Erosion Filter', onClick: () => {} },
    { id: '', value: 'HorizonEdge Filter', onClick: () => {} },
    { id: '', value: 'LaplacianEdge Filter', onClick: () => {} },
    { id: '', value: 'LaplacianEdge2 Filter', onClick: () => {} },
    { id: '', value: 'MAX MIN Filter', onClick: () => {} },
    { id: '', value: 'Opening Filter', onClick: () => {} },
    { id: '', value: 'Ranking Filter', onClick: () => {} },
    { id: '', value: 'Sharpen Filter', onClick: () => {} },
    { id: '', value: 'Sharpen2 Filter', onClick: () => {} },
    { id: '', value: 'Skeleton Filter', onClick: () => {} },
    { id: '', value: 'Smooth Filter', onClick: () => {} },
    { id: '', value: 'Thickening Filter', onClick: () => {} },
    { id: '', value: 'Thinning Filter', onClick: () => {} },
    { id: '', value: 'VertEdge Filter', onClick: () => {} },
    { id: '', value: 'Watershed Filter', onClick: () => {} },
  ],

  AvailableFilterListActions: [
    { intlId: 'before', iconUrl: iconTakeOver, onClickHandler: () => {} },
  ],

  CurrentFilterListActions: [
    { intlId: 'down', iconUrl: iconDown, onClickHandler: () => {} },
    { intlId: 'up', iconUrl: iconUp, onClickHandler: () => {} },
    { intlId: 'delete', iconUrl: iconDeleteDustbin, onClickHandler: () => {} },
  ],

  CurrentFilterListItems: [
    { id: '', value: 'Custom Filter', onClick: () => {} },
    { id: '', value: 'Dilation Filter', onClick: () => {} },
    { id: '', value: 'Closing Filter', onClick: () => {} },
  ],
};

Filter.propTypes = {
  AvailableFilterListItems: AvailableFilterListItemsType,
  AvailableFilterListActions: ActionBarType,
  CurrentFilterListItems: AvailableFilterListItemsType,
  CurrentFilterListActions: ActionBarType,
};

export default Filter;
