import React, { useState, useRef } from 'react';
import { injectIntl, FormattedMessage } from 'react-intl';
import { TextField, Button } from '@material-ui/core';

import Dialog from '../../../../components/Dialog';
import { languages } from '../../../../translations';
import './styles.scss';

const AliasEditor = ({ open, onClose, selectedAlias, intl }) => {
  if (!selectedAlias.alias) {
    selectedAlias.alias = {};
  }

  const oldState = useRef({ ...selectedAlias.alias });
  const [alias, setAlias] = useState(selectedAlias || {});

  if (alias.id !== selectedAlias.id) {
    setAlias(selectedAlias);
  }

  const renderLanguageAliases = () => {
    const result = [];
    (languages || []).forEach((language) => {
      result.push(
        <div className="row" key={`rowDiv${language.locale}${selectedAlias.id}`}>
          <div className="label">Alias {language.name}:</div>
          <div className="textfield">
            <TextField
              id={`aliasLabel${language.locale}`}
              fullWidth
              defaultValue={selectedAlias.alias && selectedAlias.alias[language.locale]}
              onChange={(e) => {
                alias.alias[language.locale] = e.target.value;
              }}
            />
          </div>
        </div>,
      );
    });
    return result;
  };

  return (
    <Dialog
      isInView
      isPopup
      title={intl.messages['listView.AliasEditor.title']}
      onClose={() => {
        alias.alias = oldState.current;
        onClose(alias);
      }}
      open={open}
    >
      <div className="alias-editor-wrapper">
        <div className="row">
          <div className="label">
            <FormattedMessage id="listView.AliasEditor.dottedName" />:
          </div>
          <div className="textfield">
            <TextField id="dottedNameLabel" fullWidth value={selectedAlias.name} disabled />
          </div>
        </div>
        <div className="row">
          <div className="label">
            <FormattedMessage id="listView.AliasEditor.value" />:
          </div>
          <div className="textfield">
            <TextField id="valueLabel" fullWidth disabled value={selectedAlias.value} />
          </div>
        </div>
        {renderLanguageAliases()}
        <div className="buttons">
          <Button variant="contained" color="primary" onClick={() => onClose(alias, true)}>
            <FormattedMessage id="listView.AliasEditor.button" />
          </Button>
        </div>
      </div>
    </Dialog>
  );
};

export default injectIntl(AliasEditor);
