import React, { Component } from 'react';
import { Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import Checkbox from '@material-ui/core/Checkbox';
import Button from '@material-ui/core/Button';
import cloneDeep from 'lodash/cloneDeep';
import { FormattedMessage } from 'react-intl';

import ArrowIcon from '../../assets/icons/back.svg';
import Protobuf from '../../protobuf/Protobuf';
import {
  flattenProtobuf,
  canReadUserManagement,
  canEditUserManagement,
  getGroupPermissionsFromFlattenedPermissions,
} from '../../utils/permissions';

import './style.scss';

class PermissionManagement extends Component {
  constructor(props) {
    super(props);
    this.allAvailablePermissions = new Set();
    this.state = {
      redirect: false,
      initiallySelectedPermissions: new Map(),
      currentSelectedPermissions: new Map(),
    };
  }

  componentDidMount() {
    if (Protobuf.SvPb.Permissions) {
      this.allAvailablePermissions = flattenProtobuf(Protobuf.SvPb.Permissions);
      this.loadCurrentPermissions(this.props.groupPermissions);
    }
  }

  componentDidUpdate(prevProps) {
    if (
      (prevProps.groupPermissions === undefined && this.props.groupPermissions) ||
      prevProps.groupPermissions !== this.props.groupPermissions
    ) {
      this.loadCurrentPermissions(this.props.groupPermissions);
    }
  }

  loadCurrentPermissions = (permissions) => {
    if (permissions) {
      const initiallySelectedPermissions = permissions;
      const currentSelectedPermissions = cloneDeep(initiallySelectedPermissions);
      this.setState({ initiallySelectedPermissions, currentSelectedPermissions });
    }
  };

  onCheck(groupName, key, checked) {
    const newPermissions = cloneDeep(this.state.currentSelectedPermissions);
    if (!newPermissions.has(groupName)) {
      newPermissions.set(groupName, { groupPermissions: new Set(), users: [] });
    }
    if (checked) {
      newPermissions.get(groupName).groupPermissions.add(key);
    } else {
      newPermissions.get(groupName).groupPermissions.delete(key);
    }
    this.setState({ currentSelectedPermissions: newPermissions });
  }

  savePermissions = (flattenedPermissions) => {
    const result = getGroupPermissionsFromFlattenedPermissions(flattenedPermissions);
    this.props.proxy.setUserPermissions(result);
  };

  onSaveClicked = () => {
    this.savePermissions(this.state.currentSelectedPermissions);
  };

  onResetClicked = () => {
    this.setState({
      currentSelectedPermissions: cloneDeep(this.state.initiallySelectedPermissions),
    });
  };

  mapsAreEqual(am, bm) {
    if (am.size !== bm.size) {
      return false;
    }
    for (var a of am) {
      if (!bm.has(a[0])) {
        return false;
      }

      const bs = bm.get(a[0]).groupPermissions;
      if (a[1].groupPermissions.size !== bs.size) {
        return false;
      }

      for (var as of a[1].groupPermissions) {
        if (!bs.has(as)) {
          return false;
        }
      }
    }
    return true;
  }

  getColGroup() {
    const { currentSelectedPermissions } = this.state;
    const colGroup = [];

    colGroup.push(<col style={{ width: '200px' }} key={`col_permissions`}></col>);
    currentSelectedPermissions.forEach((permissionsPerGroup, groupName) => {
      colGroup.push(<col style={{ width: '80px' }} key={`col_${groupName}`}></col>);
    });
    colGroup.push(<col style={{ width: '*' }} key={`col_descriptions`}></col>);

    return colGroup;
  }

  getTableHead() {
    const { currentSelectedPermissions } = this.state;
    const tableheader = [];

    tableheader.push(
      <td key={`header_td_permissions`}>
        <div className="permission-management-permissions-header-left">
          <FormattedMessage id="permissions.permissions" />
        </div>
      </td>,
    );
    currentSelectedPermissions.forEach((permissionsPerGroup, groupName) => {
      tableheader.push(
        <td key={`header_td_${groupName}`}>
          <div className="permission-management-permissions-header">{groupName}</div>
          <div className="permission-management-permissions-subheader">
            {Array.isArray(permissionsPerGroup.users) && permissionsPerGroup.users.length > 0
              ? `(${permissionsPerGroup.users.join(', ')})`
              : undefined}
          </div>
        </td>,
      );
    });
    tableheader.push(
      <td key={`header_td_description`}>
        <div className="permission-management-permissions-header">
          <FormattedMessage id="permissions.description" />
        </div>
      </td>,
    );

    return <tr>{tableheader}</tr>;
  }

  getData(canEdit) {
    const { currentSelectedPermissions } = this.state;
    const data = [];
    this.allAvailablePermissions.forEach((permission) => {
      const dataRow = [];
      dataRow.push(
        <td key={`body_td_permission_${permission}`}>
          <div className="permission-management-label">{permission}</div>
        </td>,
      );
      currentSelectedPermissions.forEach((permissionsPerGroup, groupName) => {
        const selected = permissionsPerGroup.groupPermissions.has(permission);
        dataRow.push(
          <td key={`body_td_${groupName}_${permission}`}>
            <Checkbox
              type="checkbox"
              checked={selected}
              name={`checkbox_${groupName}_${permission}`}
              color="primary"
              className="permission-management-checkbox"
              onClick={(e) =>
                canEdit && groupName !== 'sdm'
                  ? this.onCheck(groupName, permission, e.target.checked)
                  : undefined
              }
              disabled={!canEdit || groupName === 'sdm'}
            />
          </td>,
        );
      });
      dataRow.push(
        <td key={`body_td_description_${permission}`}>
          <div className="permission-management-label">
            <FormattedMessage id={`permissionDescription.${permission}`} />
          </div>
        </td>,
      );
      data.push(<tr key={`body_tr_${permission}`}>{dataRow}</tr>);
    });
    return data;
  }

  render() {
    const { redirect, initiallySelectedPermissions, currentSelectedPermissions } = this.state;
    const { myPermissions } = this.props;

    const canRead = canReadUserManagement(myPermissions);
    const canEdit = canEditUserManagement(myPermissions);

    if (redirect) {
      return <Redirect to="/app" />;
    }

    const hasChanges = !this.mapsAreEqual(initiallySelectedPermissions, currentSelectedPermissions);
    const colGroup = this.getColGroup();
    const tablehead = this.getTableHead();
    const data = this.getData(canEdit);

    return (
      <div className="permission-management-wrapper">
        <div className="permission-management-fixed-header">
          <Button
            variant="contained"
            color="primary"
            onClick={() => this.setState({ redirect: true })}
          >
            <img src={ArrowIcon} alt="back" className="image-item" />
            <FormattedMessage id="permissions.backToWebApp" />
          </Button>
          <Button
            variant="contained"
            color="primary"
            disabled={!hasChanges}
            onClick={this.onSaveClicked}
          >
            Save
          </Button>
          <Button
            variant="contained"
            color="primary"
            disabled={!hasChanges}
            onClick={this.onResetClicked}
          >
            Reset
          </Button>
        </div>
        <h1>
          <FormattedMessage id="permissions.permissionManagement" />
        </h1>
        <h3>
          <FormattedMessage id="permissions.helpText" />
        </h3>
        {canRead || canEdit ? (
          <table className="permission-management-table-wrapper">
            <colgroup>{colGroup}</colgroup>
            <thead>{tablehead}</thead>
            <tbody>{data}</tbody>
          </table>
        ) : undefined}
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  groupPermissions: state.app.groupPermissions,
  proxy: state.app.proxy,
  user: state.app.user,
  myPermissions: state.app.myPermissions,
});

export default connect(mapStateToProps, null)(PermissionManagement);
