import { withRouter, Redirect } from 'react-router-dom';
import { FormattedMessage } from 'react-intl';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import { canReadUserManagement, canEditUserManagement } from '../../../../utils/permissions';
import UserIcon from '../../../../assets/icons/user.svg';
import HelpIcon from '../../../../assets/icons/help.svg';
import ShortcutHelpIcon from '../../../../assets/icons/shortcut-help.svg';
import AboutIcon from '../../../../assets/icons/info.svg';
import LogoutIcon from '../../../../assets/icons/logout.svg';
import PermissionsIcon from '../../../../assets/icons/permissions.svg';
import MenuList from '../../../../components/MenuList';
import ShortcutView from '../../../ShortcutView';
import HelpView from '../../../HelpView';
import AboutView from '../../../AboutView';

import './style.scss';

class UserInfo extends Component {
  state = {
    shortcutViewOpen: false,
    isHelpViewOpen: false,
    isAboutViewOpen: false,
    gotoPermissions: false,
  };

  render() {
    const { user, myPermissions } = this.props;
    const menuItems = [
      {
        key: 'help',
        caption: <FormattedMessage id="topNavigation.help" />,
        img: HelpIcon,
        onClick: () => {
          this.setState({ isHelpViewOpen: true });
        },
      },
      {
        key: 'shortcuthelp',
        caption: <FormattedMessage id="topNavigation.shortcut" />,
        img: ShortcutHelpIcon,
        onClick: () => {
          this.setState({
            shortcutViewOpen: !this.state.shortcutViewOpen,
          });
        },
      },
      {
        key: 'about',
        caption: <FormattedMessage id="topNavigation.about" />,
        img: AboutIcon,
        onClick: () => {
          this.setState({ isAboutViewOpen: true });
        },
      },
      {
        key: 'logout',
        caption: <FormattedMessage id="topNavigation.logout" />,
        img: LogoutIcon,
        onClick: async () => {
          setImmediate(() => (window.location.href = `${window.location.origin}/logout`));
        },
      },
    ];

    if (canReadUserManagement(myPermissions) || canEditUserManagement(myPermissions)) {
      menuItems.unshift({
        key: 'permissions',
        caption: <FormattedMessage id="topNavigation.permissions" />,
        img: PermissionsIcon,
        onClick: () => {
          this.setState({ gotoPermissions: true });
        },
      });
      if (this.state.gotoPermissions) {
        return <Redirect to="/permissions" />;
      }
    }

    return (
      <div>
        <div className="flex">
          <MenuList
            buttonIcon={UserIcon}
            buttonCaption={user.fullname}
            menuItems={menuItems}
            showBottomArrow
            onClose={() => {}}
            open={false}
          />
        </div>
        <div>
          <HelpView
            open={this.state.isHelpViewOpen}
            onClose={() => this.setState({ isHelpViewOpen: false })}
          />
          <ShortcutView
            open={this.state.shortcutViewOpen}
            onClose={() => this.setState({ shortcutViewOpen: false })}
          />
          <AboutView
            open={this.state.isAboutViewOpen}
            onClose={() => this.setState({ isAboutViewOpen: false })}
          />
        </div>
      </div>
    );
  }
}

UserInfo.propTypes = {
  user: PropTypes.shape({
    name: PropTypes.string,
  }).isRequired,
};

const mapStateToProps = (state) => ({
  user: state.app.user,
  myPermissions: state.app.myPermissions,
});

export default connect(mapStateToProps, null)(withRouter(UserInfo));
