import logger from './logger';
import Protobuf from '../protobuf/Protobuf';
import modes from '../utils/modes';
import { addVariableValueShouldBeInTheRangeNotification } from '../redux/actions/index';

const MID_SHORT_SIZE = 20;

export const drawInspectionOverlays = ({
  drawTarget,
  zoomFactor,
  overlays,
  boundingCoordinates,
  width,
  height,
  currentMode,
}) => {
  if (!overlays || overlays.length === 0) {
    logger.error('drawInspectionOverlays expects overlays but did not receive');
    return;
  }
  const targetContext = drawTarget.getContext('2d');
  zoomFactor = zoomFactor || 1;
  if (overlays && overlays.length > 0) {
    targetContext.setLineDash([]);
    overlays.forEach((overlay) => {
      const colorInt = (overlay.color && overlay.color.value) || 0;
      const colorString = getRgbaString(colorInt);
      targetContext.fillStyle = colorString;
      const fontSize = 10 / zoomFactor;
      targetContext.lineWidth = 1 / zoomFactor;
      targetContext.font = fontSize + 'px Arial';
      targetContext.strokeStyle = colorString;

      const boundingShape = overlay.boundingShape.shape;

      switch (boundingShape) {
        case Protobuf.SvPb.OverlayBoundingShape.fields.rect.name:
          renderRectangleTool(
            targetContext,
            zoomFactor,
            fontSize,
            colorString,
            overlay,
            boundingCoordinates,
            currentMode,
          );
          break;
        case Protobuf.SvPb.OverlayBoundingShape.fields.slice.name:
          renderSliceTool(targetContext, overlay, boundingCoordinates);
          break;
        case Protobuf.SvPb.OverlayBoundingShape.fields.perspective.name:
          renderPerspectiveTool(
            targetContext,
            zoomFactor,
            overlay,
            boundingCoordinates,
            width,
            height,
          );
          break;
        default:
          logger.error('Unknown boundingShape received');
          return;
      }
    });
  }
};

const renderRectangleTool = (
  targetContext,
  zoomFactor,
  fontSize,
  colorString,
  overlay,
  boundingCoordinates,
  currentMode,
) => {
  const boundingBoxX = Math.abs(boundingCoordinates.minX) + overlay.boundingShape.rect.x.value || 0;
  const boundingBoxY = Math.abs(boundingCoordinates.minY) + overlay.boundingShape.rect.y.value || 0;
  const boundingBoxWidth = overlay.boundingShape.rect.w.value || 0;
  const boundingBoxHeight = overlay.boundingShape.rect.h.value || 0;
  const { displayBounding, shapeGroups, name: overlayTitle } = overlay;

  const angle = overlay.boundingShape.rect.angle && overlay.boundingShape.rect.angle.value;

  targetContext.fillText(overlayTitle, boundingBoxX, boundingBoxY - 10 / zoomFactor);

  shapeGroups &&
    shapeGroups.forEach((shapeGroup) => {
      // Do not show detailLevel 2 OverlayShapes in run mode
      if (shapeGroup.detailLevel === 2 && currentMode === modes.runMode.id) {
        return;
      }

      shapeGroup.shapes.forEach((shape) => {
        const shapeColor = shape.color && shape.color.value;
        const shapeColorString = getRgbaString(shapeColor);
        const { shape: shapeType } = shape;

        const useRotation =
          angle && angle > 0 && shapeType !== Protobuf.SvPb.OverlayShape.fields.arrow.name;

        if (useRotation) {
          rotateContextByAngleAroundPoint(targetContext, angle, boundingBoxX, boundingBoxY);
        }

        switch (shapeType) {
          case Protobuf.SvPb.OverlayShape.fields.arrow.name:
            const x1 = Math.abs(boundingCoordinates.minX) + shape.arrow.x1.value || 0;
            const y1 = Math.abs(boundingCoordinates.minY) + shape.arrow.y1.value || 0;
            const x2 = Math.abs(boundingCoordinates.minX) + shape.arrow.x2.value || 0;
            const y2 = Math.abs(boundingCoordinates.minY) + shape.arrow.y2.value || 0;

            const calcAngleDegrees = (x, y) => (Math.atan2(y, x) * 180) / Math.PI;

            const angleBetweenP1andP2 = calcAngleDegrees(x2 - x1, y2 - y1);

            targetContext.beginPath();
            targetContext.strokeStyle = shapeColorString || colorString;

            // line
            targetContext.moveTo(x1, y1);
            targetContext.lineTo(x2, y2);

            // arrow
            targetContext.moveTo(x2, y2);
            // arrow length
            const r = 15;
            // calculate one side of arrow and convert to deg
            const theta1 = (Math.PI * (angleBetweenP1andP2 + 135)) / 180.0;
            targetContext.lineTo(x2 + r * Math.cos(theta1), y2 + r * Math.sin(theta1));

            targetContext.moveTo(x2, y2);
            // calculate another side of arrow and convert to deg
            const theta2 = (Math.PI * (angleBetweenP1andP2 - 135)) / 180.0;
            targetContext.lineTo(x2 + r * Math.cos(theta2), y2 + r * Math.sin(theta2));

            targetContext.stroke();
            targetContext.closePath();

            break;

          case Protobuf.SvPb.OverlayShape.fields.rectArray.name:
            targetContext.beginPath();
            targetContext.strokeStyle = shapeColorString || colorString;
            shape.rectArray.rectArray.forEach((rect) => {
              const rAngle = -rect.angle;
              if (rAngle && rAngle < 0) {
                rotateContextByAngleAroundPoint(
                  targetContext,
                  rAngle,
                  boundingBoxX + rect.x,
                  boundingBoxY + rect.y,
                );
              }
              targetContext.rect(
                boundingBoxX + rect.x || 0,
                boundingBoxY + rect.y || 0,
                rect.w || 0,
                rect.h || 0,
              );
              if (rAngle && rAngle < 0) {
                targetContext.restore();
              }
            });
            targetContext.stroke();
            targetContext.closePath();

            break;

          case Protobuf.SvPb.OverlayShape.fields.graph.name:
            targetContext.beginPath();
            targetContext.strokeStyle = shapeColorString || colorString;

            if (shape.graph.x.length === shape.graph.y.length && shape.graph.x.length > 0) {
              targetContext.moveTo(
                boundingBoxX + (shape.graph.x[0] * boundingBoxWidth) / shape.graph.maxValueX,
                boundingBoxY + (shape.graph.y[0] * boundingBoxHeight) / shape.graph.maxValueY,
              );
              for (let i = 0; i < shape.graph.x.length; i++) {
                targetContext.lineTo(
                  boundingBoxX + (shape.graph.x[i] * boundingBoxWidth) / shape.graph.maxValueX,
                  boundingBoxY +
                    boundingBoxHeight -
                    (shape.graph.y[i] * boundingBoxHeight) / shape.graph.maxValueY,
                ); //x
              }
            }

            targetContext.stroke();
            targetContext.closePath();

            break;

          case Protobuf.SvPb.OverlayShape.fields.marker.name:
            targetContext.beginPath();
            targetContext.strokeStyle = shapeColorString || colorString;

            if (shape.marker.orientationType === 1) {
              if (shape.marker.sizeType === 1) {
                const strokeHeight = MID_SHORT_SIZE;
                targetContext.moveTo(
                  boundingBoxX + shape.marker.value.value,
                  boundingBoxY + boundingBoxHeight / 2 - strokeHeight / 2,
                );
                targetContext.lineTo(
                  boundingBoxX + shape.marker.value.value,
                  boundingBoxY + boundingBoxHeight / 2 + strokeHeight / 2,
                );
              } else {
                targetContext.moveTo(
                  boundingBoxX +
                    (shape.marker.value.value * boundingBoxWidth) / shape.marker.maxValue +
                    1,
                  boundingBoxY,
                );
                targetContext.lineTo(
                  boundingBoxX +
                    (shape.marker.value.value * boundingBoxWidth) / shape.marker.maxValue +
                    1,
                  boundingBoxY + boundingBoxHeight,
                );
              }
            } else {
              if (shape.marker.sizeType === 1) {
                const strokeWidth = MID_SHORT_SIZE;
                targetContext.moveTo(
                  boundingBoxX + boundingBoxWidth / 2 - strokeWidth / 2,
                  boundingBoxY + shape.marker.value.value,
                );
                targetContext.lineTo(
                  boundingBoxX + boundingBoxWidth / 2 + strokeWidth / 2,
                  boundingBoxY + shape.marker.value.value,
                );
              } else {
                targetContext.moveTo(
                  boundingBoxX,
                  boundingBoxY +
                    boundingBoxHeight -
                    (shape.marker.value.value * boundingBoxHeight) / shape.marker.maxValue +
                    1,
                );
                targetContext.lineTo(
                  boundingBoxX + boundingBoxWidth,
                  boundingBoxY +
                    boundingBoxHeight -
                    (shape.marker.value.value * boundingBoxHeight) / shape.marker.maxValue +
                    1,
                );
              }
            }

            targetContext.stroke();
            targetContext.closePath();

            break;

          case Protobuf.SvPb.OverlayShape.fields.markers.name:
            targetContext.beginPath();
            targetContext.strokeStyle = shapeColorString || colorString;

            if (shape.markers.orientationType === 1) {
              if (shape.markers.sizeType === 1) {
                const strokeWidth = MID_SHORT_SIZE;
                for (let i = 0; i < shape.markers.value.length; i++) {
                  targetContext.moveTo(
                    boundingBoxX + shape.markers.value[i],
                    boundingBoxY + boundingBoxHeight / 2 - strokeWidth / 2,
                  );
                  targetContext.lineTo(
                    boundingBoxX + shape.markers.value[i],
                    boundingBoxY + boundingBoxHeight / 2 + strokeWidth / 2,
                  );
                }
              } else {
                for (let i = 0; i < shape.markers.value.length; i++) {
                  targetContext.moveTo(boundingBoxX + shape.markers.value[i], boundingBoxY);
                  targetContext.lineTo(
                    boundingBoxX + shape.markers.value[i],
                    boundingBoxY + boundingBoxHeight,
                  );
                }
              }
            } else {
              if (shape.markers.sizeType === 1) {
                const strokeHeight = MID_SHORT_SIZE;
                for (let i = 0; i < shape.markers.value.length; i++) {
                  targetContext.moveTo(
                    boundingBoxX + boundingBoxWidth / 2 - strokeHeight / 2,
                    boundingBoxY + shape.markers.value[i],
                  );
                  targetContext.lineTo(
                    boundingBoxX + boundingBoxWidth / 2 + strokeHeight / 2,
                    boundingBoxY + shape.markers.value[i],
                  );
                }
              } else {
                for (let i = 0; i < shape.markers.value.length; i++) {
                  targetContext.moveTo(boundingBoxX, boundingBoxY + shape.markers.value[i]);
                  targetContext.lineTo(
                    boundingBoxX + boundingBoxWidth,
                    boundingBoxY + shape.markers.value[i],
                  );
                }
              }
            }

            targetContext.stroke();
            targetContext.closePath();

            break;

          case Protobuf.SvPb.OverlayShape.fields.blockMarker.name:
            const colors = shape.blockMarker.colors;
            const changePos = shape.blockMarker.changePos;
            let currentPos = 0;

            for (let i = 0; i <= changePos.length; i++) {
              targetContext.beginPath();
              targetContext.lineWidth = 10 / zoomFactor;
              targetContext.strokeStyle = getRgbaString(colors[i % colors.length]);
              targetContext.moveTo(boundingBoxX + currentPos, boundingBoxY + boundingBoxHeight / 2);

              if (i < changePos.length) {
                targetContext.lineTo(
                  boundingBoxX + changePos[i],
                  boundingBoxY + boundingBoxHeight / 2,
                );
              } else {
                targetContext.lineTo(
                  boundingBoxX + boundingBoxWidth,
                  boundingBoxY + boundingBoxHeight / 2,
                );
              }

              currentPos = changePos[i];
              targetContext.stroke();
              targetContext.closePath();
              targetContext.lineWidth = 1 / zoomFactor;
            }

            break;

          case Protobuf.SvPb.OverlayShape.fields.image.name:
            const { domImage, x, y, w, h } = shape.image;
            if (domImage) {
              // TODO incorporate boundingBoxWidth and boundingBoxHeight
              targetContext.drawImage(
                domImage,
                boundingBoxX + ((x && x.value) || 0),
                boundingBoxY + ((y && y.value) || 0),
                (w && w.value) || 100, // TODO better fallback than 100!
                (h && h.value) || 100, // TODO better fallback than 100!
              );
            }
            break;

          default:
            logger.warn('Received unknown overlay shape', shapeType);
            break;
        }
        if (useRotation) {
          targetContext.restore();
        }
      });
    });

  if (displayBounding) {
    if (angle && angle > 0) {
      rotateContextByAngleAroundPoint(targetContext, angle, boundingBoxX, boundingBoxY);
    }

    targetContext.save();
    targetContext.beginPath();
    targetContext.strokeStyle = colorString;
    targetContext.rect(boundingBoxX, boundingBoxY, boundingBoxWidth, boundingBoxHeight);
    targetContext.stroke();
    targetContext.closePath();

    if (angle && angle > 0) {
      targetContext.restore();
    }
  }

  targetContext.restore();
  const maxOverlayTextX = boundingBoxX + targetContext.measureText(overlayTitle).width;
  updateBoundingCoordinates(
    boundingCoordinates,
    boundingBoxX,
    boundingBoxY - fontSize,
    Math.max(boundingBoxX + boundingBoxWidth, maxOverlayTextX),
    boundingBoxY + boundingBoxHeight,
  );
};

const rotateContextByAngleAroundPoint = (targetContext, angle, x, y) => {
  targetContext.save();
  targetContext.translate(x, y);
  targetContext.rotate((angle * Math.PI) / 180);
  targetContext.translate(-x, -y);
};

const renderSliceTool = (targetContext, overlay, boundingCoordinates) => {
  const { name: overlayTitle } = overlay;

  const centerX = Math.abs(boundingCoordinates.minX) + overlay.boundingShape.slice.centerX.value;
  const centerY = Math.abs(boundingCoordinates.minY) + overlay.boundingShape.slice.centerY.value;
  const innerRadius = overlay.boundingShape.slice.innerRadius.value;
  const outerRadius = overlay.boundingShape.slice.outerRadius.value;
  const startRadian = calculateRadiansFromAngle(overlay.boundingShape.slice.endAngle.value);
  const endRadian = calculateRadiansFromAngle(overlay.boundingShape.slice.startAngle.value);

  targetContext.fillText(overlayTitle, centerX - 20, centerY + 20);

  targetContext.beginPath();

  // Maximum a full circle
  if (endRadian - startRadian <= 2 * Math.PI) {
    targetContext.arc(centerX, centerY, innerRadius, startRadian, endRadian);
    targetContext.lineTo(centerX, centerY);
    targetContext.arc(centerX, centerY, outerRadius, startRadian, endRadian);
    targetContext.lineTo(centerX, centerY);
  } else {
    targetContext.arc(centerX, centerY, innerRadius, startRadian, startRadian + 1.99 * Math.PI);
    targetContext.arc(
      centerX,
      centerY,
      innerRadius + 1,
      startRadian + 1.99 * Math.PI,
      startRadian + 2 * Math.PI,
    );
    targetContext.arc(
      centerX,
      centerY,
      innerRadius + 2,
      startRadian + 2 * Math.PI,
      startRadian + 2.01 * Math.PI,
    );
    targetContext.arc(centerX, centerY, innerRadius + 3, startRadian + 2.01 * Math.PI, endRadian);
    targetContext.lineTo(centerX, centerY);
    targetContext.arc(centerX, centerY, outerRadius, startRadian, startRadian + 1.99 * Math.PI);
    targetContext.arc(
      centerX,
      centerY,
      outerRadius + 1,
      startRadian + 1.99 * Math.PI,
      startRadian + 2 * Math.PI,
    );
    targetContext.arc(
      centerX,
      centerY,
      outerRadius + 2,
      startRadian + 2 * Math.PI,
      startRadian + 2.01 * Math.PI,
    );
    targetContext.arc(centerX, centerY, outerRadius + 3, startRadian + 2.01 * Math.PI, endRadian);
    targetContext.lineTo(centerX, centerY);
  }

  targetContext.stroke();
  targetContext.closePath();

  const maxOverlayTextX = centerX - 20 + targetContext.measureText(overlayTitle).width;
  updateBoundingCoordinates(
    boundingCoordinates,
    centerX - outerRadius,
    centerY - outerRadius,
    Math.max(centerX + outerRadius, maxOverlayTextX),
    centerY + outerRadius,
  );
};

const calculateRadiansFromAngle = (angle) => {
  return ((360 - angle) * Math.PI) / 180;
};

const renderPerspectiveTool = (
  targetContext,
  zoomFactor,
  overlay,
  boundingCoordinates,
  width,
  height,
) => {
  const { name: overlayTitle } = overlay;

  const textWidth = targetContext.measureText(overlayTitle).width;

  targetContext.fillText(overlayTitle, width / 2 - textWidth / 2, 50);

  const boundingBoxX = Math.abs(boundingCoordinates.minX) || 0;
  const boundingBoxY = Math.abs(boundingCoordinates.minY) || 0;
  const boundingBoxWidth = width || 0;
  const boundingBoxHeight = height || 0;
  const offset =
    overlay.boundingShape.perspective.offset && overlay.boundingShape.perspective.offset.value;
  const orientation = overlay.boundingShape.perspective.orientation;

  // Orientation x
  if (orientation === 0) {
    targetContext.moveTo(boundingBoxX, boundingBoxY + boundingBoxHeight / 2);
    targetContext.lineTo(boundingBoxX + offset, boundingBoxY + 1 / zoomFactor);
    targetContext.lineTo(boundingBoxX + boundingBoxWidth - offset, boundingBoxY + 1 / zoomFactor);
    targetContext.lineTo(boundingBoxX + boundingBoxWidth, boundingBoxY + boundingBoxHeight / 2);
    targetContext.lineTo(
      boundingBoxX + boundingBoxWidth - offset,
      boundingBoxY + boundingBoxHeight - 1 / zoomFactor,
    );
    targetContext.lineTo(boundingBoxX + offset, boundingBoxY + boundingBoxHeight - 1 / zoomFactor);
    targetContext.lineTo(boundingBoxX, boundingBoxY + boundingBoxHeight / 2);
  }

  // Orientation y
  if (orientation === 1) {
    targetContext.moveTo(boundingBoxX + 1 / zoomFactor, boundingBoxY + offset);
    targetContext.lineTo(boundingBoxX + boundingBoxWidth / 2, boundingBoxY);
    targetContext.lineTo(boundingBoxX + boundingBoxWidth - 1 / zoomFactor, boundingBoxY + offset);
    targetContext.lineTo(
      boundingBoxX + boundingBoxWidth - 1 / zoomFactor,
      boundingBoxY + boundingBoxHeight - offset,
    );
    targetContext.lineTo(boundingBoxX + boundingBoxWidth / 2, boundingBoxY + boundingBoxHeight);
    targetContext.lineTo(boundingBoxX + 1 / zoomFactor, boundingBoxY + boundingBoxHeight - offset);
    targetContext.lineTo(boundingBoxX + 1 / zoomFactor, boundingBoxY + offset);
  }

  targetContext.stroke();
  targetContext.closePath();
};

const getRgbaString = (colorInt) => {
  if (typeof colorInt !== 'number' || isNaN(colorInt)) {
    logger.debug('Could not parse value into color.');
    // fall back to pink when no valid rgba color value provided
    return 'pink';
  }
  const red = colorInt & 0xff;
  const green = (colorInt >> 8) & 0xff;
  const blue = (colorInt >> 16) & 0xff;
  const alpha = 1;
  return `rgba(${red}, ${green}, ${blue}, ${alpha})`;
};

export const setBoundingCoordinatesForCustomOverlays = ({
  overlay,
  product,
  fontSize,
  boundingCoordinates,
  dispatch,
}) => {
  const coords = { x: 0, y: 0, width: 0, height: 0 };
  setCustomOverlayCoordinates(product, overlay, coords, dispatch);
  const textWidth = getTextWidth(overlay.label, fontSize);
  updateBoundingCoordinates(
    boundingCoordinates,
    coords.x,
    coords.y - fontSize * 2,
    Math.max(coords.x + coords.width, coords.x + textWidth),
    coords.y + coords.height,
  );
};

export const setBoundingCoordinatesForInspectionOverlays = ({
  overlay,
  fontSize,
  boundingCoordinates,
}) => {
  const coords = { x: 0, y: 0, width: 0, height: 0 };
  setInspectionOverlayCoordinates(overlay, coords);
  const textWidth = getTextWidth(overlay.name, fontSize);
  updateBoundingCoordinates(
    boundingCoordinates,
    coords.x,
    coords.y - fontSize * 2,
    Math.max(coords.x + coords.width, coords.x + textWidth),
    coords.y + coords.height,
  );
};

const getTextWidth = (text, fontSize) => {
  const canvas = document.createElement('canvas');
  const ctx = canvas.getContext('2d');
  ctx.font = fontSize + 'px Arial';
  return ctx.measureText(text).width;
};

const setCustomOverlayCoordinates = (product, overlay, coordinates, dispatch = () => {}) => {
  const offsetLeft =
    Number(getValueById(product, overlay.offsetLeftVariable)) || overlay.offsetLeft;
  const x = Number(getValueById(product, overlay.xVariable)) || overlay.x;
  coordinates.x = x + offsetLeft;

  const offsetTop = Number(getValueById(product, overlay.offsetTopVariable)) || overlay.offsetTop;
  const y = Number(getValueById(product, overlay.yVariable)) || overlay.y;
  coordinates.y = y + offsetTop;

  coordinates.width = Number(getValueById(product, overlay.widthVariable)) || overlay.width;
  coordinates.height = Number(getValueById(product, overlay.heightVariable)) || overlay.height;

  // we need to wait here for the fallback values to initialize
  if (
    overlay.maxHeightFallback !== 0 &&
    overlay.maxWidthFallback !== 0 &&
    overlay.minHeightFallback !== 0 &&
    overlay.minWidthFallback !== 0
  ) {
    if (coordinates.x < overlay.minWidthFallback) {
      coordinates.x = overlay.minWidthFallback;
      dispatch(
        addVariableValueShouldBeInTheRangeNotification(
          'X',
          `${overlay.minWidthFallback}  -   ${overlay.maxWidthFallback}`,
        ),
      );
    }

    if (coordinates.x > overlay.maxWidthFallback) {
      coordinates.x = overlay.maxWidthFallback;
      dispatch(
        addVariableValueShouldBeInTheRangeNotification(
          'X',
          `${overlay.minWidthFallback}  -   ${overlay.maxWidthFallback}`,
        ),
      );
    }

    if (coordinates.y < overlay.minHeightFallback) {
      coordinates.y = overlay.minHeightFallback;
      dispatch(
        addVariableValueShouldBeInTheRangeNotification(
          'Y',
          `${overlay.minHeightFallback}  -   ${overlay.maxHeightFallback}`,
        ),
      );
    }

    if (coordinates.y > overlay.maxHeightFallback) {
      coordinates.y = overlay.maxHeightFallback;
      dispatch(
        addVariableValueShouldBeInTheRangeNotification(
          'Y',
          `${overlay.minHeightFallback}  -   ${overlay.maxHeightFallback}`,
        ),
      );
    }
  }
};

const setInspectionOverlayCoordinates = (overlay, coordinates) => {
  if (overlay.boundingShape.rect) {
    coordinates.x = overlay.boundingShape.rect.x.value || 0;
    coordinates.y = overlay.boundingShape.rect.y.value || 0;
    coordinates.width = overlay.boundingShape.rect.w.value || 0;
    coordinates.height = overlay.boundingShape.rect.h.value || 0;
  }
};

export const drawCustomOverlays = ({
  drawTarget,
  customOverlays,
  product,
  zoomFactor,
  boundingCoordinates,
}) => {
  if (!customOverlays || customOverlays.length === 0) {
    return;
  }

  const targetContext = drawTarget.getContext('2d');
  const activeOverlays = customOverlays.filter((overlay) => overlay.isActive);
  zoomFactor = zoomFactor || 1;
  activeOverlays.forEach((overlay) => {
    targetContext.beginPath();

    const coords = { x: 0, y: 0, width: 0, height: 0 };
    setCustomOverlayCoordinates(product, overlay, coords);
    correctCoordsWithBoundingCoordinates(coords, boundingCoordinates);

    const colorVariable = getValueById(product, overlay.colorVariable);
    let color;
    if (!colorVariable) {
      color = overlay.color;
    } else {
      color = getRgbaString(colorVariable);
    }

    targetContext.rect(coords.x, coords.y, coords.width, coords.height);

    targetContext.fillStyle = color;
    targetContext.lineWidth = 1 / zoomFactor;
    const fontSize = 10 / zoomFactor;
    targetContext.font = fontSize + 'px Arial';
    targetContext.fillText(overlay.label, coords.x, coords.y - fontSize);

    switch (overlay.lineStyle) {
      case 'dashed':
        targetContext.setLineDash([5]);
        break;

      case 'dotted':
        targetContext.setLineDash([2, 5]);
        break;

      default:
        targetContext.setLineDash([]);
    }
    targetContext.strokeStyle = color;
    targetContext.stroke();

    targetContext.closePath();
  });
};

const correctCoordsWithBoundingCoordinates = (actualCoordinates, boundingCoordinates) => {
  actualCoordinates.x += Math.abs(boundingCoordinates.minX);
  actualCoordinates.y += Math.abs(boundingCoordinates.minY);
};

const updateBoundingCoordinates = (boundingCoordinates, minX, minY, maxX, maxY) => {
  if (minX < boundingCoordinates.minX) {
    boundingCoordinates.minX = minX;
  }

  if (minY < boundingCoordinates.minY) {
    boundingCoordinates.minY = minY;
  }

  if (maxX > boundingCoordinates.maxX) {
    boundingCoordinates.maxX = maxX;
  }

  if (maxY > boundingCoordinates.maxY) {
    boundingCoordinates.maxY = maxY;
  }
};

const getValueById = (product, id) => {
  if (!product || !id) {
    return;
  }
  const valueObj = product.getValue(id);
  return valueObj && valueObj.value;
};

export const extractInspectionOverlays = (product, imageId) => {
  if (!product || product.released) {
    return undefined;
  }

  const image = product.getImage(imageId);

  if (!image) {
    return undefined;
  }

  return (
    (image &&
      image.overlays &&
      image.overlays.overlays &&
      image.overlays.overlays.length > 0 &&
      image.overlays.overlays) ||
    []
  );
};
