import React, { useEffect } from 'react';
import { InputAdornment, IconButton } from '@material-ui/core';
import OutlinedSelection from '../../../../components/OutlinedSelection';
import ClearIcon from '../../../../assets/icons/clear.svg';

const SelectParameter = ({ value, options, labels, changeCallback, removeCallback }) => {
  useEffect(() => {
    if (typeof value === 'undefined') {
      changeCallback && changeCallback(options[0]);
    }
  });

  let valuesWithLabels = [];

  // if we have a labels array, options and labels need to be sorted together
  // if we don't have label array, the values will be used as id and value in select
  if (labels) {
    options.forEach((option, idx) =>
      valuesWithLabels.push({ option, label: labels[idx] }),
    );

    valuesWithLabels = valuesWithLabels.sort(function(a, b) {
      return a.label.localeCompare(b.label);
    });

    options = valuesWithLabels.map((value) => value.option);
    labels = valuesWithLabels.map((value) => value.label);
  } else {
    options = options.sort();
  }

  return (
    <OutlinedSelection
      selectedValue={value}
      values={options}
      labels={labels}
      onSelectionChanged={(e) => {
        changeCallback && changeCallback(e['param']);
      }}
      id="param"
      InputProps={
        removeCallback
          ? {
              endAdornment: (
                <InputAdornment position="end">
                  <IconButton
                    edge="end"
                    aria-label="remove"
                    onClick={() => removeCallback()}
                    title="Remove"
                  >
                    <img src={ClearIcon} alt="x" />
                  </IconButton>
                </InputAdornment>
              ),
            }
          : null
      }
    />
  );
};

export default SelectParameter;
