import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import React, { Component } from 'react';

import './style.scss';
import iconPlus from '../../../../assets/icons/plus.svg';
import iconDeleteDustbin from '../../../../assets/icons/delete-dustbin.svg';
import ActionBar from '../../../../components/ActionBar/index';
import ActionBarType from '../../../../types/ActionBarType';
import OverlayList from '../../../../components/OverlayList';
import { initViewState, setSharedStateData, setSaveFlag } from '../../../../redux/actions';
import OverlayPropertyList from './OverlayPropertyList';
import VariableSelection from './VariableSelection';
import { updateOverlayList } from '../../../../utils/reduxHelper';
import { canEditParameterView } from '../../../../utils/permissions';

class Overlay extends Component {
  state = {
    currentOverlay: undefined,
    isObjectSelectorOpen: false,
    variableName: 'Variables',
    variableId: '',
    selectedVariables: [],
  };

  UNSAFE_componentWillReceiveProps(nextProps) {
    if (nextProps.overlays && this.state.currentOverlay) {
      const currentOverlay = nextProps.overlays.filter(
        (item) => item.id === this.state.currentOverlay.id,
      )[0];
      this.setState({
        currentOverlay,
      });
    }

    if (nextProps.overlays) {
      this.setInitialOverlay(nextProps.overlays);
    }
  }

  setInitialOverlay = (overlays) => {
    if (!this.state.currentOverlay && overlays && overlays.length > 0) {
      this.setState({
        currentOverlay: overlays[0],
      });
    }
  };

  setAllOverlaysActive = (isActive) => {
    try {
      const updatedOverlays = this.props.overlays.map((overlay) => ({
        ...overlay,
        isActive,
      }));
      this.props.setSharedStateData('overlays', updatedOverlays);
      this.props.setSaveFlag(true);
    } catch (error) {}
  };

  addNewOverlay = () => {
    const { overlays } = this.props;

    const newOverlay = {
      color: '#ebfc02',
      height: 100,
      id: Math.floor(Math.random() * 10000),
      isActive: true,
      label: 'Overlay',
      lineStyle: 'solid',
      width: 100,
      x: 10,
      y: 20,
      offsetLeft: null,
      offsetTop: null,
    };
    try {
      this.props.setSharedStateData('overlays', [...overlays, newOverlay]);
      this.setState({
        currentOverlay: newOverlay,
      });
    } catch (error) {}
  };

  deleteCurrentOverlay = () => {
    if (!this.state.currentOverlay) {
      return;
    }

    try {
      const { overlays } = this.props;
      const updatedOverlays = overlays.filter(
        (overlay) => overlay.id !== this.state.currentOverlay.id,
      );
      this.props.setSharedStateData('overlays', updatedOverlays);

      const newCurrentOverlay = this.getNeighborOverlayId(this.state.currentOverlay.id, overlays);
      this.setState({
        currentOverlay: overlays.filter((item) => item.id === newCurrentOverlay)[0],
      });
    } catch (error) {}
  };

  getNeighborOverlayId = (id, overlays) => {
    for (let i = 0; i < overlays.length; i++) {
      if (overlays[i]['id'] === id) {
        if (overlays[i - 1]) {
          return overlays[i - 1]['id'];
        } else if (overlays[i + 1]) {
          return overlays[i + 1]['id'];
        }
      }
    }
    return -1;
  };

  updateCurrentOverlay = (currentOverlay) => {
    this.setState({
      currentOverlay: currentOverlay,
    });
  };

  updateOverlayList = (updatedOverlayList) => {
    this.props.setSharedStateData('overlays', updatedOverlayList);
    this.props.setSaveFlag(true);
  };

  renderPlaceholder = () => {
    return (
      <div className="parameter-view-overlay-placeholder">
        Please select a source for this parameter view.
      </div>
    );
  };

  setVariableSelection = (selectedVariable) => {
    if (!selectedVariable) {
      this.setState({ isObjectSelectorOpen: false });
      return;
    }
    const { overlays } = this.props;
    const { currentOverlay, variableId, selectedVariables } = this.state;
    const updatedOverlay = {
      ...currentOverlay,
      [variableId]: selectedVariable,
    };

    if (selectedVariable && !selectedVariables.includes(selectedVariable)) {
      selectedVariables.push(selectedVariable);
    }

    const updatedOverlayList = updateOverlayList(updatedOverlay, overlays);
    this.props.setSharedStateData('overlays', updatedOverlayList);
    this.setState({ isObjectSelectorOpen: false, selectedVariables });
    this.props.setSaveFlag(true);
  };

  saveChangeToOverlays = (changedValue, currentOverlay) => {
    const { overlays } = this.props;
    const updatedOverlay = { ...currentOverlay, ...changedValue };
    const updatedOverlayList = updateOverlayList(updatedOverlay, overlays);
    this.props.setSharedStateData('overlays', updatedOverlayList);
  };

  render() {
    const { overlayActions, overlays, inspectionId, id: viewId, imageDimensions } = this.props;
    const { currentOverlay } = this.state;

    if (!overlays) return this.renderPlaceholder();
    return (
      <div className="parameter-view-overlay-wrapper">
        <div className="parameter-view-overlay-table-wrapper">
          <OverlayList
            overlays={overlays}
            currentOverlay={currentOverlay}
            updateCurrentOverlay={this.updateCurrentOverlay}
            updateOverlayList={this.updateOverlayList}
            canSelectRow={true}
            onSelectAll={(checked) => this.setAllOverlaysActive(checked)}
          />
          <ActionBar actions={overlayActions(this)} />
        </div>
        <OverlayPropertyList
          inspectionId={inspectionId}
          viewId={viewId}
          currentOverlay={currentOverlay}
          imageDimensions={imageDimensions}
          onChange={(value) => this.saveChangeToOverlays(value, currentOverlay)}
          openSelection={(variableName, variableId) => {
            this.setState({
              isObjectSelectorOpen: true,
              variableName,
              variableId,
            });
          }}
        />
        <VariableSelection
          title={this.state.variableName}
          onClose={(selectedId) => this.setVariableSelection(selectedId)}
          open={this.state.isObjectSelectorOpen}
          inspectionId={inspectionId}
        />
      </div>
    );
  }
}

/* eslint-disable  */
const defaultPropTypes = {
  overlayActions: (overlay) => {
    const actions = [];
    const canEdit = canEditParameterView(overlay.props.myPermissions);

    if (canEdit) {
      actions.push({
        intlId: 'parameterview.overlay.add',
        iconUrl: iconPlus,
        onClickHandler: () => {
          overlay.props.setSaveFlag(true);
          overlay.addNewOverlay();
        },
      });
    }

    if (canEdit) {
      actions.push({
        intlId: 'parameterview.overlay.delete',
        iconUrl: iconDeleteDustbin,
        onClickHandler: () => {
          overlay.props.setSaveFlag(true);
          overlay.deleteCurrentOverlay();
        },
      });
    }

    return actions;
  },
};

const propTypes = {
  overlays: PropTypes.array,
  overlayActions: ActionBarType,
  currentOverlay: PropTypes.object, //TODO: Add overlay type
};

/* eslint-enable */

Overlay.propTypes = propTypes;
Overlay.defaultProps = defaultPropTypes;

const mapStateToProps = (state, ownProps) => {
  try {
    const { inspectionId } = ownProps;
    const editingViewId = state.inspections[inspectionId].sharedState.editingViewId;
    const overlays = state.inspections[inspectionId].sharedState.views[editingViewId].overlays;
    const imageDimensions = state.inspections[inspectionId].views[editingViewId].imageDimensions;
    const myPermissions = state.app.myPermissions;

    return { overlays, proxy: state.app.proxy, imageDimensions, myPermissions };
  } catch (error) {
    return {};
  }
};

const mapDispatchToProps = (dispatch, ownProps) => {
  const { inspectionId, id: viewId } = ownProps;
  dispatch(initViewState(inspectionId, viewId, {}, {}));
  return {
    setSharedStateData: (key, data) => dispatch(setSharedStateData(inspectionId, key, data)),
    setSaveFlag: (hasChanges) => dispatch(setSaveFlag(hasChanges)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Overlay);
export { defaultPropTypes, propTypes };
