import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import React, { Component } from 'react';
import { Treebeard } from 'react-treebeard';

import ToolSetup from './components/ToolSetup';
import ActionBar from '../../components/ActionBar';
import { setParameter } from '../../redux/actions';
import ActionBarType from '../../types/ActionBarType';
import TreebeardStyle from './components/TreebeardStyle';
import ProxyType from '../../components/Protobuf/ProxyType';
import ScrollContainer from '../../components/layout/ScrollContainer';
import { canReadToolSetView, canEditToolSetView } from '../../utils/permissions';

class ToolsetView extends Component {
  constructor(props) {
    super(props);
    this.treeModel = this.buildTreebearViewModel(this.getToolsetData());
  }

  getToolsetData = () => {
    const treeDataRaw = this.props.configuration.getConfigTreeByInspectionId(
      this.props.inspectionId,
    );
    let children;
    const treeData = ((treeDataRaw && treeDataRaw.children) || []).map((node) => {
      if (!node.children) {
        return { name: node.name };
      }
      children = node.children.map((child) => ({ name: child.name }));
      return { name: node.name, toggled: false, children };
    });
    return treeData;
  };

  onNodeClicked = (node, toggled) => {
    if (this.state && this.state.cursor) {
      this.setState({ cursor: { active: false } });
    }

    // eslint-disable-next-line
    node.active = true;

    if (node.children) {
      // eslint-disable-next-line
      node.toggled = toggled;
    }
    this.setState({
      cursor: node,
    });
  };

  buildTreebearViewModel = (tree) =>
    tree.map((node) => {
      if (node.children) {
        return {
          name: node.name,
          children: node.children,
          toggled: node.toggled,
        };
      }

      return {
        name: node.name,
      };
    });

  render() {
    const { viewActions, myPermissions } = this.props;

    if (canReadToolSetView(myPermissions) || canEditToolSetView(myPermissions)) {
      return (
        <div>
          <ScrollContainer>
            <div className="treebeard">
              <ToolSetup node={this.state && this.state.cursor} />
              <Treebeard
                data={this.treeModel}
                onToggle={this.onNodeClicked}
                style={TreebeardStyle}
              />
            </div>
          </ScrollContainer>
          <ActionBar isViewAction actions={viewActions} />
        </div>
      );
    }
    return <div></div>;
  }
}

const mapDispatchToProps = (dispatch) => ({
  setParameter: (parameter) => dispatch(setParameter(parameter)),
});

const mapStateToProps = (state, ownProps) => {
  try {
    return {
      proxy: state.app.proxy,
      configuration: state.app.configuration,
      myPermissions: state.app.myPermissions,
    };
  } catch (error) {
    return {};
  }
};

ToolsetView.defaultProps = {
  viewActions: [
    // { intlId: 'back', iconUrl: iconBack, onClickHandler: () => {} },
    // { intlId: 'before', iconUrl: iconBefore, onClickHandler: () => {} },
    // { intlId: 'save', iconUrl: iconSave, onClickHandler: () => {} },
    // { intlId: 'undo', iconUrl: iconUndo, onClickHandler: () => {} },
  ],
};

ToolsetView.propTypes = {
  viewActions: ActionBarType.isRequired,
  proxy: ProxyType,
  configuration: PropTypes.object,
};

const mappedConnect = connect(mapStateToProps, mapDispatchToProps);

export default mappedConnect(ToolsetView);
