import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import React, { Component } from 'react';
import { FormattedMessage } from 'react-intl';

import { getPropsFromInspection } from '../../../utils/propHelper';

class ImageInfo extends Component {
  render() {
    const { imageInfoClass, cursorPosition, rgbValues, imageDimensions } = this.props;
    return (
      <div className={imageInfoClass}>
        <div className="info-table">
          <div className="th">
            <div className="td">
              <FormattedMessage id="imageView.info" />
            </div>
          </div>

          <div className="tr">
            <div className="td">
              <FormattedMessage id="imageView.width" />
            </div>
            <div className="td">{imageDimensions.width}</div>
          </div>
          <div className="tr">
            <div className="td">
              <FormattedMessage id="imageView.height" />
            </div>
            <div className="td">{imageDimensions.height}</div>
          </div>
          <div className="tr">
            <div className="td">{`${rgbValues.R}, ${rgbValues.G}, ${rgbValues.B}`}</div>
            <div
              className="td"
              style={{
                backgroundColor: `rgb(${rgbValues.R},${rgbValues.G},${rgbValues.B})`,
              }}
            />
          </div>
          <div className="tr">
            <div className="td">
              <FormattedMessage id="imageView.x" />
            </div>
            <div className="td">{cursorPosition.x}</div>
          </div>
          <div className="tr">
            <div className="td">
              <FormattedMessage id="imageView.y" />
            </div>
            <div className="td">{cursorPosition.y}</div>
          </div>
        </div>
      </div>
    );
  }
}

ImageInfo.defaultProps = {
  imageInfoClass: '',
  rgbValues: { R: 0, G: 0, B: 0 },
  cursorPosition: { width: 0, height: 0 },
  imageDimensions: { width: 0, height: 0 },
};

ImageInfo.propTypes = {
  imageInfoClass: PropTypes.string,
  rgbValues: PropTypes.shape({
    R: PropTypes.number,
    G: PropTypes.number,
    B: PropTypes.number,
  }),
  imageDimensions: PropTypes.shape({
    width: PropTypes.number,
    height: PropTypes.number,
  }),
  cursorPosition: PropTypes.shape({
    x: PropTypes.number,
    y: PropTypes.number,
  }),
};

const mapStateToProps = (state, ownProps) => {
  const { inspectionId, id } = ownProps;
  const viewId = id;
  return getPropsFromInspection(state, inspectionId, viewId, {
    imageDimensions: 'imageDimensions',
    cursorPosition: 'cursorPosition',
    rgbValues: 'rgbValues',
  });
};

export default connect(mapStateToProps)(ImageInfo);
