import axios from 'axios';
import pick from 'lodash/pick';
import jwtDecode from 'jwt-decode';
import env from '../utils/env';
import logger from '../utils/logger';

export const logIn = async (username, password) => {
  const url = `${env.authEndpoint}/login`;
  const loginRes = await axios.post(url, { username, password });
  const { accessToken } = loginRes.data;
  return await authenticate(accessToken);
};

export const authenticate = async (accessToken) => {
  const url = `${env.authEndpoint}/auth`;
  const authRes = await axios.post(url, { accessToken });
  const { authToken } = authRes.data;
  // TODO verify the token signature
  const claims = jwtDecode(authToken);
  const user = pick(claims, ['email', 'fullname', 'username']);
  return { authToken, user };
};

export const logOut =
  (notifyGateway = true) =>
  async () => {
    logger.removeProxy();
    if (notifyGateway) {
      const url = `${env.authEndpoint}/logout`;
      try {
        await axios.post(url, {});
      } catch (err) {
        logger.warn({ err }, 'Error while calling /logout');
      }
    }
  };
