import omit from 'lodash/omit';
import { isObjectValid } from '../../utils/objectValidator';

const findInspectionForView = (viewId, state) =>
  Object.keys(state).find(
    (inspectionId) =>
      state[inspectionId] && state[inspectionId].views && !!state[inspectionId].views[viewId],
  );

const viewDataReducer = (state = {}, action) => {
  const { inspectionId, viewId, key } = action;
  switch (action.type) {
    case 'PURGE_VIEW_STATE': {
      const inspectionId = action.inspectionId || findInspectionForView(action.viewId, state);
      if (!inspectionId) {
        return state;
      }

      return omit(state, [
        `${inspectionId}.sharedState.views.${action.viewId}`,
        `${inspectionId}.views.${action.viewId}`,
        state[inspectionId].sharedState.editingViewId === action.viewId &&
          `${inspectionId}.sharedState.editingViewId`,
      ]);
    }
    case 'SET_EDITING_FOCUS': {
      if (!state[inspectionId]) {
        return state;
      }

      return {
        ...state,
        [inspectionId]: {
          ...state[inspectionId],
          sharedState: {
            ...state[inspectionId].sharedState,
            editingViewId: viewId,
          },
        },
      };
    }

    case 'SET_LIST_VIEW_ALIASES': {
      if (!state[inspectionId]) {
        return state;
      }

      return {
        ...state,
        [inspectionId]: {
          ...state[inspectionId],
          sharedState: {
            ...state[inspectionId].sharedState,
            listViewAliases: action.aliases,
            showAliasesInList: action.showInList,
          },
        },
      };
    }

    case 'UPDATE_LIST_VIEW_SHOW_ALIASES': {
      if (!state[inspectionId]) {
        return state;
      }

      return {
        ...state,
        [inspectionId]: {
          ...state[inspectionId],
          sharedState: {
            ...state[inspectionId].sharedState,
            showAliasesInList: action.showInList,
          },
        },
      };
    }

    case 'SET_VIEW_DATA':
      return {
        ...state,
        [inspectionId]: {
          ...state[inspectionId],
          views: {
            ...(state[inspectionId] ? state[inspectionId].views : {}),
            [viewId]: {
              ...(state[inspectionId] && state[inspectionId].views
                ? state[inspectionId].views[viewId]
                : {}),
              [key]: action.data,
            },
          },
        },
      };

    case 'SAVE_IMAGE_SETTINGS':
      return {
        ...state,
        [inspectionId]: {
          ...state[inspectionId],
          views: {
            ...(state[inspectionId] ? state[inspectionId].views : {}),
            [viewId]: {
              ...(state[inspectionId] && state[inspectionId].views
                ? state[inspectionId].views[viewId]
                : {}),
              zoomFactor: action.zoomFactor,
              imagePosition: action.imagePosition,
            },
          },
        },
      };

    case 'SET_SHARED_STATE_DATA':
      const editingViewId = action.viewId || state[inspectionId].sharedState.editingViewId;
      const newState = {
        ...state,
        [inspectionId]: {
          ...state[inspectionId],
          sharedState: {
            ...(state[inspectionId] ? state[inspectionId].sharedState : {}),
            views: {
              ...(state[inspectionId] ? state[inspectionId].sharedState.views : {}),
              [editingViewId]: {
                ...(state[inspectionId]
                  ? state[inspectionId].sharedState.views[editingViewId]
                  : {}),
                [key]: action.data,
              },
            },
          },
        },
      };
      return newState;

    case 'INIT_VIEW_STATE': {
      const { initState, initSharedState } = action;

      const newState = {
        ...state,
        [inspectionId]: {
          ...state[inspectionId],
          sharedState: {
            ...(state[inspectionId] ? state[inspectionId].sharedState : {}),
            views: {
              ...(state[inspectionId] ? state[inspectionId].sharedState.views : {}),
              [viewId]: isObjectValid(() => state[inspectionId].sharedState.views[viewId])
                ? {
                    ...initSharedState,
                    ...state[inspectionId].sharedState.views[viewId],
                  }
                : initSharedState,
            },
          },
          views: {
            ...(state[inspectionId] ? state[inspectionId].views : {}),
            [viewId]: isObjectValid(() => state[inspectionId].views[viewId])
              ? { ...initState, ...state[inspectionId].views[viewId] }
              : initState,
          },
        },
      };

      const listViewAliases =
        newState[inspectionId] &&
        newState[inspectionId].sharedState &&
        (newState[inspectionId].sharedState.listViewAliases || new Map());
      newState[inspectionId].sharedState.listViewAliases = listViewAliases;

      return newState;
    }

    default:
      return state;
  }
};

export default viewDataReducer;
