import React, { useRef, useState } from 'react';
import classNames from 'classnames';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Mosaic, MosaicContext } from 'react-mosaic-component';
import { FormattedMessage } from 'react-intl';

import './style.scss';
import { mosaicViewType } from './types';
import {
  increaseViewSizeChangeCounter,
  purgeViewState,
  clearCurrentView,
} from '../../redux/actions/index';
import ConfirmDialog from '../../components/ConfirmDialog';
import SVOView from './SVOView';
import { canEditViewSet } from '../../utils/permissions';

const ViewSet = ({
  views,
  onChange,
  topNavHidden,
  bottomNavHidden,
  increaseChangeCounter,
  dispatch,
  inspections,
  myPermissions,
}) => {
  const mosaic = useRef(null);
  const [viewToClose, setViewToClose] = useState(undefined);
  const viewSetClass = classNames({
    'view-set-wrapper': true,
    'bottom-nav-offset': !bottomNavHidden,
    'top-nav-hidden': !topNavHidden,
  });

  if (!views) {
    return false;
  }
  const editingViewIds = Object.keys(inspections).map(
    (inspection) =>
      inspections[inspection].sharedState && inspections[inspection].sharedState.editingViewId,
  );

  const handleConfirmDialogClose = (confirmed) => {
    if (confirmed) {
      closeView();
    }
    setViewToClose(undefined);
  };

  const oneViewInViewset = () => {
    return typeof views.initialValue === 'string';
  };

  const closeView = () => {
    const { viewId, path, remove } = viewToClose;
    const inspectionId = views.viewSet[viewId] && views.viewSet[viewId].inspectionId;
    if (oneViewInViewset()) {
      dispatch(clearCurrentView());
    }
    dispatch(purgeViewState(inspectionId, viewId));

    remove(path);
  };

  return (
    <div className={viewSetClass}>
      <ConfirmDialog
        open={viewToClose !== undefined}
        onClose={(confirmed) => handleConfirmDialogClose(confirmed)}
        message={<FormattedMessage id="svoView.closeViewConfirmation" />}
      />
      <Mosaic
        onChange={(layout) => {
          onChange(layout);
          increaseChangeCounter();
        }}
        ref={mosaic}
        renderTile={(id, path) => (
          <MosaicContext.Consumer>
            {({ mosaicActions }) => {
              const { remove } = mosaicActions;
              return (
                <SVOView
                  id={id}
                  path={path}
                  views={views}
                  dispatch={dispatch}
                  editingViewIds={editingViewIds}
                  onCloseView={() => {
                    setViewToClose({ viewId: id, path, remove });
                  }}
                />
              );
            }}
          </MosaicContext.Consumer>
        )}
        initialValue={views.initialValue}
        resize={canEditViewSet(myPermissions) ? '' : 'DISABLED'}
      />
    </div>
  );
};

ViewSet.defaultProps = {
  topNavHidden: false,
  bottomNavHidden: false,
  views: undefined,
};

ViewSet.propTypes = {
  views: mosaicViewType,
  onChange: PropTypes.func.isRequired,
  topNavHidden: PropTypes.bool,
  bottomNavHidden: PropTypes.bool,
  increaseChangeCounter: PropTypes.func.isRequired,
  dispatch: PropTypes.func.isRequired,
};

const mapStateToProps = (state) => ({
  inspections: state.inspections,
  myPermissions: state.app.myPermissions,
});

const mapDispatchToProps = (dispatch) => ({
  increaseChangeCounter: () => dispatch(increaseViewSizeChangeCounter()),
  dispatch: (action) => dispatch(action),
});

export default connect(mapStateToProps, mapDispatchToProps)(ViewSet);
