import { compose } from 'redux';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import React, { Component } from 'react';
import isEqual from 'lodash/isEqual';

import { forceDownload } from '../../../utils/downloader';
import {
  drawCustomOverlays,
  drawInspectionOverlays,
  setBoundingCoordinatesForCustomOverlays,
  setBoundingCoordinatesForInspectionOverlays,
  extractInspectionOverlays,
} from '../../../utils/OverlayRenderer';
import { isObjectValid } from '../../../utils/objectValidator';
import { setViewData, saveImageSettings } from '../../../redux/actions';
import logger from '../../../utils/logger';
import { canEditImageView } from '../../../utils/permissions';

class ImageEditor extends Component {
  state = {
    image: new Image(),
    left: 0,
    top: 0,
    zoomFactor: 1,
    currentCursorPosition: { x: 0, y: 0 },
    canvasDimension: { width: 0, height: 0 },
  };

  componentDidMount() {
    this.loadViewSettings();
    this.props.product.lock();
    this.initImage(this.props);
    this.bindEventListeners();
  }

  componentDidUpdate(prevProps, prevState) {
    const { zoomFactor, left, top } = this.state;

    const productChanged = prevProps.product !== this.props.product;
    const modeChanged = prevProps.currentMode !== this.props.currentMode;
    const zoomFactorChanged = prevState.zoomFactor !== zoomFactor;
    const customOverlaysChanged = !isEqual(this.props.customOverlays, prevProps.customOverlays);
    const imageMoved = prevState.left !== left || prevState.top !== top;

    if (productChanged) {
      prevProps.product.release();
      this.props.product.lock();
    }

    if (productChanged || modeChanged || zoomFactorChanged || customOverlaysChanged) {
      this.initImage(this.props);
    }

    if (zoomFactorChanged || imageMoved) {
      this.props.saveImageSettings(zoomFactor, { left, top });
    }
  }

  componentWillUnmount() {
    this.props.product.release();
  }

  loadViewSettings() {
    const { zoomFactor, imagePosition } = this.props.viewSettings;
    if (zoomFactor) {
      this.setState({ zoomFactor });
    }

    if (imagePosition && imagePosition.left) {
      this.setState({ left: imagePosition.left });
    }

    if (imagePosition && imagePosition.top) {
      this.setState({ top: imagePosition.top });
    }
  }

  initImage(props) {
    if (props && props.product && props.imageId) {
      const image = props.product.getImage(props.imageId);
      if (image && image.domImage) {
        this.onImageLoaded(image.domImage);
      }
    }
  }

  onImageLoaded(image) {
    const { zoomFactor, canvasDimension } = this.state;
    const { height, width } = canvasDimension;
    this.setState({ image });
    this.initCanvas({ canvas: this.canvas, height, width, zoomFactor });
    this.props.setViewData('imageDimensions', { width: image.width, height: image.height });
    this.draw(this.canvas, image, zoomFactor);
    this.setImageInfo();
  }

  initCanvas({ canvas, height, width, zoomFactor }) {
    canvas.width = Math.ceil(width * zoomFactor);
    canvas.height = Math.ceil(height * zoomFactor);

    this.props.setViewData('imageDimensions', { width, height });
  }

  draw(canvas, image, zoomFactor) {
    if (!canvas || !image) {
      console.error('Can not draw because we do not have a canvas');
      return;
    }

    const boundingCoordinates = this.setBoundingCoordinates(image, zoomFactor);

    this.setCanvasDimensions(canvas, boundingCoordinates, zoomFactor);

    const ctx = canvas.getContext('2d');
    ctx.webkitImageSmoothingEnabled = false;
    ctx.msImageSmoothingEnabled = false;
    ctx.imageSmoothingEnabled = false;
    ctx.save();
    ctx.clearRect(
      0,
      0,
      Math.abs(boundingCoordinates.minX) + boundingCoordinates.maxX,
      Math.abs(boundingCoordinates.minY) + boundingCoordinates.maxY,
    );
    ctx.scale(zoomFactor, zoomFactor);
    ctx.drawImage(image, Math.abs(boundingCoordinates.minX), Math.abs(boundingCoordinates.minY));
    this.drawOverlays(canvas, zoomFactor, boundingCoordinates, image.width, image.height);
    ctx.restore();
  }

  setBoundingCoordinates = (image, zoomFactor) => {
    const boundingCoordinates = {
      minX: 0,
      minY: 0,
      maxX: image.width || 0,
      maxY: image.height || 0,
    };
    const fontSize = 10 / zoomFactor;
    this.getBoundingCoordinatesFromCustomOverlays(boundingCoordinates, fontSize);
    this.getBoundingCoordinatesFromInspectionOverlays(boundingCoordinates, fontSize);

    return boundingCoordinates;
  };

  setCanvasDimensions = (canvas, boundingCoordinates, zoomFactor) => {
    const width = Math.abs(boundingCoordinates.minX) + boundingCoordinates.maxX;
    const height = Math.abs(boundingCoordinates.minY) + boundingCoordinates.maxY;

    canvas.width = Math.ceil(width * zoomFactor);
    canvas.height = Math.ceil(height * zoomFactor);

    this.setState({ canvasDimension: { width, height }, boundingCoordinates });
  };

  drawOverlays(drawTarget, zoomFactor, boundingCoordinates, width, height) {
    const { product, customOverlays, imageId, currentMode } = this.props;
    const overlays = extractInspectionOverlays(product, imageId);

    drawCustomOverlays({
      drawTarget,
      customOverlays,
      product,
      zoomFactor,
      boundingCoordinates,
    });
    drawInspectionOverlays({
      drawTarget,
      overlays,
      zoomFactor,
      boundingCoordinates,
      width,
      height,
      currentMode,
    });
  }

  getBoundingCoordinatesFromCustomOverlays = (boundingCoordinates, fontSize) => {
    const { product, customOverlays } = this.props;
    const { image } = this.state;

    const imageWidth = image?.width;
    const imageHeight = image?.height;

    const minWidthFallback = Number(-imageWidth * 2);
    const maxWidthFallback = Number(imageWidth * 2);

    const minHeightFallback = Number(-imageHeight * 2);
    const maxHeightFallback = Number(imageHeight * 2);

    let overlays;
    overlays = customOverlays.filter((overlay) => overlay.isActive);
    overlays.map((overlay) =>
      Object.assign(overlay, {
        minWidthFallback,
        maxWidthFallback,
        minHeightFallback,
        maxHeightFallback,
      }),
    );

    overlays.forEach((overlay) => {
      setBoundingCoordinatesForCustomOverlays({
        overlay,
        product,
        fontSize,
        boundingCoordinates,
        dispatch: this.props.dispatch,
      });
    });
  };

  getBoundingCoordinatesFromInspectionOverlays = (boundingCoordinates, fontSize) => {
    const { product, imageId } = this.props;
    const overlays = extractInspectionOverlays(product, imageId);

    overlays &&
      overlays.forEach((overlay) => {
        setBoundingCoordinatesForInspectionOverlays({ overlay, fontSize, boundingCoordinates });
      });
  };

  setImageInfo = () => {
    const { isImageInfoOpen } = this.props;
    const { currentCursorPosition } = this.state;

    if (currentCursorPosition && isImageInfoOpen) {
      const { x, y } = currentCursorPosition;
      this.setRgbValues(x, y);
    }
  };

  bindEventListeners() {
    if (canEditImageView(this.props.myPermissions)) {
      this.containerRef.addEventListener('mousewheel', this.handleScroll);
      this.containerRef.addEventListener('DOMMouseScroll', this.handleScroll);
      this.containerRef.addEventListener('mousedown', this.handleMouseDown);
      this.containerRef.addEventListener('mouseup', this.handleMouseUp);
      this.containerRef.addEventListener('mouseleave', this.handleMouseUp);
      this.containerRef.addEventListener('mousemove', this.handleDrag);
    }

    this.containerRef.addEventListener('mousemove', this.handleMouseMove);
  }

  handleScroll = (evt) => {
    const delta = evt.wheelDelta ? evt.wheelDelta / 40 : evt.detail ? -evt.detail : 0;
    if (delta) {
      const { offsetX, offsetY } = evt;
      this.zoom(delta, offsetX, offsetY);
    }
    evt.preventDefault();
    return false;
  };

  handleMouseDown = (event) => {
    const dragStart = { x: event.pageX, y: event.pageY };
    const { left, top } = this.state;
    const dragStartPos = { left, top };
    this.setState({ dragging: true, dragStart, dragStartPos });
  };

  handleMouseUp = () => {
    this.setState({ dragging: false, dragStart: null });
  };

  handleDrag = (event) => {
    const { dragging, dragStart, dragStartPos } = this.state;
    if (!dragging) {
      return;
    }

    const diffX = event.pageX - dragStart.x;
    const diffY = event.pageY - dragStart.y;
    this.setState({
      left: dragStartPos.left + diffX,
      top: dragStartPos.top + diffY,
    });
  };

  handleMouseMove = (event) => {
    const { isImageInfoOpen } = this.props;

    if (isImageInfoOpen) {
      const cursorPosition = this.getMouseCoordinatesOnElement(event, this.canvas);

      this.setCursorPosition(cursorPosition);
      this.setRgbValues(cursorPosition.x, cursorPosition.y);
    }
  };

  getMouseCoordinatesOnElement = (event) => {
    const { zoomFactor, boundingCoordinates } = this.state;
    let offsetX = event.offsetX;
    let offsetY = event.offsetY;
    let element = event.target;

    while (element !== this.containerRef) {
      offsetX += element.offsetLeft;
      offsetY += element.offsetTop;
      element = element.parentNode;
    }

    let x = (offsetX - this.canvas.offsetLeft) / zoomFactor;
    let y = (offsetY - this.canvas.offsetTop) / zoomFactor;

    if (boundingCoordinates) {
      x += boundingCoordinates.minX;
      y += boundingCoordinates.minY;
    }

    return {
      x: Math.trunc(x),
      y: Math.trunc(y),
    };
  };

  setRgbValues = (x, y) => {
    const { image } = this.state;
    const canvas = document.createElement('canvas');
    canvas.width = image.width;
    canvas.height = image.height;

    const ctx = canvas.getContext('2d');
    ctx.drawImage(image, 0, 0);

    const colors = ctx.getImageData(x, y, 1, 1).data;
    const red = colors[0];
    const green = colors[1];
    const blue = colors[2];
    this.props.setViewData('rgbValues', { R: red, G: green, B: blue });
  };

  setCursorPosition = (cursorPosition) => {
    const { currentCursorPosition } = this.state;
    const cursorPositionOnImage = this.trimPositionsOnImageSize(cursorPosition);
    this.props.setViewData('cursorPosition', cursorPositionOnImage);

    if (
      currentCursorPosition.x !== cursorPositionOnImage.x ||
      currentCursorPosition.y !== cursorPositionOnImage.y
    ) {
      this.setState({ currentCursorPosition: cursorPosition });
    }
  };

  trimPositionsOnImageSize = (cursorPosition) => {
    const { image } = this.state;
    if (
      cursorPosition.x < 0 ||
      cursorPosition.y < 0 ||
      cursorPosition.x > image.width ||
      cursorPosition.y > image.height
    ) {
      return { x: 0, y: 0 };
    }

    return cursorPosition;
  };

  zoom(delta, offX, offY) {
    const SCALE_BASE = 1.1;
    const MIN_ZOOM_FACTOR = 0.05;
    const MAX_ZOOM_FACTOR = 20;
    const MAX_CANVAS_SIZE = 4096;

    const scaleFactor = Math.pow(SCALE_BASE, delta);
    const zoomFactor = this.state.zoomFactor * scaleFactor;
    if (zoomFactor < MIN_ZOOM_FACTOR || zoomFactor > MAX_ZOOM_FACTOR) {
      return;
    }

    const { canvasDimension } = this.state;
    const longestSide = Math.max(
      canvasDimension.width * zoomFactor,
      canvasDimension.height * zoomFactor,
    );
    if (longestSide > MAX_CANVAS_SIZE) {
      return;
    }

    const { left, top } = this.state;
    const leftDiff = offX * (1 - scaleFactor);
    const topDiff = offY * (1 - scaleFactor);
    this.setState({
      left: left + leftDiff,
      top: top + topDiff,
      zoomFactor,
    });
  }

  clamp(v, min, max) {
    return Math.max(Math.min(v, max), min);
  }

  resetZoom() {
    const { boundingCoordinates, zoomFactor } = this.state;
    this.setState({
      zoomFactor: 1,
      left: boundingCoordinates.minX * zoomFactor,
      top: boundingCoordinates.minY * zoomFactor,
    });
  }

  fitToHeight() {
    const { height } = this.getViewSize();
    const { image, boundingCoordinates, zoomFactor } = this.state;
    this.setState({
      zoomFactor: height / image.height,
      left: boundingCoordinates.minX * zoomFactor,
      top: boundingCoordinates.minY * zoomFactor,
    });
  }

  fitToWidth() {
    const { width } = this.getViewSize();
    const { image, boundingCoordinates, zoomFactor } = this.state;
    this.setState({
      zoomFactor: width / image.width,
      left: boundingCoordinates.minX * zoomFactor,
      top: boundingCoordinates.minY * zoomFactor,
    });
  }

  getViewSize() {
    return {
      width: (this.containerRef && this.containerRef.getBoundingClientRect().width) || 200,
      height: (this.containerRef && this.containerRef.getBoundingClientRect().height) || 200,
    };
  }

  downloadOriginalImage() {
    if (!isObjectValid(() => this.state.image.src)) {
      logger.info('downloadOriginalImage can not find image source');
      return;
    }
    forceDownload(this.state.image.src, 'image.bmp');
  }

  downloadOriginalImageWithOverlays() {
    const { canvasDimension, image } = this.state;
    const canvas = document.createElement('canvas');
    canvas.width = canvasDimension.width;
    canvas.height = canvasDimension.height;
    this.draw(canvas, image, 1);

    canvas.toBlob((blob) => {
      const domURL = self.URL || self.webkitURL || self; // eslint-disable-line
      const url = domURL.createObjectURL(blob);
      forceDownload(url);
      domURL.revokeObjectURL(url);
    });
  }

  render() {
    const { zoomFactor, canvasDimension, left, top } = this.state;
    const height = canvasDimension.height * zoomFactor;
    const width = canvasDimension.width * zoomFactor;

    return (
      <div
        style={{ height: '100%', width: '100%' }}
        ref={(ref) => {
          this.containerRef = ref;
        }}
      >
        <canvas
          className="imageCanvas"
          style={{
            position: 'absolute',
            left,
            top,
            width,
            height,
          }}
          ref={(canvas) => {
            this.canvas = canvas;
          }}
        />
      </div>
    );
  }
}

const propTypes = {
  imageSrc: PropTypes.string,
};

ImageEditor.propTypes = propTypes;

const mapStateToProps = (state, ownProps) => {
  const { inspectionId, id } = ownProps;
  const viewId = id;
  try {
    return {
      customOverlays: state.inspections[inspectionId].sharedState.views[viewId].overlays,
      currentMode: state.app.currentMode,
      viewSettings: state.inspections[inspectionId].views[viewId],
      myPermissions: state.app.myPermissions,
    };
  } catch (error) {
    return {};
  }
};

const mapDispatchToProps = (dispatch, ownProps) => {
  const { inspectionId, id } = ownProps;
  const viewId = id;
  return {
    setViewData: (key, data) => dispatch(setViewData(inspectionId, viewId, key, data)),
    saveImageSettings: (zoomFactor, imagePosition) =>
      dispatch(saveImageSettings(inspectionId, viewId, zoomFactor, imagePosition)),
    dispatch: (...args) => dispatch(...args),
  };
};

export default compose(
  connect(mapStateToProps, mapDispatchToProps, null, { forwardRef: true })(ImageEditor),
);
