import isEqual from 'lodash/isEqual';

import logger from '../../utils/logger';
import ProtobufConnector from '../../protobuf/ProtobufConnector';
import Proxy from '../../protobuf/Proxy';
import { resetState, saveState } from '../../utils/localStorage';

const NUMBER_OF_NOTIFICATIONS_SHOWN = 50;

function createInitialAppState({ websocketUrl, dispatch, user, authToken }) {
  const connector = new ProtobufConnector(websocketUrl, dispatch, authToken);
  const proxy = new Proxy(dispatch);
  const locale = localStorage.getItem('CURRENT_LOCALE');
  logger.setProxy(proxy);
  return {
    connection: null,
    notifications: [],
    mode: 0,
    navigationHidden: false,
    locale: locale || 'en',
    authToken,
    user,
    currentMode: null,
    pausedInspections: [],
    connector,
    protobuf: undefined,
    socket: undefined,
    proxy,
    saveOperationInProcess: false,
    currentClientChunkRevision: 0,
    modalNotificationData: null,
    focusedViewType: null,
    isHelpViewOpen: false,
    configuration: null,
    isBrowserWindowVisible: true,
  };
}

const appReducer = (params) => {
  const initalAppState = createInitialAppState(params);
  return (state = initalAppState, action) => {
    switch (action.type) {
      case 'SET_PROTOBUF_STATIC_INITIALIZED':
        state.proxy.setProtobufStaticInitialized();
        return state;
      case 'SET_PROTOBUF': {
        const { protobuf, socket } = action;
        protobuf.onConnect(socket);
        state.proxy.setProtobuf(protobuf);
        return Object.assign({}, state, { protobuf, socket });
      }
      case 'ADD_NOTIFICATION': {
        const newNotifications = getNewNotifications(action.notification, state);

        return Object.assign({}, state, {
          notifications: newNotifications,
        });
      }
      case 'LOG_IN':
        saveState(action.authToken, action.user);
        state.connector.connectWithToken(action.authToken);
        return { ...state, authToken: action.authToken, user: action.user };
      case 'LOG_OUT':
        resetState();
        setTimeout(() => state.connector.disconnect());
        return state;
      case 'AUTH_TOKEN_EXPIRED':
        resetState();
        return { ...state, authToken: undefined, user: undefined };
      case 'SET_LOCALE':
        localStorage.setItem('CURRENT_LOCALE', action.locale);
        return { ...state, locale: action.locale };
      case 'ON_NAVIGATION_HIDDEN': {
        return Object.assign({}, state, {
          navigationHidden: !state.navigationHidden,
        });
      }
      case 'SHOW_GLOBAL_MESSAGE_OVERLAY':
        const { message } = action;
        logger.info({ message }, 'Showing global message overlay');
        return {
          ...state,
          modalNotificationData: {
            message,
          },
        };
      case 'HIDE_GLOBAL_MESSAGE_OVERLAY':
        logger.info('Hiding global message overlay');
        return {
          ...state,
          modalNotificationData: null,
        };
      case 'SET_CURRENT_MODE':
        state.proxy.setMode(action.mode);
        return state;
      case 'SET_CURRENT_MODE_DONE':
        return {
          ...state,
          currentMode: action.mode,
        };
      case 'SET_PAUSED_INSPECTIONS':
        return {
          ...state,
          pausedInspections: action.pausedInspections,
        };
      case 'UPDATE_CLIENT_CHUNK_REVISION':
        return {
          ...state,
          currentClientChunkRevision: action.revision,
        };

      case 'UPDATE_SAVE_OPERATION_IN_PROCESS':
        return {
          ...state,
          saveOperationInProcess: action.inProcess,
        };
      case 'SET_CONFIGURATION':
        return {
          ...state,
          configuration: action.configuration,
        };

      case 'SET_FOCUSED_VIEW_TYPE':
        return {
          ...state,
          focusedViewType: action.viewType,
        };

      case 'OPEN_HELP_VIEW':
        return {
          ...state,
          isHelpViewOpen: action.isHelpViewOpen,
        };

      case 'CLOSE_HELP_VIEW':
        return {
          ...state,
          isHelpViewOpen: action.isHelpViewOpen,
        };

      case 'SET_GROUP_PERMISSIONS':
        return {
          ...state,
          groupPermissions: action.groupPermissions,
        };

      case 'SET_MY_PERMISSIONS':
        return {
          ...state,
          myPermissions: action.myPermissions,
        };

      case 'SET_BROWSER_WINDOW_VISIBILITY':
        return {
          ...state,
          isBrowserWindowVisible: action.visible,
        };
      default:
        return state;
    }
  };
};

function getNewNotifications(notification2Add, state) {
  let newNotifications = [...state.notifications];
  let notification = addTimestampToNotification(notification2Add, state.locale);

  const equalsLastNotification = equalsLastNotificationCurrentNotification(
    notification,
    newNotifications,
  );

  if (!equalsLastNotification) {
    notification = setCountForNotification(notification);
    newNotifications.unshift(notification);
  } else {
    newNotifications[0] = setCountForNotification(newNotifications[0]);
  }

  if (newNotifications && newNotifications.length > NUMBER_OF_NOTIFICATIONS_SHOWN) {
    newNotifications.splice(-1, 1);
  }

  return newNotifications;
}

function addTimestampToNotification(notification, locale) {
  const date = new Date();
  const dateOptions = { day: '2-digit', month: '2-digit', year: 'numeric' };
  const timeOptions = { hour: '2-digit', minute: '2-digit', second: '2-digit' };
  const timestamp = `${date.toLocaleString(locale, dateOptions)} ${date.toLocaleString(
    locale,
    timeOptions,
  )}`;

  notification = {
    ...notification,
    timestamp: timestamp,
  };

  return notification;
}

function equalsLastNotificationCurrentNotification(currentNotification, notifications) {
  if (notifications && notifications.length > 0) {
    return isEqual(currentNotification.message, notifications[0].message);
  }

  return false;
}

function setCountForNotification(notification) {
  let count = notification.count;
  if (count === undefined) {
    count = 1;
  } else {
    count++;
  }

  notification = {
    ...notification,
    count,
  };

  return notification;
}

export default appReducer;
