import React, { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { Button } from '@material-ui/core';
import { cloneDeep, get, set } from 'lodash';
import { FormattedMessage } from 'react-intl';

import Protobuf from '../../protobuf/Protobuf';

import './style.scss';
import TextParameter from './specialized/TextParameter';
import TextArrayParameter from './specialized/TextArrayParameter';
import BoolParameter from './specialized/BoolParameter';
import BoolArrayParameter from './specialized/BoolArrayParameter';
import IntegerParameter from './specialized/IntegerParameter';
import IntegerArrayParameter from './specialized/IntegerArrayParameter';
import DecimalParameter from './specialized/DecimalParameter';
import DecimalArrayParameter from './specialized/DecimalArrayParameter';
import BytesParameter from './specialized/BytesParameter';
import EnumParameter from './specialized/EnumParameter';
import EnumArrayParameter from './specialized/EnumArrayParameter';
import SelectParameter from './generic/SelectParameter';

const RequestParameter = ({ name, onResponse }) => {
  const protobuf = useSelector((state) => state.app.protobuf);
  const [parameters, setParameters] = useState({});
  const [nestedParamsMap, setNestedParamsMap] = useState({});
  const [currentRequestName, setCurrentRequestName] = useState('');

  useEffect(() => {
    if (currentRequestName === name) return;

    setParameters({});
    setNestedParamsMap({});
    setCurrentRequestName(name);
  }, [name, currentRequestName]);

  if (!Protobuf.SvPb[name]) {
    return <div></div>;
  }

  const sendRequest = async () => {
    onResponse('');
    if (name === 'MyDummyRequest') {
      onResponse(parameters);
      return;
    }
    const request = Protobuf.SvPb[name].create(parameters);
    console.dir(parameters);
    try {
      const response = await protobuf.request(request);
      onResponse(response);
    } catch (e) {
      onResponse(`An error occured: ${e.message}`);
    }
  };

  const deepValue = (obj, path) => {
    if (!path) return obj;
    return get(obj, path);
  };

  const getStringControl = (field, currentPath, currentValue) => {
    return (
      <TextParameter
        key={`txt_${field.name}`}
        typeName={field.type}
        value={currentValue}
        changeCallback={(newValue) =>
          changeValue(currentPath ? `${currentPath}.${field.name}` : field.name, newValue)
        }
      />
    );
  };

  const getStringArrayControl = (field, currentPath, currentValue) => {
    return (
      <TextArrayParameter
        typeName={field.type}
        value={currentValue}
        changeCallback={(newValue) =>
          changeValue(currentPath ? `${currentPath}.${field.name}` : field.name, newValue)
        }
      />
    );
  };

  const getBoolControl = (field, currentPath, currentValue) => {
    return (
      <BoolParameter
        key={`bool_${field.name}`}
        typeName={field.type}
        value={currentValue}
        changeCallback={(newValue) =>
          changeValue(currentPath ? `${currentPath}.${field.name}` : field.name, newValue)
        }
      />
    );
  };

  const getBoolArrayControl = (field, currentPath, currentValue) => {
    return (
      <BoolArrayParameter
        typeName={field.type}
        value={currentValue}
        changeCallback={(newValue) =>
          changeValue(currentPath ? `${currentPath}.${field.name}` : field.name, newValue)
        }
      />
    );
  };

  const getIntegerControl = (field, currentPath, currentValue) => {
    return (
      <IntegerParameter
        key={`integer_${field.name}`}
        typeName={field.type}
        value={currentValue}
        changeCallback={(newValue) =>
          changeValue(currentPath ? `${currentPath}.${field.name}` : field.name, Number(newValue))
        }
      />
    );
  };

  const getIntegerArrayControl = (field, currentPath, currentValue) => {
    return (
      <IntegerArrayParameter
        typeName={field.type}
        value={currentValue}
        changeCallback={(newValue) =>
          changeValue(currentPath ? `${currentPath}.${field.name}` : field.name, Number(newValue))
        }
      />
    );
  };

  const getDecimalControl = (field, currentPath, currentValue) => {
    return (
      <DecimalParameter
        key={`decimal_${field.name}`}
        typeName={field.type}
        value={currentValue}
        changeCallback={(newValue) =>
          changeValue(currentPath ? `${currentPath}.${field.name}` : field.name, Number(newValue))
        }
      />
    );
  };

  const getDecimalArrayControl = (field, currentPath, currentValue) => {
    return (
      <DecimalArrayParameter
        typeName={field.type}
        value={currentValue}
        changeCallback={(newValue) =>
          changeValue(currentPath ? `${currentPath}.${field.name}` : field.name, Number(newValue))
        }
      />
    );
  };

  const getBytesControl = (field, currentPath, currentValue) => {
    return (
      <BytesParameter
        key={`bytes_${field.name}`}
        value={currentValue}
        changeCallback={(newValue) =>
          changeValue(currentPath ? `${currentPath}.${field.name}` : field.name, newValue)
        }
      />
    );
  };

  const getEnumControl = (type, field, currentPath, currentValue) => {
    return (
      <EnumParameter
        value={currentValue}
        options={type.values}
        changeCallback={(value) => {
          changeValue(currentPath ? `${currentPath}.${field.name}` : field.name, value);
        }}
      ></EnumParameter>
    );
  };

  const getArrayEnumControl = (type, field, currentPath, currentValue) => {
    return (
      <EnumArrayParameter
        value={currentValue}
        options={type.values}
        changeCallback={(value) => {
          changeValue(currentPath ? `${currentPath}.${field.name}` : field.name, value);
        }}
      ></EnumArrayParameter>
    );
  };

  const addArrayEntryForType = (currentPath, currentValue) => {
    const newValue = currentValue ? [...currentValue] : [];
    newValue.push({});
    changeValue(currentPath, newValue);
  };

  const getControlForField = (field, isArray, currentPath) => {
    const currentValue = deepValue(
      parameters,
      currentPath ? `${currentPath}.${field.name}` : field.name,
    );

    switch (field.type) {
      case 'string':
        if (isArray) {
          return getStringArrayControl(field, currentPath, currentValue);
        }

        return getStringControl(field, currentPath, currentValue);
      case 'bool':
        if (isArray) {
          return getBoolArrayControl(field, currentPath, currentValue);
        }

        return getBoolControl(field, currentPath, currentValue);
      case 'int32':
      case 'int64':
      case 'uint32':
      case 'uint64':
      case 'sint32':
      case 'sint64':
      case 'fixed32':
      case 'fixed64':
      case 'sfixed32':
      case 'sfixed64':
        if (isArray) {
          return getIntegerArrayControl(field, currentPath, currentValue);
        }

        return getIntegerControl(field, currentPath, currentValue);
      case 'double':
      case 'float':
        if (isArray) {
          return getDecimalArrayControl(field, currentPath, currentValue);
        }

        return getDecimalControl(field, currentPath, currentValue);
      case 'bytes':
        return getBytesControl(field, currentPath, currentValue);
      default:
        const type = Protobuf.SvPb.lookup(field.type);
        if (field.type.endsWith('Enum')) {
          if (isArray) {
            return getArrayEnumControl(type, field, currentPath, currentValue);
          }

          return getEnumControl(type, field, currentPath, currentValue);
        }

        const newPath = currentPath ? `${currentPath}.${field.name}` : field.name;

        if (isArray) {
          return getArrayControlsForType(type, newPath, currentValue);
        }

        return getControlsForType(type, newPath, currentValue);
    }
  };

  const changeValue = (path, value) => {
    const newParams = cloneDeep(parameters);
    set(newParams, path, value);
    setParameters(newParams);
  };

  const getControlsForType = (type, path) => {
    const controls = [];
    const fieldKeys = Object.keys(type.fields);

    if (type.oneofs) {
      Object.keys(type.oneofs).forEach((oneOfKey) => {
        controls.push(
          <div className="field-container" key={`div_${oneOfKey}`}>
            <div className="field-name-type">
              {oneOfKey}
              <small>oneof type</small>
            </div>
            <div className="field-settings">
              <SelectParameter
                value={nestedParamsMap[`${path}.${oneOfKey}`]}
                options={type.oneofs[oneOfKey].oneof}
                changeCallback={(value) => {
                  setNestedParamsMap({
                    ...nestedParamsMap,
                    [`${path}.${oneOfKey}`]: value,
                  });
                }}
              ></SelectParameter>
            </div>
          </div>,
        );
      });
    }

    fieldKeys.forEach((fieldkey) => {
      const field = type.fields[fieldkey];
      if (!field) return;

      if (field.partOf && field.name !== nestedParamsMap[`${path}.${field.partOf.name}`]) {
        return;
      }

      controls.push(
        <div className="field-container" key={`div_${field.name}`}>
          <div className="field-name-type">
            {field.name}
            <small>
              {field.rule} {field.type}
              {field.required ? '*' : ''}
            </small>
          </div>
          <div className="field-settings">
            {getControlForField(field, field.rule === 'repeated', path)}
          </div>
        </div>,
      );
    });

    if (!fieldKeys.length && path.length)
      controls.push(
        <div className="field-container" key={`div_type.name`}>
          <div className="field-name-type">
            <em>
              <FormattedMessage id="debugger.nothingToConfigureMessage"></FormattedMessage>
            </em>
          </div>
          <div className="field-settings"></div>
        </div>,
      );

    return controls;
  };

  const getArrayControlsForType = (type, path, value) => {
    const controls = [];
    (value || []).forEach((_, idx) => {
      controls.push(
        <div className="field-container" key={`${type.name}_array_${idx}`}>
          <div className="field-name-type">
            {idx}
            <small>{type.name}</small>
          </div>
          <div className="field-settings">
            {getControlsForType(type, path ? `${path}.${idx}` : idx, value[idx])}
          </div>
        </div>,
      );
    });

    return (
      <div>
        <div className="field-container is-array">
          {controls.length ? (
            controls
          ) : (
            <div className="field-container">
              <div className="field-name-type">
                <em>
                  <FormattedMessage id="debugger.noItemsMessage" />
                </em>
              </div>
              <div className="field-settings"></div>
            </div>
          )}
        </div>
        <Button
          variant="contained"
          color="primary"
          onClick={() => addArrayEntryForType(type, path, value)}
        >
          Add
        </Button>
      </div>
    );
  };

  const fields = getControlsForType(Protobuf.SvPb[name], '');
  return (
    <div className="request-parameters-container">
      <div className="request-name-type">
        {name}
        <small>Request</small>
      </div>
      <div className="request-parameters-fields-container">
        {fields && fields.length ? fields : <FormattedMessage id="debugger.noParametersMessage" />}
      </div>

      <Button
        className="send-button"
        variant="contained"
        color="primary"
        onClick={() => sendRequest()}
      >
        <FormattedMessage id="debugger.sendRequest" />
      </Button>
    </div>
  );
};

export default RequestParameter;
