import ProtoJs from 'protobufjs';
import { Observable } from './Observable';
import logger from '../utils/logger';

export default class Protobuf {
  // will be initialized with Protobuf.init()
  static SvPenv = {};
  static SvPb = {};
  static nextTxId = 0;
  static nextStreamId = 0;
  static initialized = false;

  static async init() {
    Protobuf.SvPenv = await Protobuf.loadProto('/res/proto/Envelope.proto', 'SvPenv');
    Protobuf.SvPb = await Protobuf.loadProto('/res/proto/SVRC.proto', 'SvPb');
    Protobuf.SvAuth = await Protobuf.loadProto('/res/proto/SVRC.proto', 'SvAuth');
    Protobuf.initialized = true;
  }

  static async loadProto(path, namespace) {
    const root = await ProtoJs.load(path);
    return root.lookup(namespace);
  }

  constructor() {
    this.pendingRequests = new Map();
    this.openStreams = new Map();
    this.socket = null;
  }

  isConnected() {
    return !!this.socket;
  }

  onConnect(socket) {
    const prevSocket = this.socket;
    this.socket = socket;
    if (prevSocket !== socket) {
      this.cancelAllPendingRequests(new Error('Connection changed'));
    }
  }

  async readMessage(data) {
    const env = await Protobuf.SvPenv.Envelope.decode(new Uint8Array(data));

    switch (env.type) {
      case Protobuf.SvPenv.MessageType.response:
        this.onResponse(env);
        break;
      case Protobuf.SvPenv.MessageType.errorResponse:
        this.onErrorResponse(env);
        break;
      case Protobuf.SvPenv.MessageType.streamResponse:
        this.onStreamResponse(env);
        break;
      case Protobuf.SvPenv.MessageType.streamErrorResponse:
        this.onStreamErrorResponse(env);
        break;
      case Protobuf.SvPenv.MessageType.streamFinish:
        this.onStreamFinish(env);
        break;
      case Protobuf.SvPenv.MessageType.streamCancel:
        this.onStreamCancel(env);
        break;
      default:
        logger.error({ type: env.type }, 'Received message with unexpected type');
    }
  }

  onResponse(env) {
    const txId = env.transactionId;
    const hdl = this.pendingRequests.get(txId);
    if (!hdl) {
      logger.error({ txId }, 'Received response for unknown transaction id');
      return;
    }
    const res = this.unwrapResponse(env);
    this.pendingRequests.delete(txId);
    if (hdl.handled) {
      return;
    }

    clearTimeout(hdl.timeout);
    hdl.resolve(res);
  }

  onErrorResponse(env) {
    const txId = env.transactionId;
    const err = this.unwrapErrorResponse(env);
    return this.onErrorResponseImpl(txId, err);
  }

  onErrorResponseImpl(txId, err) {
    const hdl = this.pendingRequests.get(txId);
    if (!hdl) {
      logger.error({ txId }, 'Received response for unknown transaction id');
      return;
    }

    this.pendingRequests.delete(txId);
    if (hdl.handled) {
      return;
    }

    clearTimeout(hdl.timeout);
    hdl.reject(err);
  }

  cancelAllPendingRequests(err) {
    for (let txId of this.pendingRequests.keys()) {
      this.onErrorResponseImpl(txId, err);
    }
  }

  onRequestTimeout(txId) {
    const hdl = this.pendingRequests.get(txId);
    if (!hdl) {
      logger.error({ txId }, 'Received response for unknown transaction id');
      return;
    }

    hdl.handled = true;
    hdl.reject(new Error('Request timed out'));
  }

  async onStreamResponse(env) {
    const streamId = env.transactionId;
    const hdl = this.openStreams.get(streamId);
    if (!hdl) {
      logger.info({ streamId }, 'Received update for unknown stream id');
      return;
    }
    const res = this.unwrapResponse(env);
    await hdl.observer.next(res);
    this.ackStreamResponse(env);
  }

  onStreamErrorResponse(env) {
    const streamId = env.transactionId;
    const hdl = this.openStreams.get(streamId);
    if (!hdl) {
      logger.error({ streamId }, 'Received error for unknown stream id');
      return;
    }
    const err = this.unwrapErrorResponse(env);
    this.openStreams.delete(streamId);
    hdl.observer.error(err);
  }

  onStreamFinish(env) {
    const streamId = env.transactionId;
    const hdl = this.openStreams.get(streamId);
    if (!hdl) {
      logger.error({ streamId }, 'Received finish for unknown stream id');
      return;
    }

    this.openStreams.delete(streamId);
    hdl.observer.complete();
  }

  onStreamCancel(env) {
    // ignore cancel envelopes. they should
    // be sent from client to server only.
  }

  request(req) {
    return new Promise((resolve, reject) => {
      if (!this.socket) {
        return reject(new Error('No socket available'));
      }
      Protobuf.nextTxId += 1;
      const txId = Protobuf.nextTxId;
      const env = this.wrapMessage(req, txId, Protobuf.SvPenv.MessageType.request);
      const buf = this.serializeEnvelope(env);
      const timeout = setTimeout(this.onRequestTimeout.bind(this, txId), 30 * 1000);
      this.pendingRequests.set(txId, { resolve, reject, timeout });
      this.socket.send(buf);
    });
  }

  stream(req) {
    return Observable.create((observer) => {
      if (!this.socket) {
        return observer.error(new Error('No socket available'));
      }
      Protobuf.nextStreamId += 1;
      const streamId = Protobuf.nextStreamId;
      const env = this.wrapMessage(req, streamId, Protobuf.SvPenv.MessageType.streamRequest);
      const buf = this.serializeEnvelope(env);
      this.openStreams.set(streamId, { observer });
      this.socket.send(buf);

      return () => {
        this.openStreams.delete(streamId);
        this.cancelStream(streamId);
      };
    });
  }

  cancelStream(transactionId) {
    if (!this.socket) {
      return;
    }
    const type = Protobuf.SvPenv.MessageType.streamCancel;
    const params = { transactionId, type };
    const env = Protobuf.SvPenv.Envelope.fromObject(params);
    const buf = this.serializeEnvelope(env);
    this.socket.send(buf);
  }

  ackStreamResponse(env) {
    const ackEnv = Protobuf.SvPenv.Envelope.fromObject({
      transactionId: env.transactionId,
      sequenceNumber: env.sequenceNumber,
      type: Protobuf.SvPenv.MessageType.streamAck,
    });
    const buf = this.serializeEnvelope(ackEnv);
    this.socket.send(buf);
  }

  wrapMessage(req, transactionId, type = Protobuf.SvPenv.MessageType.request) {
    const message = this.wrapOneOf(req);
    const envelope = Protobuf.SvPenv.Envelope.fromObject({
      payloadType: this.getPayloadTypeForOneOfMessage(message),
      payload: this.packToAny(message),
      transactionId,
      type,
    });
    return envelope;
  }

  // NOTE: ignoring payload_type as we want to unpack anyways
  //       so no need to dispatch the message before unpacking
  unwrapResponse(env) {
    const message = this.unpackFromAny(env.payload);
    const res = this.unwrapOneOf(message);
    return res;
  }

  unwrapErrorResponse(env) {
    const error = new Error(env.error.message);
    error.code = env.error.errorCode;

    if (env.error.payload) {
      const payload = this.unpackFromAny(env.error.payload);
      error.payload = payload;
    }
    return error;
  }

  wrapOneOf(req) {
    const fieldName = this.getFieldNameForSVRCMessage(req);
    return Protobuf.SvPb.SVRCMessages.create({ [fieldName]: req });
  }

  unwrapOneOf(message) {
    return message[message.message];
  }

  // by convention, the field name of the SVRCMessages oneof is
  // equal to the type name, except that first letter is lower-case.
  getFieldNameForSVRCMessage(message) {
    const name = message.$type.name;
    return name.substr(0, 1).toLowerCase() + name.substr(1);
  }

  getPayloadTypeForOneOfMessage(message) {
    const fieldName = message.message;
    const type = message.$type.fields[fieldName];
    return type.id;
  }

  packToAny(message) {
    const type = message.$type;
    const typeName = type.fullName.slice(1); // remove leading dot
    // the c++ protobuf library uses this as the default prefix
    const typeUrlPrefix = 'type.googleapis.com';
    const encoded = type.encode(message).finish();
    return {
      type_url: `${typeUrlPrefix}/${typeName}`,
      value: encoded,
    };
  }

  unpackFromAny(any) {
    let typeName = any.type_url;
    const slashIdx = typeName.indexOf('/');
    if (slashIdx !== -1) {
      typeName = typeName.slice(slashIdx + 1);
    }
    const type = Protobuf.SvPb.lookup(typeName);
    if (!type) {
      logger.error({ type: any.type_url }, 'Received message with unknown payload type');
      return;
    }
    return type.decode(any.value);
  }

  serializeEnvelope(envelope) {
    const buf = Protobuf.SvPenv.Envelope.encode(envelope).finish();
    const raw = buf.buffer.slice(buf.byteOffset, buf.byteOffset + buf.length);
    return raw;
  }
}
