import React from 'react';
import ArrayParameter from '../../generic/ArrayParameter';
import SelectParameter from '../../generic/SelectParameter';

const EnumArrayParameter = ({ value, options, changeCallback }) => {
  return (
    <ArrayParameter
      value={(value || []).map((i) => String(i))}
      changeCallback={changeCallback}
      options={Object.values(options)}
      labels={Object.keys(options)}
      SingleParameterComponent={SelectParameter}
    />
  );
};

export default EnumArrayParameter;
