import ViewSetFactory, { DEFAULT_VIEWSET_NAME } from '../../views/ViewSetFactory';

const viewSetFactory = new ViewSetFactory();

const initialState = () => {
  const hideBottomNavigation =
    localStorage.getItem('BOTTOM_NAVIGATION_HIDDEN') === 'true';
  const hideTopNavigation =
    localStorage.getItem('TOP_NAVIGATION_HIDDEN') === 'true';
  const defaultViewSetName = localStorage.getItem('DEFAULT_VIEWSET_NAME');

  return {
  currentViewSet: undefined,
  createdViewSets: {},
  fullscreenMode: false,
  fullscreenComponentDescriptor: undefined,
  viewSizeChangeCounter: 0,
    bottomNavHidden: hideBottomNavigation || false,
    topNavHidden: hideTopNavigation || false,
    defaultViewSetName: defaultViewSetName || null,
  isSVOConfigLoaded: false,
  unsavedChanges: false,
  };
};

const enterFullScreenMode = (componentDescriptor, state) => {
  componentDescriptor.setFullscreen(true);
  return {
    ...state,
    fullscreenMode: true,
    fullscreenComponentDescriptor: componentDescriptor,
  };
};

const leaveFullScreenMode = (componentDescriptor, state) => {
  componentDescriptor.setFullscreen(false);
  return {
    ...state,
    fullscreenMode: false,
    fullscreenComponentDescriptor: undefined,
  };
};

const findCreatedViewSetWithName = (name, createdViewSets) => {
  const viewSetId = Object.keys(createdViewSets).find(
    (viewSetId) => createdViewSets[viewSetId] && createdViewSets[viewSetId].name === name,
  );
  return viewSetId && createdViewSets[viewSetId];
};

const switchViewSet = (viewSet, state) => {
  const viewSetId = viewSet.id;
  const cached = state.createdViewSets[viewSetId];
  const targetViewSet = cached || viewSetFactory.getViewSet(viewSetId);
  const createdViewSet = !cached ? { [viewSetId]: targetViewSet } : {};

  const newState = {
    ...state,
    currentViewSet: targetViewSet,
    createdViewSets: Object.assign({}, state.createdViewSets, createdViewSet),
  };
  return newState;
};

const updateCurrentViewSet = (state, viewSet, viewSetId) => {
  return {
    ...state,
    currentViewSet: viewSet,
    createdViewSets: {
      ...state.createdViewSets,
      [viewSetId]: viewSet,
    },
    unsavedChanges: true,
  };
};

const removeViewSet = (state, name) => {
  const { createdViewSets } = state;

  let newCreatedViewSets = Object.keys(createdViewSets)
    .filter((id) => id !== name)
    .reduce((p, c) => ({ ...p, [c]: createdViewSets[c] }), {});

  if (Object.keys(newCreatedViewSets).length === 0) {
    const name = DEFAULT_VIEWSET_NAME;
    newCreatedViewSets = {
      [name]: viewSetFactory.buildEmptyViewSet(name),
    };
  }

  return {
    createdViewSets: newCreatedViewSets,
    currentViewSet: newCreatedViewSets[Object.keys(newCreatedViewSets)[0]],
    unsavedChanges: true,
  };
};

const viewSetReducer = (state = initialState(), action) => {
  switch (action.type) {
    case 'LOG_OUT':
      return initialState();
    case 'SET_VIEWSET':
      return updateCurrentViewSet(state, action.viewSet, action.viewSetId);
    case 'REPLACE_VIEW_IN_CURRENT_VIEWSET': {
      const { viewId, inspectionId, replacementComponentId } = action;
      const createdView = viewSetFactory.getViewById(replacementComponentId);
      createdView.inspectionId = inspectionId;
      const currentViewSetId = state.currentViewSet.id;
      const updatedViewSet = {
        ...state.currentViewSet,
        viewSet: {
          ...state.currentViewSet.viewSet,
          [viewId]: createdView,
        },
      };
      return updateCurrentViewSet(state, updatedViewSet, currentViewSetId);
    }
    case 'SWITCH_VIEWSET':
      return switchViewSet(action.viewSet, state);
    case 'ENTER_FULLSCREEN_MODE': {
      if (
        state.fullscreenComponentDescriptor &&
        state.fullscreenComponentDescriptor.id !== action.componentDescriptor.id
      ) {
        leaveFullScreenMode(state.fullscreenComponentDescriptor, state);
      }
      return enterFullScreenMode(action.componentDescriptor, state);
    }
    case 'LEAVE_FULLSCREEN_MODE': {
      return leaveFullScreenMode(action.componentDescriptor, state);
    }
    case 'TOGGLE_FULLSCREEN_MODE': {
      if (!state.fullscreenComponentDescriptor) {
        return enterFullScreenMode(action.componentDescriptor, state);
      }
      if (state.fullscreenComponentDescriptor.id === action.componentDescriptor.id) {
        if (state.fullscreenMode) {
          return leaveFullScreenMode(action.componentDescriptor, state);
        }
        return enterFullScreenMode(action.componentDescriptor, state);
      }
      return state;
    }
    case 'INCREASE_VIEW_SIZE_CHANGE_COUNTER':
      return {
        ...state,
        viewSizeChangeCounter: state.viewSizeChangeCounter + 1,
      };
    case 'SET_BOTTOM_NAV_HIDDEN':
      localStorage.setItem('BOTTOM_NAVIGATION_HIDDEN', action.hidden);
      return {
        ...state,
        bottomNavHidden: action.hidden,
      };
    case 'CHANGE_VIEWSET_AFTER_LOADING':
      return {
        ...state,
        changeViewSetToName: action.name,
      };
    case 'SET_TOP_NAV_HIDDEN':
      localStorage.setItem('TOP_NAVIGATION_HIDDEN', action.hidden);
      return {
        ...state,
        topNavHidden: action.hidden,
      };
    case 'SET_DEFAULT_VIEWSET_NAME':
      localStorage.setItem('DEFAULT_VIEWSET_NAME', action.name);
      return {
        ...state,
        defaultViewSetName: action.name,
      };
    case 'NEW_VIEW_SET': {
      const newViewSet = {
        [action.name]: viewSetFactory.buildEmptyViewSet(action.name),
      };

      return {
        ...state,
        createdViewSets: {
          ...state.createdViewSets,
          ...newViewSet,
        },
        unsavedChanges: true,
      };
    }
    case 'REMOVE_VIEWSET': {
      const { createdViewSets, currentViewSet } = removeViewSet(state, action.name);
      return {
        ...state,
        currentViewSet,
        createdViewSets,
        unsavedChanges: true,
      };
    }
    case 'CLEAR_CURRENT_VIEW': {
      if (!state?.currentViewSet?.name) {
        return state;
      }

      const emptyViewSet = viewSetFactory.buildEmptyViewSet(
        state.currentViewSet.name,
      );
      state.createdViewSets[state.currentViewSet.name] = emptyViewSet;

      return {
        ...state,
        currentViewSet: emptyViewSet,
        unsavedChanges: true,
      };
    }
    case 'RENAME_VIEWSET': {
      const viewsetToEdit = state.createdViewSets[action.oldName];
      const renamedViewSet = {
        ...viewsetToEdit,
        id: action.newName,
        name: action.newName,
      };
      const createdViewSets = Object.keys(state.createdViewSets).reduce(
        (all, id) =>
          id === action.oldName
            ? { ...all, [action.newName]: renamedViewSet }
            : { ...all, [id]: state.createdViewSets[id] },
        {},
      );
      return {
        ...state,
        createdViewSets,
        unsavedChanges: true,
      };
    }
    case 'SET_CURRENT_LAYOUT':
      if (!action.layout) {
        return state;
      }
      return {
        ...state,
        createdViewSets: Object.assign({}, state.createdViewSets, {
          [state.currentViewSet.id]: {
            ...state.currentViewSet,
            initialValue: action.layout,
          },
        }),
        currentViewSet: {
          ...state.currentViewSet,
          initialValue: action.layout,
        },
        currentLayout: action.layout,
        unsavedChanges: true,
      };
    case 'LOAD_CLIENT_CHUNK':
      if (state.changeViewSetToName) {
        let loadedViewSet = findCreatedViewSetWithName(
          state.changeViewSetToName,
          action.chunk.createdViewSets,
        );

        if (loadedViewSet) {
          const newState = {
            ...state,
            currentViewSet: loadedViewSet,
            createdViewSets: action.chunk.createdViewSets,
            changeViewSetToName: undefined,
          };
          return switchViewSet(loadedViewSet, newState);
        }
      }

      const config = {
        createdViewSets: action.chunk.createdViewSets,
        currentViewSet: action.chunk.currentViewSet,
      };
      return {
        ...state,
        ...config,
      };
    case 'SET_SVO_CONFIG_LOADED':
      return {
        ...state,
        isSVOConfigLoaded: action.isLoaded,
        unsavedChanges: false,
      };
    case 'SET_SAVE_FLAG':
      return {
        ...state,
        unsavedChanges: action.hasChanges,
      };
    default:
      return state;
  }
};

export default viewSetReducer;
