import React from 'react';

import ArrayParameter from '../../generic/ArrayParameter';
import DecimalParameter from '../DecimalParameter';

const DecimalArrayParameter = ({ value, changeCallback, typeName }) => {
  return (
    <ArrayParameter
      value={value}
      changeCallback={changeCallback}
      typeName={typeName}
      defaultValue={0}
      SingleParameterComponent={DecimalParameter}
    />
  );
};

export default DecimalArrayParameter;
