import PropTypes from 'prop-types';
import React, { Component } from 'react';
import Table, { AutoResizer } from 'react-base-table';
import Checkbox from '@material-ui/core/Checkbox';

import { updateOverlayList } from '../../utils/reduxHelper';

import 'react-base-table/styles.css';
import './style.scss';

class OverlayList extends Component {
  state = {
    selectAll: false,
    selectedRowIndex: -1,
  };

  componentDidMount() {
    this.checkAndUpdateAllSelected(this.props.overlays);

    if (this.props.canSelectRow) {
      this.setState({ selectedRowIndex: 0 });
    }
  }

  componentDidUpdate(prevProps) {
    if (prevProps !== this.props) {
      this.checkAndUpdateAllSelected(this.props.overlays);
    }
  }

  shouldComponentUpdate() {
    return true;
  }

  checkAndUpdateAllSelected = () => {
    const { overlays } = this.props;
    let selectAll = true;
    (overlays || []).forEach((overlay) => {
      if (!overlay.isActive) {
        selectAll = false;
      }
    });

    this.setState({ selectAll });
  };

  handleSelectAll = (checked) => {
    this.setState({ selectAll: checked });
    this.props.onSelectAll(checked);
  };

  handleSingleSelect = (checked, overlay) => {
    const { overlays } = this.props;
    const updatedOverlay = { ...overlay, isActive: checked };
    const updatedOverlayList = updateOverlayList(updatedOverlay, overlays);

    this.checkAndUpdateAllSelected(updatedOverlayList);
    this.props.updateOverlayList(updatedOverlayList);
  };

  getTableDataFromOverlays = () => {
    const { overlays } = this.props;
    const data =
      overlays &&
      overlays.map((overlay) => {
        return {
          checkbox: this.getCheckboxDefinition4Cell(overlay.id),
          name: overlay.label,
          id: overlay.id,
        };
      });
    return data;
  };

  getColumnDefinition4OverlayTable = () => {
    return [
      {
        key: 'checkbox',
        dataKey: 'checkbox',
        title: '',
        width: 40,
      },
      {
        key: 'name',
        dataKey: 'name',
        title: 'Overlays',
        width: 0,
        flexGrow: 1,
      },
    ];
  };

  getCheckboxDefinition4Cell = (currentId) => {
    const { overlays } = this.props;
    const overlay = overlays.filter((item) => item.id === currentId)[0];
    const isActive = overlay ? overlay.isActive : false;
    return (
      <Checkbox
        checked={isActive}
        onChange={(e) => this.handleSingleSelect(e.target.checked, overlay)}
        style={{
          padding: 0,
        }}
        color="primary"
        key={`checkbox_${currentId}`}
      />
    );
  };

  getSelectedRowClass = (currendId) => {
    if (this.props.currentOverlay && this.props.currentOverlay.id === currendId) {
      return 'overlay-table-selected-row';
    }

    return '';
  };

  headerRenderer = ({ cells, columns }) => {
    const columnHeaders = [];
    columns.forEach((column, columnIndex) => {
      if (columnIndex === 0) {
        columnHeaders.push(
          <div
            className="base-table-header-column"
            style={{ width: columns[columnIndex].width }}
            key={`header_${columnIndex}`}
          >
            <Checkbox
              checked={this.state.selectAll}
              onChange={(e) => this.handleSelectAll(e.target.checked)}
              style={{
                padding: 0,
              }}
              color="primary"
              key="select-all-checkbox"
            />
          </div>,
        );
      } else {
        columnHeaders.push(
          <div
            className="base-table-header-column"
            key={`header_${columnIndex}`}
            style={{ padding: ' 0 7.5px' }}
          >
            {columns[columnIndex].title}
          </div>,
        );
      }
    });

    return columnHeaders;
  };

  rowEventHandlers = {
    onClick: ({ rowData, rowIndex }) => {
      const { overlays, updateCurrentOverlay } = this.props;
      this.setState({ selectedRowIndex: rowIndex });
      updateCurrentOverlay(overlays.filter((item) => item.id === overlays[rowIndex].id)[0]);
    },
  };

  setRowClassName = ({ rowData, rowIndex }) => {
    return this.state.selectedRowIndex === rowIndex ? 'overlay-table-selected-row' : '';
  };

  render() {
    const { overlays, canSelectRow, hasActionBar } = this.props;

    if (!overlays) return null;
    const data = this.getTableDataFromOverlays();
    const columns = this.getColumnDefinition4OverlayTable();

    return (
      <AutoResizer key={`autoresizer`}>
        {({ width, height }) => (
          <Table
            headerRenderer={this.headerRenderer}
            columns={columns}
            data={data}
            width={width}
            height={hasActionBar ? height - 30 : height}
            headerHeight={30}
            rowHeight={30}
            key="overlay-table"
            rowEventHandlers={canSelectRow ? this.rowEventHandlers : {}}
            rowClassName={this.setRowClassName}
          />
        )}
      </AutoResizer>
    );
  }
}

OverlayList.defaultProps = {
  overlays: [],
  hasActionBar: true,
};

OverlayList.propTypes = {
  overlays: PropTypes.array,
  onSelectAll: PropTypes.func.isRequired,
  hasActionBar: PropTypes.bool,
};

export default OverlayList;
