import Protobuf from '../protobuf/Protobuf';

export const flattenProtobuf = (oldObject) => {
  const permissions = new Set();
  flattenProtobufRec(oldObject, permissions, '');
  return permissions;
};

const flattenProtobufRec = (currentObject, permissions, parentPath) => {
  for (const key in currentObject.nested) {
    const value = currentObject.nested[key];
    const currKey = key.toLowerCase();
    const currPath = parentPath ? `${parentPath}.${currKey}` : currKey;
    if (!value.nested && value.fields) {
      if (currPath === 'webapp.view') {
        const viewTypeProtobuf = Protobuf.SvPb.lookup('ViewType');
        for (let i = 0; i < Object.keys(viewTypeProtobuf.values).length; i++) {
          const viewPath = `${currPath}[type=${getViewName(i)}]`;
          addFieldValues(permissions, viewPath, value.fields);
        }
      } else {
        addFieldValues(permissions, currPath, value.fields);
      }
    } else {
      flattenProtobufRec(value, permissions, currPath);
    }
  }
};

const addFieldValues = (permissions, path, fields) => {
  Object.keys(fields).forEach((key) => {
    const field = fields[key];
    if (field.type === 'bool') {
      const name = field.name.toLowerCase();
      permissions.add(`${path}.${name}`);
    }
  });
};

export const flattenPermissionsRec = (currentObject, permissions, parentPath) => {
  for (const key in currentObject) {
    const value = currentObject[key];
    if (key.indexOf('$') !== -1) {
      continue;
    }
    const currPath = parentPath ? `${parentPath}.${key}` : key;
    if (Array.isArray(value)) {
      if (currPath === 'webapp.view') {
        for (let i = 0; i < value.length; ++i) {
          const entry = value[i];
          const viewPath = `${currPath}[type=${getViewName(entry.type)}]`;
          flattenPermissionsRec(entry, permissions, viewPath);
        }
      }
    } else if (typeof value === 'object') {
      flattenPermissionsRec(value, permissions, currPath);
    } else if (typeof value === 'boolean' && value) {
      permissions.add(currPath);
    } else {
      //boolean = false
      //another type, p.e. number
    }
  }
};

const createObjectFromFlattenedPermissions = (a, obj, v) => {
  var k = a.shift();

  if (a.length > 0) {
    if (k.startsWith('view[')) {
      const type = getViewId(k.substring(k.indexOf('[type=') + 6, k.indexOf(']')));

      if (!obj.view) {
        obj.view = [];
      }
      const index = obj.view.findIndex((view) => view.type === type);
      if (index !== -1) {
        obj.view[index] = { ...obj.view[index], [a[0]]: true };
      } else {
        obj.view.push({ type, [a[0]]: true });
      }
    } else {
      if (Object(obj[k]) !== obj[k]) {
        obj[k] = {};
      }

      createObjectFromFlattenedPermissions(a, obj[k], v);
    }
  } else {
    obj[k] = true;
  }
};

export const getGroupPermissionsFromFlattenedPermissions = (flattenedPermissions) => {
  const result = [];
  flattenedPermissions.forEach((groupEntry, name) => {
    const permissionForUser = {};
    groupEntry.groupPermissions.forEach((permission) => {
      createObjectFromFlattenedPermissions(
        permission.split('.'),
        permissionForUser,
        permissionForUser[permission],
      );
    });
    result.push({
      name,
      permissions: permissionForUser,
    });
  });

  return result;
};

export const getViewName = (viewType) => {
  switch (viewType) {
    case 0:
      return 'AnyView';
    case 1:
      return 'ImageView';
    case 2:
      return 'ListView';
    case 3:
      return 'ImageStripView';
    case 4:
      return 'ParameterView';
    case 5:
      return 'ToolSetView';
    default:
      return '';
  }
};

export const getViewId = (viewType) => {
  switch (viewType) {
    case 'AnyView':
      return 0;
    case 'ImageView':
      return 1;
    case 'ListView':
      return 2;
    case 'ImageStripView':
      return 3;
    case 'ParameterView':
      return 4;
    case 'ToolSetView':
      return 5;
    default:
      return 0;
  }
};

