import React from 'react';
import { TextField } from '@material-ui/core';

const BytesParameter = ({ value, changeCallback, removeCallback }) => {
  return (
    <TextField
      fullWidth
      type="file"
      variant="outlined"
      value={value || ''}
      onChange={(e) => changeCallback && changeCallback(e.target.value)}
    />
  );
};

export default BytesParameter;
