import React from 'react';
import { FormattedMessage } from 'react-intl';
import LZString from 'lz-string';

import logger from '../utils/logger';
import modes from '../utils/modes';
import {
  flattenPermissionsRec,
  canReadUserManagement,
  canEditUserManagement,
} from '../utils/permissions';
import { defaultPermissions } from '../utils/defaultPermissions';
import {
  CONFIG_INFO,
  CONFIGURATION_CHUNK_TEMPLATE,
  CONFIGURATION_CHUNK_LIST,
} from '../utils/localStorage';
import { chunkSubstr } from '../utils/stringChunker';
import { replacerMapToJson } from '../utils/jsonMapper';
import Configuration from './Configuration';
import ProductHistoryManager from './ProductHistoryManager';
import Protobuf from './Protobuf';
import SubscriptionManager from './SubscriptionManager';
import {
  mapLogLevelNameToProtobufLogSeverity,
  mapProtobufDeviceModeToWebAppMode,
  mapWebAppModeToProtobufDeviceMode,
} from './Mapper';
import {
  addModeSwitchNotification,
  addProjectLoadedNotification,
  addProjectUnloadedNotification,
  addProjectModifiedNotification,
  addServerMessageNotification,
  addRejectStreamPausedNotification,
  loadClientChunk,
  resetClientChunk,
  setCurrentModeDone,
  setCurrentConfiguration,
  setPausedInspections,
  showGlobalMessageOverlay,
  hideGlobalMessageOverlay,
  setSVOConfigLoaded,
  setGroupPermissions,
  setMyPermissions,
  clearCurrentView,
} from '../redux/actions';

export default class Proxy {
  constructor(dispatch) {
    this.dispatch = dispatch;
    this.currentConfiguration = null;
    this.loading = false;
    this.mode = null;
    this.protobuf = null;
    this.protobufInitialized = false;
    this.observerNotificationSubscription = null;
    this.observerMessageSubscription = null;
    this.gatewayNotificationSubscription = null;
    this.gatewayMessageSubscription = null;
    this.nextSubId = 0;
    this.productHistoryManager = new ProductHistoryManager();
    this.subscriptionManager = new SubscriptionManager({
      productHistoryManager: this.productHistoryManager,
    });
    this.showConnectingSplashScreen();
  }

  setMode(mode) {
    this.onCurrentModeChanged(mode);
  }

  setProtobufStaticInitialized() {
    this.protobufInitialized = true;
    this.onProtobufChangedImpl();
  }

  setProtobuf(protobuf) {
    this.protobuf = protobuf;
    this.subscriptionManager.setProtobuf(protobuf);
    this.onProtobufChangedImpl();
  }

  onProtobufChangedImpl() {
    if (this.protobuf && this.protobuf.isConnected() && this.protobufInitialized) {
      this.hideConnectingSplashScreen();
      setImmediate(() => this.loadProject());
    } else {
      this.showConnectingSplashScreen();
    }
  }

  sendLogsToServer = async (entries) => {
    const logs = entries.map((e) => this.mapLogEntry(e));
    const req = Protobuf.SvPb.StoreClientLogsRequest.create({
      client: 'WebApp',
      logs,
    });
    try {
      if (this.protobuf) {
        await this.protobuf.request(req);
      }
    } catch (e) {
      console.error('Error while sending logs to server:', e);
    }
  };

  mapLogEntry(entry) {
    return {
      severity: mapLogLevelNameToProtobufLogSeverity(entry.levelName),
      message: JSON.stringify(entry),
    };
  }

  loadProject = async () => {
    if (this.loading) {
      this.loadProjectRequested = true;
      return;
    }

    this.loading = true;
    this.showLoadingSplashScreen();
    try {
      await this.getMyPermissions();
      await this.discoverConfiguration();
      await this.discoverDeviceMode();
      this.subscribeToNotificationAndMessageStreams();
      // only load the client chunk when we have a valid configuration
      if (this.currentConfiguration) {
        this.dispatch(setSVOConfigLoaded(true));
        await this.loadClientChunk();
      } else {
        await this.resetClientChunk();
        this.dispatch(setSVOConfigLoaded(false));
      }
      // TODO: re-subscribe all current subscriptions?
      // this.scheduleRebuildSubscriptions();
      this.hideLoadingSplashScreen();
    } catch (err) {
      this.showLoadingErrorOverlay(err);
    }
    this.loading = false;

    if (this.loadProjectRequested) {
      this.loadProjectRequested = false;
      this.loadProject();
    }
  };

  showConnectingSplashScreen() {
    // can not call dispatch right away, because we reach this point
    // while the store itself is still in initialization phase.
    setImmediate(() => {
      this.dispatch(
        showGlobalMessageOverlay({
          message: <p>Connecting</p>,
        }),
      );
    });
  }

  hideConnectingSplashScreen() {
    // This function might be triggered by a redux event itself.
    // Use setImmediate to not trigger a redux event inside a
    // redux event (which is forbidden).
    setImmediate(() => {
      this.dispatch(hideGlobalMessageOverlay());
    });
  }

  showLoadingSplashScreen() {
    this.dispatch(
      showGlobalMessageOverlay({
        message: <p>Loading Configuration</p>,
      }),
    );
  }

  showLoadingErrorOverlay(err) {
    this.dispatch(
      showGlobalMessageOverlay({
        message: <p>Error while loading Configuration: {`${err}`}</p>,
      }),
    );
  }

  hideLoadingSplashScreen() {
    this.dispatch(hideGlobalMessageOverlay());
  }

  async getConfigurationInfo() {
    try {
      let res = await this.protobuf.request(Protobuf.SvPb.GetConfigurationInfoRequest.create());

      if (Object.keys(res).length === 0) {
        logger.warn(
          'Empty response for GetConfigurationInfoRequest, configuration in SVO not loaded',
        );
        return null;
      }

      if (!res.lastModified) {
        res = {
          ...res,
          lastModified: res.loadedSince,
        };
      }

      return res;
    } catch (err) {
      logger.error('GetConfigurationInfoRequest ERROR', err);
    }

    return null;
  }

  async discoverConfiguration() {
    let cachedConfigInfo = null;
    const configInfo = await this.getConfigurationInfo();

    if (!configInfo || !configInfo.loadedSince) {
      this.currentConfiguration = null;
      return;
    }

    try {
      cachedConfigInfo = await JSON.parse(localStorage.getItem(CONFIG_INFO));
    } catch (err) {
      logger.error({ err }, 'Timestamp request failed');
    }

    // Timestamps should be equal but not null
    if (
      configInfo &&
      cachedConfigInfo &&
      cachedConfigInfo.hash === configInfo.hash &&
      Number(cachedConfigInfo.lastModified) === configInfo.lastModified
    ) {
      const cachedConfig = await Configuration.loadFromCache(this.protobuf);
      this.currentConfiguration = cachedConfig;
      this.dispatch(setCurrentConfiguration(cachedConfig));
      this.setUpProductHistories(cachedConfig);
    } else {
      const currConfig = await Configuration.discover(this.protobuf);
      if (currConfig) {
        this.currentConfiguration = currConfig;

        const stringifiedConfig = LZString.compress(JSON.stringify(currConfig, replacerMapToJson));
        const configChunks = chunkSubstr(stringifiedConfig, 1000000); // 1m chars = 1MB chunks
        let configChunkNames = [];

        for (let i = 0; i < configChunks.length; i++) {
          const chunk = configChunks[i];
          const chunkName = `${CONFIGURATION_CHUNK_TEMPLATE}${i}`;
          localStorage.setItem(chunkName, chunk);
          configChunkNames.push(chunkName);
        }

        localStorage.setItem(CONFIGURATION_CHUNK_LIST, configChunkNames.join(','));
        localStorage.setItem(CONFIG_INFO, JSON.stringify(configInfo));

        this.setUpProductHistories(currConfig);
        this.dispatch(setCurrentConfiguration(currConfig));

        this.dispatch(clearCurrentView());
      } else {
        this.currentConfiguration = null;
      }
    }
  }

  async onCurrentModeChanged(currentMode) {
    // set internal mode to unknown while switching the mode to
    // lower the possibility of running into errors in the
    // subscription code, e.g. e.g. fetching images which are not
    // accessible in certain modes
    this.mode = modes.unknownMode.id;

    const mode = mapWebAppModeToProtobufDeviceMode(currentMode);
    const req = Protobuf.SvPb.SetDeviceModeRequest.create({ mode });
    let res = null;
    try {
      res = await this.protobuf.request(req);
    } catch (err) {
      logger.error({ err }, 'Error while changing the mode');
      return;
    }

    if (res.hResult) {
      logger.error({ hResult: res.hResult }, 'Error while changing the mode');
      return;
    }

    // Leaving this here as a safety net in case SVObserver is not
    // informing us about the new mode via a notification. As we
    // do nothing when the discovered mode is the one we already,
    // have in this.mode, it is cheap to do it multiple times.
    setTimeout(this.discoverDeviceMode, 500);
  }

  discoverDeviceMode = async () => {
    let mode = null;
    let modeName = null;
    try {
      const req = Protobuf.SvPb.GetDeviceModeRequest.create({});
      const res = await this.protobuf.request(req);
      modeName = res.mode;
      mode = mapProtobufDeviceModeToWebAppMode(modeName);
    } catch (err) {
      logger.error({ err }, 'Error while fetching mode');
    }

    if (mode === null || this.mode === mode) {
      return;
    }

    // Initially we get mode from SVObserver and no change happens
    if (this.mode === null) {
      this.mode = mode;
      return;
    }

    logger.info(
      { oldMode: this.mode, newMode: mode, newModeName: modeName },
      'Discovered new mode',
    );
    this.mode = mode;

    this.dispatch(addModeSwitchNotification(mode));
    return this.dispatch(setCurrentModeDone(mode));
  };

  loadClientChunk() {
    return this.dispatch(loadClientChunk(this.protobuf));
  }

  resetClientChunk() {
    return this.dispatch(resetClientChunk(this.protobuf));
  }

  subscribeToNotificationAndMessageStreams() {
    this.subscribeToObserverNotificationStream();
    this.subscribeToObserverMessageStream();
    this.subscribeToGatewayNotificationStream();
    this.subscribeToGatewayMessageStream();
  }

  subscribeToObserverNotificationStream() {
    this.unsubscribeFromObserverNotificationStream();
    const req = Protobuf.SvPb.GetNotificationStreamRequest.create({});
    this.observerNotificationSubscription = this.protobuf.stream(req).subscribe({
      next: (evt) => this.onObserverNotification(evt),
      error: (err) => this.onObserverNotificationError(err),
      complete: () => logger.debug('Notification observable completed'),
    });
  }

  unsubscribeFromObserverNotificationStream() {
    if (this.observerNotificationSubscription) {
      this.observerNotificationSubscription.unsubscribe();
    }
  }

  onObserverNotification({ type, parameter }) {
    let fileName, configName;
    switch (type) {
      case Protobuf.SvPb.NotifyType.currentMode:
        const mode = this.getVariantValue(parameter);
        logger.debug({ mode }, 'Notification currentMode');
        // the notification contains the new mode already, but it might be "modeChanging"
        // so lets just fetch it from the SVObserver so we always have the actual mode.
        this.discoverDeviceMode();
        break;
      case Protobuf.SvPb.NotifyType.lastModified:
        const timestamp = this.getVariantValue(parameter) * 1000;
        logger.debug({ timestamp: new Date(timestamp) }, 'Notification lastModified');
        this.dispatch(addProjectModifiedNotification());
        this.loadProject();
        break;
      case Protobuf.SvPb.NotifyType.configLoaded:
        fileName = this.getVariantValue(parameter);
        configName = Configuration.extractConfigNameFromFileName(fileName);
        logger.debug({ fileName, configName }, 'Notification configLoaded');
        this.dispatch(addProjectLoadedNotification(fileName));
        this.loadProject();
        break;
      case Protobuf.SvPb.NotifyType.configUnloaded:
        fileName = this.getVariantValue(parameter);
        configName = Configuration.extractConfigNameFromFileName(fileName);
        logger.debug({ fileName, configName }, 'Notification configUnloaded');
        this.dispatch(addProjectUnloadedNotification(fileName));
        this.loadProject();
        break;
      default:
        logger.warn({ type, parameter }, 'Unknown notification type');
        break;
    }
  }

  getVariantValue(variant) {
    return variant && variant[variant.data];
  }

  formatMessageNotification(messageText, errorNumber) {
    return (
      <div>
        <FormattedMessage id="notifications.checkSvimOutput" />
        {messageText && (
          <div>
            {errorNumber ? `(${errorNumber})` : ' '} {messageText}
          </div>
        )}
      </div>
    );
  }

  onObserverNotificationError(err) {
    logger.error({ err }, 'Notification error');
    this.unsubscribeFromObserverNotificationStream();
  }

  subscribeToObserverMessageStream() {
    this.unsubscribeFromObserverMessageStream();
    const req = Protobuf.SvPb.GetMessageStreamRequest.create({});
    this.observerMessageSubscription = this.protobuf.stream(req).subscribe({
      next: (evt) => this.onObserverMessage(evt),
      error: (err) => this.onObserverMessageError(err),
      complete: () => logger.debug('Notification observable completed'),
    });
  }

  unsubscribeFromObserverMessageStream() {
    if (this.observerMessageSubscription) {
      this.observerMessageSubscription.unsubscribe();
    }
  }

  onObserverMessage(evt) {
    const { type, messageList } = evt;
    const { messages } = messageList;
    logger.debug({ ...evt }, 'SVObserver message stream event');

    switch (type) {
      case Protobuf.SvPb.MessageType.startMsgBox:
        if (!messages || messages.length !== 1) {
          logger.warn({ type, messages }, 'Expected exactly one message in the message event');
          return;
        }
        const firstMessage = messages[0];
        const message = this.formatMessageNotification(
          firstMessage.messageText,
          firstMessage.messageCode,
        );
        this.dispatch(showGlobalMessageOverlay({ message }));
        break;

      case Protobuf.SvPb.MessageType.endMsgBox:
        this.dispatch(hideGlobalMessageOverlay());
        break;

      case Protobuf.SvPb.MessageType.msgLog:
        // TODO also handle seveirty
        (messages || []).forEach(({ messageCode, messageText }) => {
          this.dispatch(addServerMessageNotification(messageText, messageCode));
        });
        break;

      default:
        logger.warn({ type }, 'Unknown message event type');
        break;
    }
  }

  onObserverMessageError(err) {
    logger.error({ err }, 'SVObserver message stream error');
    this.unsubscribeFromObserverMessageStream();
  }

  subscribeToGatewayNotificationStream() {
    this.unsubscribeFromGatewayNotificationStream();
    const req = Protobuf.SvPb.GetGatewayNotificationStreamRequest.create({});
    this.gatewayNotificationSubscription = this.protobuf.stream(req).subscribe({
      next: (evt) => this.onGatewayNotification(evt),
      error: (err) => this.onGatewayNotificationError(err),
      complete: () => logger.debug('Gateway Notification observable completed'),
    });
  }

  unsubscribeFromGatewayNotificationStream() {
    if (this.gatewayNotificationSubscription) {
      this.gatewayNotificationSubscription.unsubscribe();
    }
  }

  onGatewayNotification(evt) {
    const value = evt[evt.message];
    switch (evt.message) {
      case 'rejectStreamPaused':
        const { pausedInspectionIds } = value;
        logger.debug({ pausedInspectionIds }, 'Notification rejectStreamPaused');
        this.onPauseNotification(pausedInspectionIds);
        break;
      default:
        logger.warn({ type: evt.message }, 'Unknown gateway notification type');
        break;
    }
  }

  onGatewayNotificationError(err) {
    logger.error({ err }, 'Gateway Notification error');
    this.unsubscribeFromGatewayNotificationStream();
  }

  subscribeToGatewayMessageStream() {
    this.unsubscribeFromGatewayMessageStream();
    const req = Protobuf.SvPb.GetGatewayMessageStreamRequest.create({});
    this.GatewayMessageSubscription = this.protobuf.stream(req).subscribe({
      next: (evt) => this.onGatewayMessage(evt),
      error: (err) => this.onGatewayMessageError(err),
      complete: () => logger.debug('Notification observable completed'),
    });
  }

  unsubscribeFromGatewayMessageStream() {
    if (this.GatewayMessageSubscription) {
      this.GatewayMessageSubscription.unsubscribe();
    }
  }

  onGatewayMessage(evt) {
    const {
      type,
      messageList: { messages = [] },
    } = evt;
    logger.debug({ ...evt }, 'Gateway message stream event');

    switch (type) {
      case Protobuf.SvPb.MessageType.startMsgBox:
        if (messages.length !== 1) {
          logger.warn({ type, messages }, 'Expected exactly one message in the message event');
          return;
        }
        const firstMessage = messages[0];
        const message = this.formatMessageNotification(
          firstMessage.messageText,
          firstMessage.messageCode,
        );
        this.dispatch(showGlobalMessageOverlay({ message }));
        break;

      case Protobuf.SvPb.MessageType.endMsgBox:
        this.dispatch(hideGlobalMessageOverlay());
        break;

      case Protobuf.SvPb.MessageType.msgLog:
        // TODO also handle seveirty
        messages.forEach(({ messageCode, messageText }) => {
          this.dispatch(addServerMessageNotification(messageText, messageCode));
        });
        break;

      default:
        logger.warn({ type }, 'Unknown message event type');
        break;
    }
  }

  onGatewayMessageError(err) {
    logger.error({ err }, 'Gateway message stream error');
    this.unsubscribeFromGatewayMessageStream();
  }

  subscribeToProductStream(desc, cb) {
    const id = this._getNextSubId();
    this.subscriptionManager.addSubscription(id, desc, cb);
    return id;
  }

  unsubscribeFromProductStream(id) {
    this.subscriptionManager.removeSubscription(id);
  }

  _getNextSubId() {
    return `${++this.nextSubId}`;
  }

  setUpProductHistories(config) {
    const inspections = config.getInspections();
    this.productHistoryManager.init(inspections);
  }

  async replayProduct(inspectionId, product) {
    await this.fetchMissingProductData(inspectionId, product);
    this.subscriptionManager.replayProduct(inspectionId, product);
  }

  async fetchMissingProductData(inspectionId, product) {
    const missingImageIds = this.getMissingProductImages(inspectionId, product);
    const missingValueIds = this.getMissingProductValues(inspectionId, product);
    if (missingImageIds.length === 0 && missingValueIds.length === 0) {
      return;
    }

    const req = Protobuf.SvPb.GetProductDataRequest.create({
      inspectionId,
      trigger: product.trigger,
      imageIds: missingImageIds,
      valueIds: missingValueIds,
      includeOverlays: true,
    });
    try {
      const res = await this.protobuf.request(req);
      await product.addData({
        trigger: res.trigger,
        imageIds: missingImageIds,
        images: res.images,
        overlays: res.overlays,
        valueIds: missingValueIds,
        values: res.values,
      });
    } catch (error) {
      logger.error(error);
    }
  }

  getMissingProductImages(inspectionId, product) {
    const imageIds = this.subscriptionManager.getSubscribedImages(inspectionId);
    return imageIds.filter((imageId) => product.getImage(imageId) === undefined);
  }

  getMissingProductValues(inspectionId, product) {
    const valueIds = this.subscriptionManager.getSubscribedValues(inspectionId);
    return valueIds.filter((valueId) => product.getValue(valueId) === undefined);
  }

  onPauseNotification(pausedInspectionIds) {
    this.dispatch(addRejectStreamPausedNotification(pausedInspectionIds));
    this.subscriptionManager.updatePausedInspections(pausedInspectionIds);
    this.dispatch(setPausedInspections(pausedInspectionIds));
  }

  pauseRejectStream(inspectionId) {
    this.pauseRejectStreamImpl(true, inspectionId);
  }

  resumeRejectStream(inspectionId) {
    this.pauseRejectStreamImpl(false, inspectionId);
  }

  async pauseRejectStreamImpl(pause, inspectionId) {
    const request = Protobuf.SvPb.SetRejectStreamPauseStateRequest.create({
      inspectionId: inspectionId || undefined,
      pause,
    });

    try {
      await this.protobuf.request(request);
    } catch (error) {
      logger.error(error);
    }
  }

  async getMyPermissions() {
    let response = {};
    try {
      const request = Protobuf.SvPb.GetMyPermissionsRequest.create();
      response = await this.protobuf.request(request);
    } catch (err) {
      logger.error('Cannot get my permissions, fall back to default permissions');
      response = defaultPermissions;
    }

    if (!response || !response.permissions) {
      logger.error('Cannot get my permissions');
      return false;
    }

    const myPermissions = new Set();
    flattenPermissionsRec(response.permissions, myPermissions, '');
    this.dispatch(setMyPermissions(myPermissions));

    if (canReadUserManagement(myPermissions) || canEditUserManagement(myPermissions)) {
      await this.getGroupPermissions();
    }
  }

  async getGroupPermissions() {
    const request = Protobuf.SvPb.GetGroupDetailsRequest.create();
    const response = await this.protobuf.request(request);

    if (!response || !response.groupDetails) {
      logger.error('Cannot get group permissions');
      return false;
    }

    const flattenedPermissions = this.flattenPermissions(response.groupDetails);
    this.dispatch(setGroupPermissions(flattenedPermissions));
  }

  flattenPermissions(groupDetails) {
    const permissions = new Map();
    (groupDetails || []).forEach((groupDetail) => {
      const groupPermissions = new Set();
      flattenPermissionsRec(groupDetail.permissions, groupPermissions, '');
      permissions.set(groupDetail.name, {
        groupPermissions,
        users: groupDetail.users,
      });
    });
    return permissions;
  }

  async setUserPermissions(groupPermissions) {
    const request = Protobuf.SvPb.UpdateGroupPermissionsRequest.create({
      groupPermissions,
    });
    try {
      if (this.protobuf) {
        await this.protobuf.request(request);
        await this.getGroupPermissions();
      }
    } catch (e) {
      logger.error({ e }, 'Error while setting user permissions');
    }

    logger.info('Successfully saved user permissions');
  }
}
