import React from 'react';

import SingleParameter from '../../generic/SingleParameter';

const TextParameter = ({ value, changeCallback, removeCallback, typeName }) => {
  return (
    <SingleParameter
      value={value}
      typeName={typeName}
      changeCallback={changeCallback}
      removeCallback={removeCallback}
      fieldType="text"
      defaultValue={''}
    />
  );
};

export default TextParameter;
