import PropTypes from 'prop-types';
import React, { Component } from 'react';
import Paper from '@material-ui/core/Paper';
import MuiMenuList from '@material-ui/core/MenuList';
import MenuItem from '@material-ui/core/MenuItem';
import { withStyles } from '@material-ui/core/styles';

import './style.scss';
import OutsideClickHandler from '../../../src/components/OutsideClickHandler';

const styles = (theme) => ({
  rootMenu: {
    overflow: 'visible',
    backgroundColor: '#f3f3f3',
    position: 'absolute',
    '& ul': {
      padding: 0,
    },
  },
  menuItem: {
    cursor: 'pointer',
    display: 'flex',
    justifyContent: 'space-between',
    overflow: 'visible',
    position: 'relative',
    borderBottom: '0.1rem #cccccc solid',
    '& a': {
      color: theme.palette.common.black,
    },
  },
  caption: {
    alignItems: 'center',
    display: 'flex',
    fontSize: '1.4rem',
    '& img': {
      marginRight: '0.5rem',
      maxWidth: '24px',
    },
  },
});
class MenuList extends Component {
  state = {
    openMenu: false,
  };
  renderMenuItems = () => {
    const { menuItems, onClose, classes, enabled } = this.props;
    return menuItems.map((menuItem) => {
      return (
        <MenuItem
          disabled={!enabled}
          key={menuItem.key}
          onClick={() => {
            if (enabled) {
              onClose();
              this.closeMenu();
              menuItem.onClick();
            }
          }}
          className={
            menuItem.className ? classes.menuItem + ' ' + menuItem.className : classes.menuItem
          }
        >
          <div className={classes.caption}>
            {menuItem.img ? <img src={menuItem.img} alt={menuItem.caption} /> : null}
            {menuItem.caption}
          </div>
        </MenuItem>
      );
    });
  };

  handleClick = () => {
    this.setState({ openMenu: true });
  };

  closeMenu = () => {
    this.setState({ openMenu: false });
    this.props.onClose();
  };

  render() {
    const { buttonIcon, buttonCaption, tooltip, open, onClose, showBottomArrow, classes } =
      this.props;
    const { openMenu } = this.state;

    return (
      <div>
        <button className={'menu-button'} onClick={this.handleClick} title={tooltip}>
          {buttonIcon && <img src={buttonIcon} alt={buttonCaption} />}
          {buttonCaption}
          {showBottomArrow ? <div className="bottomArrow"></div> : null}
        </button>
        <OutsideClickHandler
          onOutsideClick={() => {
            this.closeMenu();
          }}
        >
          {openMenu ? (
            <Paper className={classes.rootMenu}>
              <MuiMenuList open={open} onClose={onClose}>
                {this.renderMenuItems()}
              </MuiMenuList>
            </Paper>
          ) : (
            <div></div>
          )}
        </OutsideClickHandler>
      </div>
    );
  }
}

MenuList.defaultProps = {
  open: false,
  enabled: true,
};

MenuList.propTypes = {
  onClose: PropTypes.func.isRequired,
  open: PropTypes.bool.isRequired,
};

export default withStyles(styles)(MenuList);
