import React from 'react';

import BoolParameter from '../BoolParameter';
import ArrayParameter from '../../generic/ArrayParameter';

const BoolArrayParameter = ({ value, changeCallback, typeName }) => {
  return (
    <ArrayParameter
      value={value}
      changeCallback={changeCallback}
      typeName={typeName}
      defaultValue={false}
      SingleParameterComponent={BoolParameter}
    />
  );
};

export default BoolArrayParameter;
