import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';

import './style.scss';
import NavigationItem from '../../../../../../components/NavigationItem';

const PresetButton = ({ id, title, onClick, active }) => {
  const presetButtonClass = classNames({
    'preset-button': true,
    'preset-button-active': active,
  });
  return (
    <NavigationItem>
      <button id={id} onClick={onClick} className={presetButtonClass}>
        <span>{title}</span>
      </button>
    </NavigationItem>
  );
};

PresetButton.defaultProps = {
  id: undefined,
  onClick: () => {},
  active: false,
};

PresetButton.propTypes = {
  id: PropTypes.string,
  title: PropTypes.node.isRequired,
  onClick: PropTypes.func,
  active: PropTypes.bool,
};

export default PresetButton;
