export const setSaveOperationInProcess = (inProcess) => ({
  type: 'UPDATE_SAVE_OPERATION_IN_PROCESS',
  inProcess,
});

export const setSaveFlag = (hasChanges) => ({
  type: 'SET_SAVE_FLAG',
  hasChanges,
});

export const saveClientChunk = (protobuf, currentState) => async (dispatch) => {
  const actions = {
    setSaveOperationInProcess,
    setSaveFlag,
  };
  await dispatch(saveChunk(protobuf, currentState, actions));
};

export const loadClientChunk = (protobuf) => async (dispatch) => {
  const actions = {
    setSharedStateData,
    setClientChunk,
    setViewData,
    setEditingFocus,
    addIncompatibilityDetectedNotification,
    setListViewAliases,
    saveImageSettings,
    setSVOConfigLoaded,
  };
  await dispatch(loadChunk(protobuf, actions));
};

export const resetClientChunk = (protobuf) => async (dispatch) => {
  const actions = {
    setClientChunk,
    setSVOConfigLoaded,
  };
  await dispatch(resetChunk(protobuf, actions));
};

