import React from 'react';
import { useSelector } from 'react-redux';
import { FormattedMessage } from 'react-intl';

import MenuList from '../../../../components/MenuList';
import iconPlay from '../../../../assets/icons/play.svg';
import iconPlayMultiple from '../../../../assets/icons/play-multiple.svg';
import iconPause from '../../../../assets/icons/pause.svg';
import { canReadInspectionState, canEditInspectionState } from '../../../../utils/permissions';

export const InspectionStateSwitch = () => {
  const { proxy, configuration, pausedInspections, myPermissions } = useSelector(
    (state) => state.app,
  );
  const inspections = (configuration && configuration.getInspections()) || [];
  const allRunning = pausedInspections.length === 0;
  const canRead = canReadInspectionState(myPermissions);
  const canEdit = canEditInspectionState(myPermissions);
  const menuItems = inspections.map((inspection) => {
    const isPaused = (pausedInspections || []).includes(inspection.id);

    return {
      key: inspection.id,
      onClick: () => {
        isPaused ? proxy.resumeRejectStream(inspection.id) : proxy.pauseRejectStream(inspection.id);
      },
      caption: inspection.name,
      img: isPaused ? iconPlay : iconPause,
      className: isPaused ? 'paused-inspection' : undefined,
    };
  });
  const globalItem = {
    key: 'globalResumePause',
    onClick: () => {
      allRunning ? proxy.pauseRejectStream() : proxy.resumeRejectStream();
    },
    caption: allRunning ? 'Pause all inspections' : 'Resume all inspections',
    img: allRunning ? iconPause : iconPlay,
    className: 'all-inspections',
  };
  menuItems.unshift(globalItem);

  return canRead || canEdit ? (
    <div>
      <MenuList
        buttonCaption={
          <FormattedMessage
            id="topNavigation.inspectionState"
            values={{
              runningInspections: inspections.length - pausedInspections.length,
              numberOfInspections: inspections.length,
            }}
          />
        }
        buttonIcon={iconPlayMultiple}
        menuItems={menuItems}
        onClose={() => {}}
        showBottomArrow
        enabled={canEdit}
      />
    </div>
  ) : (
    <div></div>
  );
};
