class Observer {
  constructor(observable) {
    this.closed = false;
    this.observable = observable;
  }

  next(v) {
    return this.observable.onNext(v);
  }

  error(err) {
    return this.observable.onError(err);
  }

  complete() {
    return this.observable.onComplete();
  }
}

export class Observable {
  static create(onSubscribe) {
    return new Observable(onSubscribe);
  }

  constructor(onSubscribe) {
    this._onSubscribe = onSubscribe;
    this._next = () => {};
    this._error = () => {};
    this._complete = () => {};
  }

  subscribe(desc) {
    if (typeof desc.next === 'function') {
      this._next = desc.next;
    }
    if (typeof desc.error === 'function') {
      this._error = desc.error;
    }
    if (typeof desc.complete === 'function') {
      this._complete = desc.complete;
    }
    const observer = new Observer(this);
    // TODO use of tearDownCb
    const tearDownCb = this._onSubscribe(observer);
    return {
      unsubscribe: () => {
        if (typeof tearDownCb === 'function') {
          tearDownCb();
        }
        // TODO support multiple subscriber?
        this._next = () => {};
        this._error = () => {};
        this._complete = () => {};
      },
    };
  }

  onNext(v) {
    return this._next(v);
  }

  onError(err) {
    return this._error(err);
  }

  onComplete() {
    return this._complete();
  }
}
