const AUTH_TOKEN_KEY = 'svAuthToken';
const USER_KEY = 'svObserverUser';
export const CONFIG_INFO = 'configInfo';
export const CONFIGURATION_CHUNK_TEMPLATE = 'configChunk_';
export const CONFIGURATION_CHUNK_LIST = 'configChunkList';

export const loadState = () => {
  const authToken = localStorage.getItem(AUTH_TOKEN_KEY);
  if (!authToken) {
    return {};
  }

  const userStr = localStorage.getItem(USER_KEY);
  let user;
  try {
    user = JSON.parse(userStr);
  } catch (e) {
    return {};
  }

  if (!user) {
    return {};
  }

  return { authToken, user };
};

export const resetState = () => {
  localStorage.removeItem(AUTH_TOKEN_KEY);
  localStorage.removeItem(USER_KEY);
  localStorage.removeItem(CONFIG_INFO);
  localStorage.removeItem(CONFIGURATION_CHUNK_LIST);
};

export const saveState = (authToken, user) => {
  localStorage.setItem(AUTH_TOKEN_KEY, authToken);
  localStorage.setItem(USER_KEY, JSON.stringify(user));
};
