import PropTypes from 'prop-types';
import classNames from 'classnames';
import React, { Component } from 'react';
import { injectIntl } from 'react-intl';

import './style.scss';
import closeIcon from '../../assets/icons/close.svg';

import IconButton from '../buttons/IconButton';
import ActionBar from '../../components/ActionBar';
import ActionBarType from '../../types/ActionBarType';

class Dialog extends Component {
  render() {
    const {
      children,
      onClose,
      dialogActions,
      title,
      intl,
      open,
      isInView,
      isHeight100,
      isFullscreen,
      isPopup,
    } = this.props;
    const dialogClass = classNames({
      dialog: true,
      'dialog--open': open,
      'is-in-view': isInView,
      'is-height-100': isHeight100,
    });
    const dialogWrapperClass = classNames({
      'dialog-wrapper': true,
      'is-height-100': isInView || isFullscreen,
      'is-width-100': isFullscreen,
      isPopup: isPopup,
    });

    const dialogContentWrapperClass = classNames({
      'dialog-content-wrapper': true,
      'is-padding-0': isInView,
      isPopup: isPopup,
    });

    if (!open) {
      return false;
    }
    return (
      <div className={dialogClass}>
        <div className={dialogWrapperClass}>
          <div className="dialog-toolbar">
            <div className="dialog-title">{title}</div>
            <div className="dialog-controll-wrapper">
              <IconButton
                tooltip={intl.messages['viewSetManager.close']}
                iconUrl={closeIcon}
                onClick={() => {
                  onClose();
                }}
              />
            </div>
          </div>
          <div className={dialogContentWrapperClass}>{children}</div>
          <div className="dialog-action-wrapper">
            {dialogActions && <ActionBar isViewAction actions={dialogActions} />}
          </div>
        </div>
      </div>
    );
  }
}

Dialog.defaultProps = {
  open: false,
  title: undefined,
  dialogActions: undefined,
  isInView: false,
  isFullscreen: false,
  isPopup: false,
};

Dialog.propTypes = {
  children: PropTypes.node.isRequired,
  open: PropTypes.bool,
  isInView: PropTypes.bool,
  dialogActions: ActionBarType,
  title: PropTypes.node,
  onClose: PropTypes.func.isRequired,
  isFullscreen: PropTypes.bool,
  isPopup: PropTypes.bool,
};

export default injectIntl(Dialog);
