import modes from '../utils/modes';
import Protobuf from './Protobuf';

export const mapProtobufDeviceModeToWebAppMode = (deviceMode) => {
  switch (deviceMode) {
    case Protobuf.SvPb.DeviceModeType.available:
      return modes.unknownMode.id;
    case Protobuf.SvPb.DeviceModeType.runMode:
      return modes.runMode.id;
    case Protobuf.SvPb.DeviceModeType.stopMode:
      return modes.stopMode.id;
    case Protobuf.SvPb.DeviceModeType.regressionMode:
      return modes.regressionMode.id;
    case Protobuf.SvPb.DeviceModeType.testMode:
      return modes.testMode.id;
    case Protobuf.SvPb.DeviceModeType.editMode:
      return modes.editMode.id;
    case Protobuf.SvPb.DeviceModeType.modeChanging:
      return modes.unknownMode.id;
    default:
      throw new Error(`Unsupported device mode ${deviceMode}`);
  }
};

export const mapWebAppModeToProtobufDeviceMode = (mode) => {
  switch (mode) {
    case modes.unknownMode.id:
      return Protobuf.SvPb.DeviceModeType.available;
    case modes.runMode.id:
      return Protobuf.SvPb.DeviceModeType.runMode;
    case modes.stopMode.id:
      return Protobuf.SvPb.DeviceModeType.stopMode;
    case modes.regressionMode.id:
      return Protobuf.SvPb.DeviceModeType.regressionMode;
    case modes.testMode.id:
      return Protobuf.SvPb.DeviceModeType.testMode;
    case modes.editMode.id:
      return Protobuf.SvPb.DeviceModeType.editMode;
    default:
      throw new Error(`Unsupported mode ${mode}`);
  }
};

export const mapLogLevelNameToProtobufLogSeverity = (levelName) => {
  switch (levelName) {
    case 'trace':
      return Protobuf.SvPb.LogSeverity.trace;
    case 'debug':
      return Protobuf.SvPb.LogSeverity.debug;
    case 'info':
      return Protobuf.SvPb.LogSeverity.info;
    case 'warn':
      return Protobuf.SvPb.LogSeverity.warning;
    case 'error':
      return Protobuf.SvPb.LogSeverity.error;
    default:
      return Protobuf.SvPb.LogSeverity.error;
  }
};
