import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import InspectionSelection from './components/InspectionSelection';
import ViewTypeSelection from './components/ViewTypeSelection';
import { replaceViewInCurrentViewSet, purgeViewState } from '../../redux/actions';
import './style.scss';

class ViewSelection extends Component {
  state = {
    visibleComponent: 'ViewTypeSelection',
    selectedInspectionId: 0,
    selectedViewType: '',
  };

  componentDidMount() {
    const { viewToShowWithoutInspection } = this.props;

    if (viewToShowWithoutInspection) {
      this.setState({
        visibleComponent: 'InspectionSelection',
        selectedViewType: viewToShowWithoutInspection,
      });
    }
  }

  setSelectedInspectionId = (inspectionId) => {
    this.setState({
      visibleComponent: '',
      selectedInspectionId: inspectionId,
    });

    this.props.changeTitle(inspectionId);
    this.updateView(inspectionId);
  };

  setSelectedViewType = (viewType) => {
    this.setState({
      visibleComponent: 'InspectionSelection',
      selectedViewType: viewType,
    });
  };

  goBackToViewTypeSelection = () => {
    this.setState({
      visibleComponent: 'ViewTypeSelection',
      selectedInspectionId: 0,
    });
  };

  cancelViewSelection = () => {
    const { changeContent } = this.props;
    changeContent();
  };

  updateView = (inspectionId) => {
    const { replaceViewInCurrentViewSet, viewId, changeContent } = this.props;
    const { selectedViewType } = this.state;

    replaceViewInCurrentViewSet(viewId, selectedViewType, inspectionId);

    changeContent();
  };

  render() {
    const { showCancel } = this.props;
    const { visibleComponent } = this.state;

    return (
      <div className="view-selection-wrapper">
        {(() => {
          switch (visibleComponent) {
            case 'InspectionSelection':
              return (
                <InspectionSelection
                  onSelection={this.setSelectedInspectionId}
                  goBack={this.goBackToViewTypeSelection}
                  showCancel={showCancel}
                />
              );
            case 'ViewTypeSelection':
              return (
                <ViewTypeSelection
                  onSelection={this.setSelectedViewType}
                  goBack={this.cancelViewSelection}
                />
              );
            default:
              return <div></div>;
          }
        })()}
      </div>
    );
  }
}

ViewSelection.propTypes = {
  replaceViewInCurrentViewSet: PropTypes.func.isRequired,
  viewId: PropTypes.string.isRequired,
  changeContent: PropTypes.func,
  changeTitle: PropTypes.func,
  showCancel: PropTypes.bool,
};

const mapDispatchToProps = (dispatch) => ({
  replaceViewInCurrentViewSet: (viewId, replacementComponentId, inspectionId) => {
    dispatch(purgeViewState(undefined, viewId));
    dispatch(replaceViewInCurrentViewSet(viewId, replacementComponentId, inspectionId));
  },
});

export default connect(null, mapDispatchToProps)(ViewSelection);
