import { connect } from 'react-redux';
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { injectIntl } from 'react-intl';

import './style.scss';

import Overlay from './components/Overlay';
import ActionBar from '../../components/ActionBar';
import ActionBarType from '../../types/ActionBarType';
import Container from '../../components/layout/Container';
import { canReadParameterView, canEditParameterView } from '../../utils/permissions';

class ParameterView extends Component {
  doSomething = () => {}; // Implement some more logic

  render() {
    const { parameter, myPermissions } = this.props;

    if (canReadParameterView(myPermissions) || canEditParameterView(myPermissions)) {
      return (
        <div className="parameter-view-wrapper">
          <Container>
            {parameter.component || (
              <Overlay inspectionId={this.props.inspectionId} id={this.props.id} />
            )}
          </Container>
          <ActionBar isViewAction actions={[]} />
        </div>
      );
    }
    return <div></div>;
  }
}

ParameterView.defaultProps = {};

ParameterView.propTypes = {
  viewActions: ActionBarType,
  parameter: PropTypes.arrayOf(
    PropTypes.shape({
      component: PropTypes.element,
    }),
  ).isRequired,
};

const mapStateToProps = (state) => ({
  parameter: state.toolSet.parameter,
  myPermissions: state.app.myPermissions,
});

export default injectIntl(connect(mapStateToProps)(ParameterView));
