import Protobuf from './Protobuf';
import WebsocketConnector from './WebsocketConnector';

import logger from '../utils/logger';
import { setProtobuf } from '../redux/actions';

export default class ProtobufConnector {
  constructor(url, dispatch, authToken) {
    this.url = url;
    this.dispatch = dispatch;
    this.protobuf = new Protobuf();
    this.websocketConnector = null;
    this.connectionLost = false;
    this.authToken = authToken;
    this.connect();
  }

  connect = () => {
    this.disconnect();

    if (!this.authToken) {
      logger.info('[ProtobufConnector] cannot connect yet. no auth token available');
      return;
    }

    this.websocketConnector = new WebsocketConnector(
      this.onConnect,
      this.onMessage,
      this.onClose,
      this.onError,
      this.url,
      this.authToken,
    );
  };

  connectWithToken = (token) => {
    this.authToken = token;
    this.connect();
  };

  disconnect = () => {
    if (this.websocketConnector) {
      this.websocketConnector.closeSocket();
      this.websocketConnector = null;
    }
  };

  onMessage = (data) => {
    this.protobuf.readMessage(data);
  };

  onConnect = async (socket) => {
    // Force a hard reload on re-conncet to make sure we start fresh
    if (this.connectionLost) {
      this.connectionLost = false;
      setImmediate(() => {
        window.location.href = `${window.location.origin}/`;
      });
      return;
    }

    // once we have a websocket connection established we publish the protobuf connector via store
    this.dispatch(setProtobuf(this.protobuf, socket));
  };

  onClose = () => {
    this.connectionLost = true;
    // unsetting the socket will set proxy into disconnected
    // state and will show the "Connecting" screen.
    this.dispatch(setProtobuf(this.protobuf, /*socket=*/ null));
  };

  onError = (event) => {
    console.error('Websocket error: ', event);
  };
}
