import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import { setLocale, addUserSwitchedLanguageNotification } from '../../../../redux/actions';
import LanguageIcon from '../../../../assets/icons/language.svg';
import MenuList from '../../../../components/MenuList';
import { languages } from '../../../../translations';

import './style.scss';

class LanguageChooser extends Component {
  changeLocation = (locale) => {
    const { changeLocale, dispatchUserSwitchedLanguageNotification } = this.props;
    changeLocale(locale);
    dispatchUserSwitchedLanguageNotification(locale);
  };

  render() {
    const { languages, locale } = this.props;
    const menuItems = languages.map((language) => ({
      key: language.locale,
      caption: language.name,
      onClick: () => {
        this.changeLocation(language.locale);
      },
    }));

    return (
      <div className="flex">
        <MenuList
          buttonIcon={LanguageIcon}
          buttonCaption={locale}
          menuItems={menuItems}
          showBottomArrow
          onClose={() => {}}
          open={false}
        />
      </div>
    );
  }
}

LanguageChooser.propTypes = {
  languages: PropTypes.arrayOf(
    PropTypes.shape({
      locale: PropTypes.string,
      name: PropTypes.node,
    }),
  ),
  locale: PropTypes.string.isRequired,
  changeLocale: PropTypes.func.isRequired,
};

LanguageChooser.defaultProps = {
  languages,
};

const mapStateToProps = (state) => ({
  locale: state.app.locale,
});

const mapDispatchToProps = (dispatch) => ({
  changeLocale: (locale) => dispatch(setLocale(locale)),
  dispatchUserSwitchedLanguageNotification: (locale) =>
    dispatch(addUserSwitchedLanguageNotification(locale)),
});

export default connect(mapStateToProps, mapDispatchToProps)(LanguageChooser);
