import PropTypes from 'prop-types';
import { useSelector, useDispatch } from 'react-redux';
import React, { useState, useRef } from 'react';
import { FormattedMessage, injectIntl } from 'react-intl';
import semver from 'semver';
import Table, { AutoResizer } from 'react-base-table';

import addIcon from '../../assets/icons/plus.svg';
import removeIcon from '../../assets/icons/delete.svg';
import iconSave from '../../assets/icons/save.svg';
import exportIcon from '../../assets/icons/export.svg';
import importIcon from '../../assets/icons/import.svg';
import iconCheck from '../../assets/icons/check.svg';
import EditIcon from '../../assets/icons/edit.svg';

import Dialog from '../../components/Dialog';
import ConfirmDialog from '../../components/ConfirmDialog';
import InformationDialog from '../../components/InformationDialog';
import { forceDownload } from '../../utils/downloader';
import {
  canReadViewSet,
  canEditViewSet,
  canDeleteViewSet,
  canSaveConfig,
} from '../../utils/permissions';
import logger from '../../utils/logger';
import TextField from '@material-ui/core/TextField';
import IconButton from '../../components/buttons/IconButton';
import {
  removeViewSet,
  renameViewSet,
  newViewSet,
  setViewSet,
  setEditingFocus,
  purgeViewState,
} from '../../redux/actions';

import './style.scss';

const ViewSetManager = ({ onClose, onSave, intl }) => {
  const fileChooser = useRef(null);
  const dispatch = useDispatch();
  const createdViewSets = useSelector((state) => state.viewSet.createdViewSets);
  const currentViewSet = useSelector((state) => state.viewSet.currentViewSet);
  const inspections = useSelector((state) => state.inspections);
  const configuration = useSelector((state) => state.app.configuration);
  const myPermissions = useSelector((state) => state.app.myPermissions);
  const canEdit = canEditViewSet(myPermissions);
  const canDelete = canDeleteViewSet(myPermissions);

  const [name, setName] = useState(currentViewSet.name);
  const [isImportConfirmOpen, setIsImportConfirmOpen] = useState(false);
  const [viewsetToDelete, setViewsetToDelete] = useState('');
  const [currentImportedViewSet, setCurrentImportedViewSet] = useState(null);
  const [isInformationDialogOpen, setIsInformationDialogOpen] = useState(false);
  const [informationDialogTitle, setInformationDialogTitle] = useState('');
  const [informationDialogMessage, setInformationDialogMessage] = useState('');
  const [shouldCloseViewSetManager, setShouldCloseViewSetManager] = useState(false);
  const [viewSetToEdit, setViewSetToEdit] = useState('');

  const fileLoaderVersion = '1.0.0';

  const getNextFreeName = (nameToTry) => {
    let name = nameToTry;
    let counter = 1;
    while (existsName(name)) {
      name = `${nameToTry}${counter}`;
      counter++;
    }

    return name;
  };

  const existsName = (name) => {
    return Object.keys(createdViewSets).filter((key) => key === name).length > 0;
  };

  const addViewSet = () => {
    const newViewSetName = getNextFreeName('Initial');
    dispatch(newViewSet(newViewSetName));
  };

  const removeSelectedViewSet = () => {
    dispatch(removeViewSet(viewsetToDelete));
  };

  const saveViewSets = () => {
    onSave();
    closeViewSetManager();
  };

  const exportViewSets = async () => {
    const webAppVersion = configuration.getWebAppVersion();
    const dataToSave = {
      currentViewSet,
      editingViewIds: getEditingViewIdsPerInspection(),
      timestamp: new Date(),
      version: '1.0.0',
      webAppVersion,
    };

    await createJsonFileFromObject(dataToSave);

    closeViewSetManager();
  };

  const getEditingViewIdsPerInspection = () => {
    const editingViewIds = [];

    Object.keys(inspections).forEach((inspectionIdStr) => {
      const inspectionId = parseInt(inspectionIdStr, 10);
      if (
        inspections[inspectionIdStr] &&
        inspections[inspectionIdStr].sharedState &&
        inspections[inspectionIdStr].sharedState.editingViewId
      ) {
        editingViewIds.push({
          inspectionId,
          editingViewId: inspections[inspectionIdStr].sharedState.editingViewId,
        });
      }
    });

    return editingViewIds;
  };

  const createJsonFileFromObject = async (object) => {
    const json = JSON.stringify(object);
    const blob = new Blob([json], { type: 'application/json' });
    const data = await URL.createObjectURL(blob);

    forceDownload(data, 'viewset.json');
  };

  const importViewSets = () => {
    fileChooser.current.click();
  };

  const onFileAdded = (evt) => {
    const file = evt.target.files[0];

    if (file) {
      const fileReader = new FileReader();
      fileReader.readAsText(file);

      fileReader.onload = (event) => {
        handleFileData(event.target.result);
      };
    }
  };

  const handleFileData = (fileText) => {
    try {
      const data = JSON.parse(fileText);
      if (equalsViewSetName(data.currentViewSet.id)) {
        setIsImportConfirmOpen(true);
        setCurrentImportedViewSet(data);
        return;
      }

      setViewSetConfigurationFromFileData(data);
    } catch (error) {
      showErrorWhileLoadingFileDialog();
      logger.error(error);
    }
  };

  const handleImportConfirmDialogClose = (confirmed) => {
    setIsImportConfirmOpen(false);
    if (confirmed) {
      loadViewSetFromFile();
    } else {
      fileChooser.current.value = '';
    }
  };

  const handleDeleteConfirmDialogClose = (confirmed) => {
    setViewsetToDelete('');
    if (confirmed) {
      removeSelectedViewSet();
    }
  };

  const loadViewSetFromFile = () => {
    setViewSetConfigurationFromFileData(currentImportedViewSet);
  };

  const equalsViewSetName = (name) => {
    if (currentViewSet.id === name) {
      return true;
    }

    return false;
  };

  const setViewSetConfigurationFromFileData = (data) => {
    loadSuccessfulInformationDialogText();

    if (!isMajorVersionCompatible(data.version)) {
      showIncompatibleVersionErrorDialog();
      logger.error('Version of file is not valid.');
      return;
    }

    if (isMinorVersionNewer(data.version)) {
      loadVersionChangeInformationDialogText();
    }

    if (!data.currentViewSet.id) {
      showErrorWhileLoadingFileDialog();
      logger.error('Could not load viewset id.');
      return;
    }
    dispatch(setViewSet(data.currentViewSet, data.currentViewSet.id));

    if (data.editingViewIds) {
      data.editingViewIds.forEach((entry) => {
        dispatch(setEditingFocus(entry.inspectionId, entry.editingViewId));
      });
    }

    dispatch(purgeViewState());
    showInformationDialog();
  };

  const isMajorVersionCompatible = (version) => {
    if (fileLoaderVersion === version) {
      return true;
    }

    if (!semver.valid(version) || semver.diff(fileLoaderVersion, version) === 'major') {
      return false;
    }

    return true;
  };

  const isMinorVersionNewer = (version) => {
    return (
      semver.diff(fileLoaderVersion, version) === 'minor' && semver.gt(version, fileLoaderVersion)
    );
  };

  const closeViewSetManager = () => {
    onClose();
  };

  const loadSuccessfulInformationDialogText = () => {
    setInformationDialogTitle(<FormattedMessage id="notifications.successfulAction" />);
    setInformationDialogMessage(<FormattedMessage id="notifications.successfullyLoadedFile" />);
    setShouldCloseViewSetManager(true);
  };

  const showErrorWhileLoadingFileDialog = () => {
    setInformationDialogTitle(<FormattedMessage id="notifications.errorOccurred" />);
    setInformationDialogMessage(<FormattedMessage id="notifications.error.loadingFile" />);
    setIsInformationDialogOpen(true);
  };

  const showIncompatibleVersionErrorDialog = () => {
    setInformationDialogTitle(
      <FormattedMessage id="notifications.viewsetVersionIncompatibleTitle" />,
    );
    setInformationDialogMessage(
      <FormattedMessage id="notifications.viewsetVersionIncompatibleMessage" />,
    );
    setIsInformationDialogOpen(true);
  };

  const loadVersionChangeInformationDialogText = () => {
    setInformationDialogTitle(<FormattedMessage id="notifications.minorVersionDiffTitle" />);
    setInformationDialogMessage(<FormattedMessage id="notifications.minorVersionDiffMessage" />);
    setShouldCloseViewSetManager(true);
  };

  const showInformationDialog = () => {
    setIsInformationDialogOpen(true);
  };

  const getActions = () => {
    const actions = [];

    if (canSaveConfig(myPermissions)) {
      actions.push({
        intlId: 'viewSetManager.save',
        iconUrl: iconSave,
        onClickHandler: () => {
          saveViewSets();
        },
      });
    }

    if (canReadViewSet(myPermissions)) {
      actions.push({
        intlId: 'viewSetManager.export',
        iconUrl: exportIcon,
        onClickHandler: () => {
          exportViewSets();
        },
      });
    }

    if (canEdit) {
      actions.push({
        intlId: 'viewSetManager.import',
        iconUrl: importIcon,
        onClickHandler: () => {
          importViewSets();
        },
      });
    }

    return actions;
  };

  const getColumns = () => {
    return [
      {
        key: 'name',
        dataKey: 'name',
        title: 'Name',
        width: 0,
        flexGrow: 1,
      },
      {
        key: 'edit',
        dataKey: 'edit',
        title: '',
        width: 40,
      },
      {
        key: 'delete',
        dataKey: 'delete',
        title: '',
        width: 40,
      },
    ];
  };

  const changeViewSetName = () => {
    if (viewSetToEdit !== name && Object.keys(createdViewSets).includes(name)) {
      alert('Viewset with this name already exists.');
      return;
    }

    dispatch(renameViewSet(viewSetToEdit, name));

    setViewSetToEdit('');
    setName('');
  };

  const getViewSetEditName = () => {
    return (
      <div className="viewsetmanager-edit-viewset-name-wrapper">
        <TextField
          fullWidth
          type="text"
          inputProps={{
            style: {
              height: 29,
            },
          }}
          value={name}
          onChange={(e) => setName(e.target.value.substring(0, 20))}
        />
      </div>
    );
  };

  const getViewSetEditingIcon = (viewsetName) => {
    if (!canEdit) {
      return undefined;
    }

    if (viewSetToEdit !== viewsetName) {
      return (
        <IconButton
          onClick={() => {
            setViewSetToEdit(viewsetName);
            setName(viewsetName);
          }}
          tooltip={intl.messages['viewSetManager.editViewSetName']}
          iconUrl={EditIcon}
        />
      );
    } else {
      return (
        <IconButton
          onClick={() => changeViewSetName()}
          tooltip={intl.messages['viewSetManager.changeViewSetName']}
          iconUrl={iconCheck}
        />
      );
    }
  };

  const getViewSets = () => {
    const data =
      createdViewSets &&
      Object.keys(createdViewSets).map((viewsetName) => {
        return {
          edit: getViewSetEditingIcon(viewsetName),
          delete: canDelete ? (
            <IconButton
              onClick={() => setViewsetToDelete(viewsetName)}
              tooltip={intl.messages['viewSetManager.deleteIconTooltip']}
              iconUrl={removeIcon}
            />
          ) : undefined,
          name: viewSetToEdit === viewsetName ? getViewSetEditName() : viewsetName,
          id: viewsetName,
        };
      });
    return data;
  };

  const headerRenderer = ({ cells, columns }) => {
    const columnHeaders = [];
    columns.forEach((column, columnIndex) => {
      if (column.key === 'delete') {
        columnHeaders.push(
          <div
            style={{ width: columns[columnIndex].width, paddingRight: 3 }}
            key={`header_${columnIndex}`}
          >
            <IconButton
              tooltip={intl.messages['viewSetManager.newViewSet']}
              iconUrl={addIcon}
              onClick={() => addViewSet()}
            />
          </div>,
        );
      } else {
        columnHeaders.push(
          <div key={`header_${columnIndex}`} className="viewsetmanager-header-wrapper">
            {columns[columnIndex].title}
          </div>,
        );
      }
    });

    return columnHeaders;
  };

  const actions = getActions();
  const data = getViewSets();
  const columns = getColumns();

  return (
    <div>
      <Dialog
        title={<FormattedMessage id="viewSetManager.title" />}
        dialogActions={actions}
        onClose={() => {
          closeViewSetManager();
        }}
        open
      >
        <div className="viewsetmanager-headline-wrapper">
          <FormattedMessage id="viewSetManager.editViewSetHeadline" />
        </div>
        <div className="viewsetmanager-table-wrapper">
          <AutoResizer key={`autoresizer`}>
            {({ width, height }) => (
              <Table
                headerRenderer={headerRenderer}
                columns={columns}
                data={data}
                width={width}
                height={height - 20}
                headerHeight={30}
                rowHeight={30}
                key="viewset-table"
              />
            )}
          </AutoResizer>
        </div>
      </Dialog>
      <ConfirmDialog
        open={isImportConfirmOpen}
        onClose={(confirmed) => handleImportConfirmDialogClose(confirmed)}
        title={<FormattedMessage id="viewSetManager.importConfirm.title" />}
        message={<FormattedMessage id="viewSetManager.importConfirm.message" />}
      />
      <ConfirmDialog
        open={viewsetToDelete !== ''}
        onClose={(confirmed) => handleDeleteConfirmDialogClose(confirmed)}
        message={
          <FormattedMessage
            id="viewSetManager.deleteConfirm.message"
            values={{ viewsetName: viewsetToDelete }}
          />
        }
      />
      <InformationDialog
        onClose={() => {
          setIsInformationDialogOpen(false);
          if (shouldCloseViewSetManager) {
            closeViewSetManager();
          }
        }}
        open={isInformationDialogOpen}
        title={informationDialogTitle}
        message={informationDialogMessage}
      />
      <input ref={fileChooser} style={{ display: 'none' }} type="file" onChange={onFileAdded} />
    </div>
  );
};

ViewSetManager.propTypes = {
  onClose: PropTypes.func.isRequired,
  onSave: PropTypes.func.isRequired,
};

export default injectIntl(ViewSetManager);
