import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import './style.scss';
import PresetButton from './components/PresetButton';
import { viewSetType, mosaicViewType } from '../../../ViewSet/types';
import { switchViewSet } from '../../../../redux/actions';
import { canReadViewSet, canEditViewSet } from '../../../../utils/permissions';

const PresetSlider = ({ currentViewSet, createdViewSets, switchViewSet, myPermissions }) =>
  canReadViewSet(myPermissions) || canEditViewSet(myPermissions) ? (
    <div className="preset-slider-wrapper">
      {createdViewSets &&
        createdViewSets.map((viewSet) => (
          <PresetButton
            title={viewSet.name || viewSet.id}
            key={viewSet.id}
            onClick={() => switchViewSet(viewSet)}
            active={currentViewSet && currentViewSet.id === viewSet.id}
          />
        ))}
    </div>
  ) : (
    <div></div>
  );

PresetSlider.defaultProps = {
  currentViewSet: undefined,
  createdViewSets: [],
};

PresetSlider.propTypes = {
  currentViewSet: mosaicViewType,
  createdViewSets: PropTypes.arrayOf(viewSetType),
  switchViewSet: PropTypes.func.isRequired,
};

const mapStateToProps = (state) => ({
  createdViewSets: Object.keys(state.viewSet.createdViewSets).map(
    (key) => state.viewSet.createdViewSets[key],
  ),
  currentViewSet: state.viewSet.currentViewSet,
  myPermissions: state.app.myPermissions,
});

const mapDispatchToProps = (dispatch) => ({
  switchViewSet: (viewSet) => dispatch(switchViewSet(viewSet)),
});

export default connect(mapStateToProps, mapDispatchToProps)(PresetSlider);
