import PropTypes from 'prop-types';
import React, { Component } from 'react';

class OutsideClickHandler extends Component {
  componentDidMount() {
    window.addEventListener('click', this.onClickOutside, true);
  }

  componentWillUnmount() {
    window.removeEventListener('click', this.onClickOutside, true);
  }

  onClickOutside = (e) => {
    if (!this.wrappedNode || !this.wrappedNode.contains(e.target)) {
      this.props.onOutsideClick(e);
    }
  };

  render() {
    return (
      <div
        className="outside-click-handler-wrapper"
        ref={(ref) => {
          this.wrappedNode = ref;
        }}
      >
        {this.props.children}
      </div>
    );
  }
}

OutsideClickHandler.propTypes = {
  children: PropTypes.node.isRequired,
  onOutsideClick: PropTypes.func.isRequired,
};

export default OutsideClickHandler;
