import PropTypes from 'prop-types';
import classNames from 'classnames';
import { connect } from 'react-redux';
import React, { Component } from 'react';
import isEqual from 'lodash/isEqual';

import './style.scss';
import iconOne from '../../assets/icons/one.svg';
import iconDownload from '../../assets/icons/download.svg';
import iconInfo from '../../assets/icons/info.svg';
import iconFitToWidth from '../../assets/icons/fit-to-width.svg';
import iconOverlayList from '../../assets/icons/overlay-list.svg';
import iconFitToHeight from '../../assets/icons/fit-to-height.svg';
import iconDownloadWithOverlays from '../../assets/icons/downloadWithOverlays.svg';

import logger from '../../utils/logger';
import { canReadImageView, canEditImageView } from '../../utils/permissions';
import ImageInfo from './components/ImageInfo';
import ImageEditor from './components/ImageEditor';
import ActionBar from '../../components/ActionBar';
import ActionBarType, { SEPARATOR } from '../../types/ActionBarType';
import OverlayList from '../../components/OverlayList';
import ProxyType from '../../components/Protobuf/ProxyType';
import ObjectSelector from '../../components/ObjectSelector';
import {
  initViewState,
  setViewData,
  setSharedStateData,
  addImageNotAvailableNotification,
  setSaveFlag,
} from '../../redux/actions/index';

class ImageView extends Component {
  state = {
    viewSizeChangeCounter: this.props.viewSizeChangeCounter,
    isOverlayInfoOpen: false,
    isImageInfoOpen: false,
    canvasInitResize: false,
    subscribedSource: undefined,
  };

  componentDidMount() {
    if (this.props.sourceImage) {
      this.unsubscribeFromProductStream();
      this.resubscribeToProductStream(this.props.sourceImage, this.props.overlays);
    }
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    this.updateViewSize(nextProps.viewSizeChangeCounter);

    const oldVariables = this.getUsedVariablesFromOverlay(this.props.overlays);
    const newVariables = this.getUsedVariablesFromOverlay(nextProps.overlays);
    if (
      !isEqual(oldVariables, newVariables) ||
      (this.state.subscribedSource !== nextProps.sourceImage && nextProps.sourceImage)
    ) {
      this.resubscribeToProductStream(nextProps.sourceImage, nextProps.overlays);
    }

    if (this.props.isBrowserWindowVisible !== nextProps.isBrowserWindowVisible) {
      if (nextProps.isBrowserWindowVisible) {
        this.resubscribeToProductStream(this.props.sourceImage, this.props.overlays);
      } else {
        this.unsubscribeFromProductStream();
      }
    }
  }

  componentWillUnmount() {
    this.unsubscribeFromProductStream();
  }

  updateViewSize = (newViewSizeChangeCounter) => {
    if (
      newViewSizeChangeCounter > this.state.viewSizeChangeCounter ||
      this.state.viewSizeChangeCounter === undefined
    ) {
      this.setState({ viewSizeChangeCounter: newViewSizeChangeCounter });
    }
  };

  resubscribeToProductStream = (imageId, overlays) => {
    if (imageId) {
      this.unsubscribeFromProductStream();
      this.subscribeToProductStream(imageId, overlays);
      this.setState({ subscribedSource: imageId });
    }
  };

  subscribeToProductStream = (imageId, overlays) => {
    if (!this.productSub) {
      const { inspectionId } = this.props;
      const startAtTriggerCount = -1; // image view can only show one item at a time
      const imageIds = [imageId];
      const valueIds = this.getUsedVariablesFromOverlay(overlays);
      const desc = { inspectionId, imageIds, valueIds, startAtTriggerCount };
      this.productSub = this.props.proxy.subscribeToProductStream(desc, this.onProduct);
    }
  };

  unsubscribeFromProductStream = () => {
    if (this.productSub) {
      this.props.proxy.unsubscribeFromProductStream(this.productSub);
      this.productSub = null;
    }
  };

  getUsedVariablesFromOverlay = (overlays) => {
    const variables = [];
    overlays &&
      overlays.forEach((overlay) => {
        Object.keys(overlay).forEach((key) => {
          if (key.indexOf('Variable') > -1 && overlay[key] && !variables.includes(overlay[key])) {
            variables.push(overlay[key]);
          }
        });
      });
    return variables;
  };

  setAllOverlaysActive = (isActive) => {
    const updatedOverlays = this.props.overlays.map((overlay) => ({
      ...overlay,
      isActive,
    }));
    this.props.setSharedStateData('overlays', updatedOverlays);
    this.props.setSaveFlag(true);
  };

  createActiveOverlayList = (overlays) => overlays.filter((overlay) => overlay.isActive);

  createActiveOverlayIdList = (overlays) => {
    const activeOverlays = overlays.filter((overlay) => overlay.isActive);
    return activeOverlays.map((overlay) => overlay.id);
  };

  onProduct = ({ product }) => {
    const imageId = this.props.sourceImage;

    this.setState({ imageId, product });
    if (!this.state.canvasInitResize) {
      this.setState({ canvasInitResize: true });
    }
  };

  toggleOverlayInfo = () => {
    this.setState({
      isOverlayInfoOpen: !this.state.isOverlayInfoOpen,
      isImageInfoOpen: false,
    });
  };

  toggleImageInfo = () => {
    this.setState({
      isImageInfoOpen: !this.state.isImageInfoOpen,
      isOverlayInfoOpen: false,
    });
  };

  setSourceImageId = (id) => {
    if (!id) {
      logger.error(`OjectSelector did not return a valid id ${id}.`);
      return;
    }
    this.props.setSharedStateData('sourceImage', id);
  };

  updateOverlayList = (updatedOverlayList) => {
    this.props.setSharedStateData('overlays', updatedOverlayList);
    this.props.setSaveFlag(true);
  };

  setTitleFromConfig = () => {
    const { sourceImage, inspectionId, configuration } = this.props;
    if (configuration) {
      const path = configuration.getValueNameForId(inspectionId, sourceImage);
      if (path) {
        this.props.setViewData('windowName', path);
      }
    }
  };

  renderObjectSelector = () => {
    const { configuration, inspectionId } = this.props;
    if (!configuration || !inspectionId) {
      return;
    }
    return (
      <div style={{ width: '100%', overflow: 'auto' }}>
        <ObjectSelector
          singleSelect
          data={configuration.getObjectSelectorImagesForInspection(inspectionId).tree}
          onChange={(selectedId, checkedLocation) => {
            this.setSourceImageId(selectedId);
            this.props.setViewData('windowName', checkedLocation);
            this.props.setSaveFlag(true);
          }}
        />
      </div>
    );
  };

  renderImageView = () => {
    const { viewActions } = this.props;

    return (
      <div style={{ height: '100%', width: '100%' }}>
        <div style={{ height: '100%', width: '100%' }}>{this.renderImageViewImpl()}</div>
        <ActionBar isViewAction actions={viewActions(this)} />
      </div>
    );
  };

  renderImageViewImpl() {
    const { product } = this.state;
    if (!product || product.released) {
      return <div>&lt;UNAVAILABLE&gt;</div>;
    }

    return [this.renderImageEditor(), this.renderImageInfo(), this.renderOverlayList()];
  }

  renderImageEditor() {
    const { inspectionId, id } = this.props;
    const { product, imageId, isImageInfoOpen } = this.state;

    return (
      <ImageEditor
        ref={(ref) => {
          this.imageEditor = ref;
        }}
        id={id}
        inspectionId={inspectionId}
        imageId={imageId}
        product={product}
        key={`ImageEditor${id}`}
        isImageInfoOpen={isImageInfoOpen}
      />
    );
  }

  renderImageInfo() {
    const { inspectionId, id } = this.props;
    const { isImageInfoOpen } = this.state;

    if (!isImageInfoOpen) {
      return;
    }

    return (
      <ImageInfo
        id={id}
        key={`ImageInfo${id}`}
        inspectionId={inspectionId}
        imageInfoClass={classNames({ 'info-box': true })}
      />
    );
  }

  renderOverlayList() {
    const { overlays, id } = this.props;
    const { isOverlayInfoOpen } = this.state;

    if (!isOverlayInfoOpen) {
      return;
    }

    return (
      <div className={classNames({ 'info-box': true })} key={`OverlayList${id}`}>
        <OverlayList
          overlays={overlays}
          updateOverlayList={this.updateOverlayList}
          canSelectRow={false}
          onSelectAll={(checked) => this.setAllOverlaysActive(checked)}
          hasActionBar={false}
        />
      </div>
    );
  }

  render() {
    const { sourceImage, myPermissions } = this.props;

    this.setTitleFromConfig();

    if (canReadImageView(myPermissions) || canEditImageView(myPermissions)) {
      return (
        <div className="image-view-wrapper">
          {sourceImage ? this.renderImageView() : this.renderObjectSelector()}
        </div>
      );
    }

    return <div></div>;
  }
}

ImageView.defaultProps = {
  viewActions: (imageView) => {
    const actions = [];
    const canEdit = canEditImageView(imageView.props.myPermissions);

    if (canEdit) {
      actions.push({
        intlId: 'imageView.zoomOut',
        iconUrl: iconOne,
        onClickHandler: () => {
          if (imageView.imageEditor) {
            imageView.imageEditor.resetZoom();
          }
        },
      });
      actions.push({
        intlId: 'imageView.fitToWidth',
        iconUrl: iconFitToWidth,
        onClickHandler: () => {
          if (imageView.imageEditor) {
            imageView.imageEditor.fitToWidth();
          }
        },
      });
      actions.push({
        intlId: 'imageView.fitToHeight',
        iconUrl: iconFitToHeight,
        onClickHandler: () => {
          if (imageView.imageEditor) {
            imageView.imageEditor.fitToHeight();
          }
        },
      });
    }

    if (canReadImageView(imageView.props.myPermissions) || canEdit) {
      actions.push({
        type: SEPARATOR,
      });
      actions.push({
        intlId: 'imageView.save',
        iconUrl: iconDownload,
        onClickHandler: () => {
          if (imageView.imageEditor) {
            imageView.imageEditor.downloadOriginalImage();
          }
        },
      });
      actions.push({
        intlId: 'imageView.saveWithOverlays',
        iconUrl: iconDownloadWithOverlays,
        onClickHandler: () => {
          if (imageView.imageEditor) {
            imageView.imageEditor.downloadOriginalImageWithOverlays();
          }
        },
      });
      actions.push({
        type: SEPARATOR,
      });
      actions.push({
        intlId: 'imageView.overlayListToggle',
        iconUrl: iconOverlayList,
        onClickHandler: () => {
          imageView.toggleOverlayInfo();
        },
      });
      actions.push({
        intlId: 'imageView.imageInfoToggle',
        iconUrl: iconInfo,
        onClickHandler: () => {
          imageView.toggleImageInfo();
        },
      });
    }

    return actions;
  },
  overlayActions: () => [],
  inRunMode: false,
  currentMode: undefined,
  proxy: undefined,
  rgbValues: { R: 0, G: 0, B: 0 },
  cursorPosition: { width: 0, height: 0 },
};

ImageView.propTypes = {
  viewActions: ActionBarType,
  overlayActions: ActionBarType,
  currentMode: PropTypes.string,
  viewSizeChangeCounter: PropTypes.number,
  overlays: PropTypes.array, //eslint-disable-line
  proxy: ProxyType,
  sourceImage: PropTypes.number,
};

const initState = {
  scaleFactor: 0,
  rgbValues: { R: 0, G: 0, B: 0 },
  imageDimensions: { width: 0, height: 0 },
  cursorPosition: { x: 0, y: 0 },
};

const initSharedState = {
  overlays: [],
  sourceImage: 0,
};

const mapDispatchToProps = (dispatch, ownProps) => {
  const { inspectionId, id: viewId } = ownProps;
  dispatch(initViewState(inspectionId, viewId, initState, initSharedState));
  return {
    setViewData: (key, data) => dispatch(setViewData(inspectionId, viewId, key, data)),
    setSharedStateData: (key, data) =>
      dispatch(setSharedStateData(inspectionId, key, data, viewId)),
    addImageNotAvailableNotification: () => dispatch(addImageNotAvailableNotification()),
    setSaveFlag: (hasChanges) => dispatch(setSaveFlag(hasChanges)),
  };
};

const mapStateToProps = (state, ownProps) => {
  const { inspectionId, id: viewId } = ownProps;
  const result = {
    proxy: state.app.proxy,
    configuration: state.app.configuration,
    currentMode: state.app.currentMode,
    viewSizeChangeCounter: state.viewSet.viewSizeChangeCounter,
    myPermissions: state.app.myPermissions,
    isBrowserWindowVisible: state.app.isBrowserWindowVisible,
  };

  try {
    return {
      ...result,
      overlays: state.inspections[inspectionId].sharedState.views[viewId].overlays,
      sourceImage: Number(state.inspections[inspectionId].sharedState.views[viewId].sourceImage),
    };
  } catch (error) {
    return result;
  }
};
export default connect(mapStateToProps, mapDispatchToProps)(ImageView);
