import PropTypes from 'prop-types';

const actionList = PropTypes.arrayOf(
  PropTypes.shape({ intlId: PropTypes.string, iconUrl: PropTypes.string }),
);
const ActionBarType = PropTypes.oneOfType([actionList, PropTypes.func]);

export const TOGGLE_BUTTON = 'TOGGLE_BUTTON';
export const SEPARATOR = 'SEPARATOR';

export default ActionBarType;
