import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import React, { Component } from 'react';
import { IntlProvider } from 'react-intl';
import { Redirect } from 'react-router-dom';
import throttle from 'lodash/throttle';
import { ReactReduxContext } from 'react-redux';

import ViewSet from '../views/ViewSet';
import HelpView from '../views/HelpView';
import translations from '../translations';
import TopNavigation from '../views/TopNavigation';
import BottomNavigation from '../views/BottomNavigation';
import ProtobufType from '../components/Protobuf/ProtobufType';
import ModalNotification from '../components/ModalNotification.js';
import ViewSetFactory, { DEFAULT_VIEWSET } from '../views/ViewSetFactory';
import {
  setViewSet,
  setCurrentLayout,
  addConnectionEstablishedNotification,
  addConnectionLostNotification,
  saveClientChunk,
  closeHelpView,
  increaseViewSizeChangeCounter,
  changeViewSetAfterLoading,
} from '../redux/actions';

class SVObserver extends Component {
  static contextType = ReactReduxContext;
  state = {
    viewSetFactory: new ViewSetFactory(),
    throttledWindwResizeHandler: throttle(() => this.props.increaseChangeCounter(), 500),
  };

  handleBeforeUnload(event) {
    event.preventDefault();
    event.returnValue = '';
  }

  componentDidMount() {
    if (!this.props.currentViewSet) {
      const viewSetId = DEFAULT_VIEWSET;
      const viewSet = this.state.viewSetFactory.getViewSet(viewSetId);
      this.props.setViewSet(viewSet, viewSet.id);
    }

    if (this.props.defaultViewSetName) {
      this.props.changeViewSetAfterLoading(this.props.defaultViewSetName);
    }

    if (this.props.protobuf && this.props.socket) {
      this.onWebsocketConnect(this.props.protobuf);
    }

    window.addEventListener('resize', this.state.throttledWindwResizeHandler);
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.state.throttledWindwResizeHandler);
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    if (nextProps.socket !== this.props.socket && nextProps.protobuf && nextProps.socket) {
      this.onWebsocketConnect(nextProps.protobuf);
    }
    if (nextProps.socket !== this.props.socket && !nextProps.socket) {
      this.onWebsocketDisconnect();
    }
  }

  componentDidUpdate(prevProps) {
    const { unsavedChanges } = this.props;
    if (unsavedChanges && prevProps.currentViewSet) {
      window.addEventListener('beforeunload', this.handleBeforeUnload);
    } else {
      window.removeEventListener('beforeunload', this.handleBeforeUnload);
    }
  }

  onWebsocketConnect = async (protobuf) => {
    this.props.addConnectionEstablishedNotification();
  };

  onWebsocketDisconnect = () => {
    this.props.addConnectionLostNotification();
  };

  render() {
    const {
      locale,
      currentViewSet,
      bottomNavHidden,
      topNavHidden,
      protobuf,
      modalNotificationData,
      isHelpViewOpen,
      closeHelpView,
    } = this.props;

    if (!this.props.user || !this.props.authToken) {
      return <Redirect to="/" />;
    }

    return (
      <IntlProvider locale={locale} messages={translations[locale]}>
        <div className="vh-100 center">
          <ModalNotification
            visible={!!modalNotificationData}
            message={modalNotificationData && modalNotificationData.message}
          />
          <TopNavigation onLoggedOut={this.props.onLoggedOut} />
          <ViewSet
            views={currentViewSet}
            onChange={(layout) => {
              this.props.setLayout(layout);
            }}
            bottomNavHidden={bottomNavHidden}
            topNavHidden={topNavHidden}
          />
          <BottomNavigation
            onSave={() => {
              const currentState = this.context.store.getState();
              this.props.saveClientChunk(protobuf, currentState);
            }}
          />
          <HelpView open={isHelpViewOpen} onClose={() => closeHelpView()}></HelpView>
        </div>
      </IntlProvider>
    );
  }
}

SVObserver.defaultProps = {
  bottomNavHidden: false,
  topNavHidden: false,
  protobuf: undefined,
  viewSetTemplate: undefined,
  user: undefined,
  authToken: undefined,
  location: { state: {} },
  currentViewSet: undefined,
};

SVObserver.propTypes = {
  locale: PropTypes.string.isRequired,
  user: PropTypes.shape({ name: PropTypes.string }),
  authToken: PropTypes.string,
  currentViewSet: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.shape({ id: PropTypes.string }),
  ]),
  bottomNavHidden: PropTypes.bool,
  topNavHidden: PropTypes.bool,
  setLayout: PropTypes.func.isRequired,
  setViewSet: PropTypes.func.isRequired,
  viewSetTemplate: PropTypes.string,
  addConnectionEstablishedNotification: PropTypes.func.isRequired,
  addConnectionLostNotification: PropTypes.func.isRequired,
  protobuf: ProtobufType,
  location: PropTypes.shape({ state: PropTypes.any }),
};

const mapStateToProps = (state) => ({
  isHelpViewOpen: state.app.isHelpViewOpen,
  locale: state.app.locale,
  protobuf: state.app.protobuf,
  socket: state.app.socket,
  user: state.app.user,
  authToken: state.app.authToken,
  currentViewSet: state.viewSet.currentViewSet,
  bottomNavHidden: state.viewSet.bottomNavHidden,
  topNavHidden: state.viewSet.topNavHidden,
  defaultViewSetName: state.viewSet.defaultViewSetName,
  createdViewSets: state.app.createdViewSets,
  modalNotificationData: state.app.modalNotificationData,
  increaseChangeCounter: PropTypes.func.isRequired,
  unsavedChanges: state.viewSet.unsavedChanges,
});

const mapDispatchToProps = (dispatch, ownProps) => ({
  closeHelpView: () => {
    dispatch(closeHelpView());
  },
  setViewSet: (viewSet, viewSetId) => {
    dispatch(setViewSet(viewSet, viewSetId));
  },
  setLayout: (layout) => {
    dispatch(setCurrentLayout(layout));
  },
  addConnectionEstablishedNotification: () => {
    dispatch(addConnectionEstablishedNotification());
  },
  addConnectionLostNotification: () => {
    dispatch(addConnectionLostNotification());
  },
  saveClientChunk: (protobuf, chunk) => {
    dispatch(saveClientChunk(protobuf, chunk));
  },
  increaseChangeCounter: () => {
    dispatch(increaseViewSizeChangeCounter());
  },
  changeViewSetAfterLoading: (name) => {
    dispatch(changeViewSetAfterLoading(name));
  },
});

export default connect(mapStateToProps, mapDispatchToProps)(SVObserver);
