import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import React, { Component } from 'react';

import Filter from '../../ParameterView/components/Filter';
import ViewSetFactory from '../../ViewSetFactory';
import { setParameter, setViewSet } from '../../../redux/actions';

class ToolSetup extends Component {
  componentDidUpdate(prevProps) {
    if (prevProps.node !== this.props.node) {
      this.setup(this.props.node);
    }
  }

  setup = (node) => {
    switch (node.name) {
      case 'Equation':
        this.props.setupParameterView({
          component: <Filter />,
        });

        break;
      default:
        break;
    }
  };

  viewSetFactory = new ViewSetFactory();

  render() {
    return null;
  }
}

ToolSetup.defaultProps = {
  node: undefined,
};

ToolSetup.propTypes = {
  currentViewSet: PropTypes.shape({
    id: PropTypes.string,
    viewSet: PropTypes.shape({ id: PropTypes.string }),
  }).isRequired,
  setupParameterView: PropTypes.func.isRequired,
  setViewSet: PropTypes.func.isRequired,
  node: PropTypes.shape({
    uniqueReferenceID: PropTypes.string,
  }),
};

const mapStateToProps = (state) => ({
  currentViewSet: state.viewSet.currentViewSet,
});

const mapDispatchToProps = (dispatch) => ({
  setupParameterView: (parameter) => {
    dispatch(setParameter(parameter));
  },
  setViewSet: (viewSet) => {
    dispatch(setViewSet(viewSet));
  },
});

export default connect(mapStateToProps, mapDispatchToProps)(ToolSetup);
