import PropTypes from 'prop-types';
import classNames from 'classnames';
import { connect } from 'react-redux';
import React, { Component } from 'react';
import { FormattedMessage, injectIntl } from 'react-intl';

import './style.scss';
import modes from '../../utils/modes';
import UserInfo from './components/UserInfo';
import DateTime from './components/DateTime';
import ViewSetFactory from '../ViewSetFactory';
import Logo from '../../components/layout/Logo';
import KoerberLogo from '../../assets/logo/logo.svg';
import ModeSwitch from './components/ModeSwitch';
import StatusReport from './components/StatusReport';
import { InspectionStateSwitch } from './components/InspectionStateSwitch';
import LanguageChoooser from './components/LanguageChooser';
import {
  setTopNavHidden,
  setViewSetForMode,
  setViewSet,
  setCurrentMode,
  setPausedInspections,
} from '../../redux/actions';
const viewSetFactory = new ViewSetFactory();

class TopNavigation extends Component {
  constructor(props) {
    super(props);
    this.state = {
      value: 0,
      open: false,
    };

    this.handleChange = this.handleChange.bind(this);
    this.handleRequestClose = this.handleRequestClose.bind(this);
  }

  handleChange = (event, index, value) => this.setState({ value });
  openModePopover = (event) => {
    event.preventDefault();

    this.setState({
      anchorEl: event.currentTarget,
      open: true,
    });
  };

  handleRequestClose = () => {
    this.setState({
      open: false,
    });
  };

  changeMode = (modeKey, modeId) => {
    this.props.setCurrentMode(modeId);
    this.handleRequestClose();
  };

  mapModeIdToKey(modeId) {
    return Object.keys(modes).find((modeKey) => modes[modeKey].id === modeId);
  }

  getConfigName = () => {
    const { configuration, isSVOConfigLoaded } = this.props;
    if (!isSVOConfigLoaded) {
      return <FormattedMessage id="noConfigName" />;
    }
    if (!configuration || !configuration.name) {
      return <FormattedMessage id="unknownConfigName" />;
    }
    return <span>{configuration.name}</span>;
  };

  render() {
    const { topNavHidden, isSVOConfigLoaded } = this.props;
    const currentMode = this.props.currentMode || modes.unknownMode.id;
    const currentModeKey = this.mapModeIdToKey(currentMode);
    const currentModeFormatted = <FormattedMessage id={currentModeKey} />;
    const configName = this.getConfigName();
    const navClass = classNames({
      nav: true,
      'nav-hidden': topNavHidden,
    });
    return (
      <div className={navClass}>
        <button
          className="hider"
          onClick={() => {
            this.props.setTopNavHidden(!topNavHidden);
          }}
        >
          {topNavHidden ? 'show' : 'hide'}
        </button>
        <div className="flex-container">
          <div className="config-wrapper">
            <Logo src={KoerberLogo} title="logo" />
            {this.props.unsavedChanges ? (
              <div style={{ fontStyle: 'italic' }}>{configName}*</div>
            ) : (
              configName
            )}
          </div>
          <ModeSwitch
            changeMode={this.changeMode}
            open={this.state.open}
            anchorEl={this.state.anchorEl}
            handleRequestClose={this.handleRequestClose}
            openModePopover={this.openModePopover}
            currentMode={currentModeFormatted}
            enabled={isSVOConfigLoaded}
          />
          <StatusReport />
          {isSVOConfigLoaded && <InspectionStateSwitch />}
        </div>
        <div className="flex-container">
          <UserInfo onChangeUser={() => {}} onLoggedOut={this.props.onLoggedOut} />
          <LanguageChoooser />
          <div className="separator">
            <DateTime date={new Date()} />
          </div>
        </div>
      </div>
    );
  }
}

TopNavigation.defaultProps = {
  fullscreenMode: false,
  currentMode: undefined,
};

TopNavigation.propTypes = {
  changeViewSet: PropTypes.func.isRequired,
  setViewSetForMode: PropTypes.func.isRequired,
  setTopNavHidden: PropTypes.func.isRequired,
  setCurrentMode: PropTypes.func.isRequired,
  setPausedInspections: PropTypes.func.isRequired,
  saveOperationInProcess: PropTypes.bool.isRequired,
  currentMode: PropTypes.string,
  topNavHidden: PropTypes.bool.isRequired,
};

const mapDispatchToProps = (dispatch) => ({
  setTopNavHidden: (hidden) => dispatch(setTopNavHidden(hidden)),
  setViewSetForMode: (mode) => dispatch(setViewSetForMode(mode)),
  changeViewSet: (viewSetId) => dispatch(setViewSet(viewSetFactory.getViewSet(viewSetId))),
  setCurrentMode: (modeId) => dispatch(setCurrentMode(modeId)),
  setPausedInspections: (pausedInspections) => dispatch(setPausedInspections(pausedInspections)),
});

const mapStateToProps = (state) => ({
  activeAppState: state.app.activeAppState,
  appStateConnection: state.app.appStateConnection,
  appMode: state.app.mode,
  currentMode: state.app.currentMode,
  fullscreenMode: state.viewSet.fullscreenMode,
  saveOperationInProcess: state.app.saveOperationInProcess,
  configuration: state.app.configuration,
  topNavHidden: state.viewSet.topNavHidden,
  proxy: state.app.proxy,
  pausedInspections: state.app.pausedInspections,
  isSVOConfigLoaded: state.viewSet.isSVOConfigLoaded,
  unsavedChanges: state.viewSet.unsavedChanges,
});

export default injectIntl(connect(mapStateToProps, mapDispatchToProps)(TopNavigation));
