import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { FormattedMessage, injectIntl } from 'react-intl';

import Dialog from '../../components/Dialog';
import Logo from '../../components/layout/Logo';
import KoerberLogo from '../../assets/logo/logo.svg';

import './style.scss';

class AboutView extends Component {
  render() {
    const { onClose, open, intl, configuration } = this.props;

    if (!configuration) {
      return <div />;
    }

    const svObserverVersion = configuration.getSvObserverVersion();
    const gatewayVersion = configuration.getGatewayVersion();
    const svwepAppVersion = configuration.getWebAppVersion();

    const { name } = require('../../../package.json');

    return (
      <Dialog title={<FormattedMessage id="aboutView" />} onClose={onClose} open={open}>
        <div className="appname">{name}</div>
        <div className="table">
          <div className="row">
            <div className="cell">{intl.messages['aboutView.applicationVersion']}</div>
            <div className="cell">{svwepAppVersion}</div>
          </div>
          <div className="row">
            <div className="cell">{intl.messages['aboutView.svoVersion']}</div>
            <div className="cell">{svObserverVersion}</div>
          </div>
          <div className="row">
            <div className="cell">{intl.messages['aboutView.gatewayVersion']}</div>
            <div className="cell">{gatewayVersion}</div>
          </div>
        </div>
        <div className="logo">
          <Logo src={KoerberLogo} title="logo" />
        </div>
      </Dialog>
    );
  }
}

AboutView.defaultProps = {
  open: false,
};

AboutView.propTypes = {
  open: PropTypes.bool,
  onClose: PropTypes.func.isRequired,
};

const mapStateToProps = (state, ownProps) => {
  try {
    const { configuration } = state.app;
    return { configuration };
  } catch (error) {
    return {};
  }
};

export default connect(mapStateToProps)(injectIntl(AboutView));
