import React from 'react';
import shortId from 'shortid';
import PropTypes from 'prop-types';
import { injectIntl } from 'react-intl';

import './style.scss';
import IconButton from './../buttons/IconButton';
import { ToggleButton } from '../buttons/ToggleButton';
import { TOGGLE_BUTTON, SEPARATOR } from '../../types/ActionBarType';

const ActionBar = ({ actions, intl, isViewAction }) => (
  <div className="action-bar-wrapper" style={{ position: isViewAction ? 'absolute' : 'relative' }}>
    {actions &&
      actions.map((action) => {
        if (action && action.type === SEPARATOR) {
          return (
            <div className="action-bar-separator" key={`separator_${shortId.generate()}`}>
              <div className="separator"></div>
            </div>
          );
        }
        if (action && action.type === TOGGLE_BUTTON) {
          return (
            <ToggleButton
              key={action.id}
              id={action.id}
              intl={intl}
              intlId={action.intlId}
              iconUrl={action.iconUrl}
              onClickHandler={action.onClickHandler}
              intlIdAlternative={action.intlIdAlternative}
              iconUrlAlternative={action.iconUrlAlternative}
              onClickHandlerAlternative={action.onClickHandlerAlternative}
              showAlternative={action.showAlternative}
            />
          );
        }

        return (
          <IconButton
            key={action.intlId}
            tooltip={intl.messages[`${action.intlId}`]}
            onClick={() => {
              action.onClickHandler();
            }}
            iconUrl={action.iconUrl}
          />
        );
      })}
  </div>
);

ActionBar.defaultProps = {
  isViewAction: false,
};

ActionBar.propTypes = {
  actions: PropTypes.arrayOf(
    PropTypes.shape({
      intlId: PropTypes.string,
      onClickHandler: PropTypes.func,
      iconUrl: PropTypes.string,
    }),
  ).isRequired,
  isViewAction: PropTypes.bool,
};

export default injectIntl(ActionBar);
