import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import React, { Component } from 'react';

import './styles.scss';
import FilmStrip from './components/FilmStrip';
import iconPlay from '../../assets/icons/play.svg';
import iconPause from '../../assets/icons/pause.svg';

import { TOGGLE_BUTTON } from '../../types/ActionBarType';
import ActionBar from '../../components/ActionBar';
import Container from '../../components/layout/Container';
import ProxyType from '../../components/Protobuf/ProxyType';
import ObjectSelector from '../../components/ObjectSelector';
import { drawInspectionOverlays, extractInspectionOverlays } from '../../utils/OverlayRenderer';
import { initViewState, setViewData, setSharedStateData, setSaveFlag } from '../../redux/actions';
import logger from '../../utils/logger';
import {
  canReadImageStripView,
  canEditImageStripView,
  canEditInspectionState,
} from '../../utils/permissions';

class ImageStripView extends Component {
  constructor(props) {
    super(props);
    this.state = {
      images: [],
      data: null,
    };
    this.productSub = null;
    this.imageStripLength = 10;
  }

  componentDidMount() {
    if (this.props.sourceImage) {
      this.unsubscribeFromProductStream();
      this.subscribeToProductStream(this.props.sourceImage);
    }
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    if (nextProps.sourceImage && nextProps.sourceImage !== this.props.sourceImage) {
      this.unsubscribeFromProductStream();
      this.subscribeToProductStream(nextProps.sourceImage);
    }

    if (
      this.props.isBrowserWindowVisible !== undefined &&
      this.props.isBrowserWindowVisible !== nextProps.isBrowserWindowVisible
    ) {
      if (nextProps.isBrowserWindowVisible) {
        this.subscribeToProductStream(nextProps.sourceImage);
      } else {
        this.unsubscribeFromProductStream();
      }
    }
  }

  componentDidUpdate() {}

  componentWillUnmount() {
    const { images } = this.state;
    const products = (images || []).map((e) => e.product);
    this.releaseProducts(products);
    // TODO resume stream when paused?
    this.unsubscribeFromProductStream();
  }

  subscribeToProductStream(sourceImage) {
    if (!this.productSub) {
      const { inspectionId } = this.props;
      const desc = {
        inspectionId,
        startAtTriggerCount: -1 * this.imageStripLength,
        rejectImageIds: [sourceImage],
        allowOldTrigger: true,
        manualRelease: true,
      };
      this.productSub = this.props.proxy.subscribeToProductStream(desc, this.onProduct);
    }
  }

  unsubscribeFromProductStream() {
    if (this.productSub) {
      this.props.proxy.unsubscribeFromProductStream(this.productSub);
      this.productSub = null;
    }

    (this.state.images || []).forEach((image) => {
      image.product?.release();
    });
    this.setState({ images: [] });
  }

  onProduct = ({ product }) => {
    const entry = this.state.images.find((e) => e.product === product);
    if (entry) {
      // we can free this reference. the second reference to the
      // same product will be released in this.releaseProducts().
      product.release();
      return;
    }

    // TODO: Image dimensions won't change for a subscription
    // better to update `onSubscribed`
    this.updateImageDimensions(product);

    const combinedSrc = this.drawImageAndOverlaysToCanvas({
      drawTarget: this.canvas,
      product,
    });
    const images = [{ combinedSrc, product }, ...this.state.images];

    // sort descending by trigger count. can happen at beginning of stream.
    images.sort((a, b) => b.product.trigger - a.product.trigger);

    let productsToRelease = [];
    if (images.length > this.imageStripLength) {
      productsToRelease = images.splice(this.imageStripLength).map((e) => e.product);
    }

    this.setState({ images });

    this.releaseProducts(productsToRelease);
  };

  releaseProducts(products) {
    (products || []).forEach((product) => product.release());
  }

  updateImageDimensions = (product) => {
    if (!product) {
      logger.error(`updateImageDimensions requires product but did not receive.`);
      return;
    }

    if (!product.isReject) {
      logger.error(`updateImageDimensions requires product that is a reject.`);
      return;
    }

    const { sourceImage } = this.props;
    const { image } = product.getImage(sourceImage);

    if (!image) {
      logger.error(`Product does not contain an Image.`);
      return;
    }

    const { height, width } = image;

    if (
      !this.state.imageWidth ||
      !this.state.imageHeigt ||
      this.state.imageWidth !== width ||
      this.state.imageHeigt !== height
    ) {
      this.setState({ imageHeigt: height, imageWidth: width });
    }
  };

  setSourceImageIndex = (id) => {
    if (!id) {
      logger.error(`OjectSelector did not return a valid id.`);
      return;
    }
    this.props.setSharedStateData('sourceImage', id);
  };

  drawImageAndOverlaysToCanvas = ({ drawTarget, product }) => {
    if (!drawTarget || !product) {
      logger.error(
        'drawImageAndOverlaysToCanvas expects drawTarget and product but did not receive.',
      );
      return;
    }

    const imageId = this.props.sourceImage;
    const image = product.getImage(imageId);
    if (!image || !image.domImage) {
      logger.warn(`ImageStrip expected an image onReject but didn't receive`);
      return;
    }

    const drawTargetContext = drawTarget.getContext('2d');
    drawTargetContext.clearRect(0, 0, drawTarget.width, drawTarget.height);
    drawTargetContext.drawImage(image.domImage, 0, 0);

    const overlays = extractInspectionOverlays(product, imageId);
    const boundingCoordinates = {
      minX: 0,
      minY: 0,
      maxX: image.width || 0,
      maxY: image.height || 0,
    };

    drawInspectionOverlays({
      drawTarget,
      overlays,
      zoomFactor: 1,
      boundingCoordinates,
      width: image.width,
      height: image.height,
    });
    return this.canvas.toDataURL('image/bmp');
  };

  renderObjectSelector = () => {
    const { configuration, inspectionId } = this.props;
    if (!configuration || !inspectionId) {
      return;
    }

    const images = configuration.getObjectSelectorImagesForInspection(inspectionId);
    const data = images ? images.tree : null;

    return (
      <div style={{ width: '100%', overflow: 'auto' }}>
        <ObjectSelector
          singleSelect
          data={data}
          onChange={(selectedIid, checkedLocation) => {
            this.setSourceImageIndex(selectedIid);
            this.props.setViewData('windowName', checkedLocation);
            this.props.setSaveFlag(true);
          }}
        />
      </div>
    );
  };

  selectProduct(product) {
    this.pauseStream();
    this.props.proxy.replayProduct(this.props.inspectionId, product);
    // TODO some highlights?
  }

  resumeStream() {
    this.props.proxy.resumeRejectStream(this.props.inspectionId);
    this.props.setSaveFlag(true);
  }

  pauseStream() {
    this.props.proxy.pauseRejectStream(this.props.inspectionId);
    this.props.setSaveFlag(true);
  }

  setTitleFromConfig = () => {
    const { sourceImage, inspectionId, configuration } = this.props;
    if (configuration) {
      const path = configuration.getValueNameForId(inspectionId, sourceImage);
      if (path) {
        this.props.setViewData('windowName', path);
      }
    }
  };

  showPause = () => {
    const { pausedInspections, inspectionId } = this.props;
    let showPause = false;
    if (inspectionId) {
      if (!pausedInspections.includes(inspectionId)) {
        showPause = true;
      }
    }
    return showPause;
  };

  renderFilmStrip = () => {
    const { inspectionId, myPermissions } = this.props;
    const { imageWidth, imageHeigt } = this.state;
    const actions = [];

    if (canEditInspectionState(myPermissions)) {
      actions.push({
        type: TOGGLE_BUTTON,
        id: inspectionId,
        intlId: 'imageStripView.play',
        iconUrl: iconPlay,
        onClickHandler: () => {
          this.resumeStream();
        },
        intlIdAlternative: 'imageStripView.pause',
        iconUrlAlternative: iconPause,
        onClickHandlerAlternative: () => {
          this.pauseStream();
        },
        showAlternative: this.showPause(),
      });
    }
    return (
      <div className="reject-view-container">
        <Container>
          <canvas
            style={{ display: 'none' }}
            width={imageWidth}
            height={imageHeigt}
            className="imageCanvas"
            ref={(canvas) => {
              this.canvas = canvas;
            }}
          />
          {this.state.images && this.state.images.length > 0 && (
            <FilmStrip
              onImageClick={(product) => this.selectProduct(product)}
              images={this.state.images}
            />
          )}
          <ActionBar isViewAction actions={actions} />
        </Container>
      </div>
    );
  };

  render() {
    const { sourceImage, myPermissions } = this.props;

    this.setTitleFromConfig();
    const canEdit = canEditImageStripView(myPermissions);

    if (canReadImageStripView(myPermissions) || canEdit) {
      return (
        <div className="reject-view-container">
          {!sourceImage && canEdit ? this.renderObjectSelector() : this.renderFilmStrip()}
        </div>
      );
    }

    return <div></div>;
  }
}

ImageStripView.defaultProps = {
  imageData: [],
  proxy: undefined,
  configuration: null,
};

ImageStripView.propTypes = {
  proxy: ProxyType,
  configuration: PropTypes.object,
  sourceImage: PropTypes.number,
};

const mapStateToProps = (state, ownProps) => {
  const { inspectionId, id: viewId } = ownProps;
  try {
    return {
      proxy: state.app.proxy,
      configuration: state.app.configuration,
      sourceImage: Number(state.inspections[inspectionId].sharedState.views[viewId].sourceImage),
      pausedInspections: state.app.pausedInspections,
      myPermissions: state.app.myPermissions,
      isBrowserWindowVisible: state.app.isBrowserWindowVisible,
    };
  } catch (error) {
    return {};
  }
};

const initState = {};

const initSharedState = {
  sourceImage: 0,
};

const mapDispatchToProps = (dispatch, ownProps) => {
  const { inspectionId, id: viewId } = ownProps;
  dispatch(initViewState(inspectionId, viewId, initState, initSharedState));
  return {
    setViewData: (key, data) => dispatch(setViewData(inspectionId, viewId, key, data)),
    setSharedStateData: (key, data) =>
      dispatch(setSharedStateData(inspectionId, key, data, viewId)),
    setSaveFlag: (hasChanges) => dispatch(setSaveFlag(hasChanges)),
  };
};

const mappedConnect = connect(mapStateToProps, mapDispatchToProps);

export default mappedConnect(ImageStripView);
