import React from 'react';

import ArrayParameter from '../../generic/ArrayParameter';
import IntegerParameter from '../IntegerParameter';

const IntegerArrayParameter = ({ value, changeCallback, typeName }) => {
  return (
    <ArrayParameter
      value={value}
      changeCallback={changeCallback}
      typeName={typeName}
      defaultValue={0}
      SingleParameterComponent={IntegerParameter}
    />
  );
};

export default IntegerArrayParameter;
