import React, { useEffect, useState } from 'react';
import { FormattedMessage } from 'react-intl';
import { TextField } from '@material-ui/core';

import './style.scss';

const RequestsList = ({ proto, chooseCallback }) => {
  const [filter, setFilter] = useState('');
  const [active, setActive] = useState(null);

  const requests = Object.keys(proto)
    .sort()
    .reduce((requests, key) => {
      if (!key.toLowerCase().includes(filter)) return requests;

      return [
        ...requests,
        <li
          key={`li_${key}`}
          requestname={key}
          className={active === key ? 'active' : ''}
          onClick={() => {
            chooseCallback(key);
            setActive(key);
          }}
        >
          {key}
        </li>,
      ];
    }, []);

  useEffect(() => {
    if (!active) {
      chooseCallback(requests[0].props.requestname);
      setActive(requests[0].props.requestname);
    }
  }, [active, chooseCallback, requests]);

  return (
    <div className="requests-list">
      <div className="request-list-header">
        <FormattedMessage id="debugger.requestsListHeader" />
        <small>Choose one from the list below</small>
      </div>
      <div className="requests-filter">
        <TextField
          fullWidth
          type="text"
          variant="outlined"
          value={filter}
          onChange={(e) => setFilter(e.target.value.toLowerCase())}
          placeholder="Search..."
        />
      </div>
      <div className="requests">
        <ul>{requests}</ul>
      </div>
    </div>
  );
};

export default RequestsList;
