import React from 'react';
import SelectParameter from '../../generic/SelectParameter';

const BoolParameter = ({ value, changeCallback, removeCallback }) => {
  return (
    <SelectParameter
      value={value && value !== 'false' ? 'true' : 'false'}
      options={['true', 'false']}
      changeCallback={(value) => changeCallback(value === 'true')}
    />
  );
};

export default BoolParameter;
