import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { injectIntl, FormattedMessage } from 'react-intl';
import React, { Component } from 'react';

import './style.scss';
import ViewSetManagerIcon from '../../assets/icons/view-manager.svg';
import PresetSlider from './components/PresetSlider';
import ViewSetManager from '../ViewSetManager';
import { setBottomNavHidden } from '../../redux/actions';
import { canReadViewSet, canEditViewSet } from '../../utils/permissions';

class BottomNav extends Component {
  state = {
    selectedIndex: 0,
    viewSetManagerOpen: false,
  };
  select = (index) => this.setState({ selectedIndex: index });

  render() {
    const { onSave, bottomNavHidden, isSVOConfigLoaded, myPermissions } = this.props;
    return (
      <div className={`bottom-nav-wrapper ${bottomNavHidden ? 'bottom-nav-hidden' : ''}`}>
        <div className="bottom-nav">
          {isSVOConfigLoaded && <PresetSlider />}
          {isSVOConfigLoaded && (canReadViewSet(myPermissions) || canEditViewSet(myPermissions)) ? (
            <div
              className="view-manager"
              onClick={() => {
                this.setState({
                  viewSetManagerOpen: !this.state.viewSetManagerOpen,
                });
              }}
            >
              <img src={ViewSetManagerIcon} alt="" />
              <FormattedMessage id="viewSetManager" />
            </div>
          ) : undefined}
        </div>
        <button
          className="bottom-hider"
          onClick={() => {
            this.props.setBottomNavHidden(!bottomNavHidden);
          }}
        >
          {bottomNavHidden ? 'show' : 'hide'}
        </button>
        {isSVOConfigLoaded && (
          <div>
            {this.state.viewSetManagerOpen && (
              <ViewSetManager
                onClose={() => this.setState({ viewSetManagerOpen: false })}
                onSave={onSave}
              />
            )}
          </div>
        )}
      </div>
    );
  }
}

BottomNav.propTypes = {
  setBottomNavHidden: PropTypes.func.isRequired,
  onSave: PropTypes.func.isRequired,
  bottomNavHidden: PropTypes.bool.isRequired,
};

const mapStateToProps = (state) => ({
  fullscreenMode: state.viewSet.fullscreenMode,
  bottomNavHidden: state.viewSet.bottomNavHidden,
  isSVOConfigLoaded: state.viewSet.isSVOConfigLoaded,
  myPermissions: state.app.myPermissions,
});

const mapDispatchToProps = (dispatch) => ({
  setBottomNavHidden: (hidden) => dispatch(setBottomNavHidden(hidden)),
});

export default injectIntl(connect(mapStateToProps, mapDispatchToProps)(BottomNav));
