import PropTypes from 'prop-types';
import React, { Component } from 'react';
import { Redirect } from 'react-router';
import { connect } from 'react-redux';

import {
  setLocale,
  authenticate,
  setTopNavHidden,
  setBottomNavHidden,
  setDefaultViewSetName,
} from '../redux/actions';
import logger from '../utils/logger';

const parseQueryParams = (search) => {
  const queryParams = {};
  try {
    search
      .substr(1)
      .split('&')
      .forEach((item) => {
        const part = item.split('=');
        const key = part[0];
        const value = part[1] && decodeURIComponent(part[1]);
        queryParams[key] = value;
      });
  } catch (error) {
    logger.error(error);
  }
  return queryParams;
};

class HmiLoginPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      authenticated: false,
      queryParams: parseQueryParams(this.props.location.search),
    };
  }

  componentDidMount() {
    this.props.setTopNavHidden(this.state.queryParams.hideTopNavigation === 'true');
    this.props.setBottomNavHidden(this.state.queryParams.hideBottomNavigation === 'true');

    this.props.setLocale(this.state.queryParams.locale || 'en');

    this.props.setDefaultViewSetName(this.state.queryParams.viewSet || null);

    if (this.state.queryParams.token) {
      this.props
        .authenticate(this.state.queryParams.token)
        .then(() => this.setState({ authenticated: true }))
        .catch((authError) => this.setState({ authError }));
    }
  }

  render() {
    if (!this.state.queryParams.token) {
      return <div>Malformed query params</div>;
    }
    if (this.state.authError) {
      return (
        <div>
          Invalid token provided. Error: {this.state.authError.response?.status} Status:{' '}
          {this.state.authError.response?.statusText}
        </div>
      );
    }
    if (!this.state.authenticated) {
      return <div>Fetching auth token for provided access token</div>;
    }

    return (
      <Redirect
        to={{
          pathname: '/app',
          state: { viewSetTemplate: this.state.queryParams.viewSet },
        }}
      />
    );
  }
}

HmiLoginPage.propTypes = {
  location: PropTypes.shape({
    search: PropTypes.string,
  }).isRequired,
  setLocale: PropTypes.func.isRequired,
  authenticate: PropTypes.func.isRequired,
  setTopNavHidden: PropTypes.func.isRequired,
  setBottomNavHidden: PropTypes.func.isRequired,
};

const mapDispatchToProps = (dispatch) => ({
  setLocale: (locale) => dispatch(setLocale(locale)),
  authenticate: (accessToken) => dispatch(authenticate(accessToken)),
  setTopNavHidden: (hidden) => dispatch(setTopNavHidden(hidden)),
  setBottomNavHidden: (hidden) => dispatch(setBottomNavHidden(hidden)),
  setDefaultViewSetName: (name) => dispatch(setDefaultViewSetName(name)),
});

export default connect(null, mapDispatchToProps)(HmiLoginPage);
