import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Button from '@material-ui/core/Button';

import './style.scss';

class ListSelection extends Component {
  render() {
    const { headline, selectionItems, onSelection, cancelButtonProps, showCancel } = this.props;

    return (
      <div>
        <h3>{headline}</h3>
        <ul className="list-wrapper">
          {selectionItems.map((item) => (
            <li className="selection-item" key={`li${item.id}`}>
              <Button
                variant="outlined"
                key={`button${item.id}`}
                onClick={() => onSelection(item.id)}
              >
                {item.icon && <img src={item.icon} alt="inspection.name" className="image-item" />}
                {item.text}
              </Button>
            </li>
          ))}
          {showCancel ? (
            <li className="selection-item" key="liCancel">
              <Button variant="contained" color="primary" onClick={cancelButtonProps.goBack}>
                {cancelButtonProps.icon && (
                  <img src={cancelButtonProps.icon} alt="type.id" className="image-item" />
                )}
                {cancelButtonProps.text}
              </Button>
            </li>
          ) : null}
        </ul>
      </div>
    );
  }
}

ListSelection.defaultProps = {
  showCancel: false,
  headline: '',
  selectionItems: [],
  cancelButtonProps: {},
};

ListSelection.propTypes = {
  headline: PropTypes.oneOfType([PropTypes.node, PropTypes.string]),
  selectionItems: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
      text: PropTypes.oneOfType([PropTypes.node, PropTypes.string]),
      icon: PropTypes.string,
    }),
  ),
  showCancel: PropTypes.bool,
  onSelection: PropTypes.func.isRequired,
  cancelButtonProps: PropTypes.shape({
    text: PropTypes.oneOfType([PropTypes.node, PropTypes.string]),
    icon: PropTypes.string,
    goBack: PropTypes.func,
  }),
};

export default ListSelection;
