import logger from '../utils/logger';
import InspectionSubscription from './InspectionSubscription';

export default class SubscriptionManager {
  constructor({ productHistoryManager }) {
    this.productHistoryManager = productHistoryManager;
    this.inspectionIdBySubId = new Map();
    this.subsByInspectionId = new Map();
    this.pausedInspectionIds = [];
    this.protobuf = null;
  }

  updatePausedInspections(pausedInspectionIds) {
    this.pausedInspectionIds = pausedInspectionIds;
    for (let [key, value] of this.subsByInspectionId.entries()) {
      if (pausedInspectionIds.includes(key)) {
        value.pause();
      } else {
        value.resume();
      }
    }
  }

  replayProduct(inspectionId, product) {
    const inspection = this.subsByInspectionId.get(inspectionId);
    if (inspection) {
      inspection.replayProduct(product);
    }
  }

  addSubscription(id, desc, cb) {
    const { inspectionId, startAtTriggerCount } = desc;
    this.inspectionIdBySubId.set(id, inspectionId);
    const isPaused = this.pausedInspectionIds.includes(inspectionId);
    let subscription = this.subsByInspectionId.get(inspectionId);
    if (!subscription) {
      subscription = new InspectionSubscription({
        inspectionId,
        startAtTriggerCount,
        productHistoryManager: this.productHistoryManager,
        paused: isPaused,
        protobuf: this.protobuf,
      });
      this.subsByInspectionId.set(inspectionId, subscription);
    }
    subscription.addSubscription(id, desc, cb);
  }

  removeSubscription(id) {
    const inspectionId = this.inspectionIdBySubId.get(id);
    if (!inspectionId) {
      logger.warn({ id }, 'Unable to unsubscribe listener: unknown id');
      return;
    }

    const subscription = this.subsByInspectionId.get(inspectionId);
    if (!subscription) {
      logger.warn({ id }, 'Unable to unsubscribe listener: unknown inspection id');
      return;
    }

    subscription.removeSubscription(id);

    if (subscription.isEmpty()) {
      this.subsByInspectionId.delete(inspectionId);
    }
  }

  getSubscribedImages(inspectionId) {
    const subscription = this.subsByInspectionId.get(inspectionId);
    if (!subscription) {
      return [];
    }
    return subscription.getSubscribedImages();
  }

  getSubscribedValues(inspectionId) {
    const subscription = this.subsByInspectionId.get(inspectionId);
    if (!subscription) {
      return [];
    }
    return subscription.getSubscribedValues();
  }

  setProtobuf(protobuf) {
    this.protobuf = protobuf;
    this.subsByInspectionId.forEach((subscription) => subscription.setProtobuf(protobuf));
  }
}
