import PropTypes from 'prop-types';
import React, { Component } from 'react';
import { connect } from 'react-redux';

import Dialog from '../../../../components/Dialog';
import ObjectSelector from '../../../../components/ObjectSelector';

class VariableSelection extends Component {
  setSelection = (selectedId) => {
    this.props.onClose(selectedId);
  };

  render() {
    const { open, title, configuration, inspectionId } = this.props;

    if (!configuration || !inspectionId) {
      return;
    }

    let tree = configuration.getObjectSelectorItemsForInspection(inspectionId).tree;
    tree = configuration.filterArrayValuesFromTree(tree);

    const filterIgnore = [];
    const name = configuration.getInspectionName(inspectionId);

    filterIgnore.push(`Inspections.${name}`);
    filterIgnore.push(`Inspections${name}`);
    filterIgnore.push(name);

    return (
      <Dialog isInView isPopup title={title} onClose={() => this.props.onClose()} open={open}>
        <div className="objectselector-in-dialog-wrapper">
          <ObjectSelector
            singleSelect
            data={tree}
            onChange={(selectedId) => this.setSelection(selectedId)}
            filterIgnore={filterIgnore}
          />
        </div>
      </Dialog>
    );
  }
}

VariableSelection.defaultProps = {
  open: false,
  title: '',
};

VariableSelection.propTypes = {
  open: PropTypes.bool.isRequired,
  onClose: PropTypes.func,
  title: PropTypes.string,
};

const mapStateToProps = (state) => {
  try {
    return {
      configuration: state.app.configuration,
    };
  } catch (error) {
    return {};
  }
};

export default connect(mapStateToProps)(VariableSelection);
